import React, { useState, useEffect } from "react";
import { useUser } from "../../store/UseUser";
import { useHistory } from "react-router-dom";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { registerLocale } from "react-datepicker";
import { useForm } from "react-hook-form";
import { getBrandList, getShopList } from "../../utils/httpUtils";
import I18n from "../../i18n";
import Loading from "../../components/Loading";
import "./ReceiptUpload.css";
import moment from "moment";
import en from "date-fns/locale/en-US";
import zhTW from "date-fns/locale/zh-TW";
import zhCN from "date-fns/locale/zh-CN";

function ReceiptUpload(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  document.body.className = "receipt-upload";
  const { accessToken } = useUser();
  let history = useHistory();

  registerLocale("en", en);
  registerLocale("zh-TW", zhTW);
  registerLocale("zh-CN", zhCN);

  const { register, errors, handleSubmit } = useForm();
  const [loading, setLoading] = useState(true);
  const [brand, setBrand] = useState(0);
  const [brandList, setBrandList] = useState([]);
  const [shop, setShop] = useState(0);
  const [shopList, setShopList] = useState([]);
  const [orginalShopList, setOrginalShopList] = useState([]);
  const [datepickerDate, setDatepickerDate] = useState("");
  const [datepickerLocale, setDatepickerLocale] = useState("zh-TW");
  const [receipDate, setReceiptDate] = useState("");
  const [disableSubmitBtn, setDisableSubmitBtn] = useState(false);
  const [electronicReceiptFile, setElectronicReceiptFile] = useState(null);
  const [storeReceiptFile, setStoreReceiptFile] = useState(null);
  const [
    electronicReceiptFilePreview,
    setElectronicReceiptFilePreview,
  ] = useState(null);
  const [storeReceiptFilePreview, setStoreReceiptFilePreview] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      getBrandList().then((response) => {
        setBrandList(response.data.content.mallList);

        getShopList({
          mallId: null,
        }).then((response2) => {
          setOrginalShopList(response2.data.content.shopList);
          setLoading(false);
        });
      });

      if (locale === "tch") {
        setDatepickerLocale("zh-TW");
      } else if (locale === "sch") {
        setDatepickerLocale("zh-CN");
      } else if (locale === "eng") {
        setDatepickerLocale("en");
      } else {
        setDatepickerLocale("zh-TW");
      }
    };
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [locale]);

  const getBrandName = (e) => {
    let brandName = "";
    let brandId = Number(e);
    if (brandId === 0) {
      brandName = <I18n t="super_mall" />;
    } else {
      for (let i = 0; i < brandList.length; i++) {
        if (brandList[i].mallId === brandId) {
          switch (locale) {
            case "eng":
              brandName = brandList[i].nameLang1;
              break;
            case "tch":
              brandName = brandList[i].nameLang2;
              break;
            case "sch":
              brandName = brandList[i].nameLang3;
              break;
            default:
              brandName = brandList[i].nameLang1;
              break;
          }
        }
      }
    }
    return brandName;
  };

  const getShopName = (e) => {
    let shopName = "";
    let shopId = Number(e);
    if (shopId === 0) {
      shopName = <I18n t="super_mall" />;
    } else {
      for (let i = 0; i < shopList.length; i++) {
        if (shopList[i].shopId === shopId) {
          shopName = shopList[i].shopName;
          break;
        }
      }
    }
    return shopName;
  };

  const getMallShopList = (mallId) => {
    var tempShopList = orginalShopList.filter(function (a) {
      return a.mallId === Number(mallId);
    });
    setShopList(tempShopList);
  };

  const handleBrandChange = (e) => {
    setBrand(e.target.value);
    getMallShopList(e.target.value);
  };

  const handleShopChange = (e) => {
    setShop(e.target.value);
  };

  const handleDateChange = (date) => {
    let tempDate = date ? moment(date).format("YYYY-MM-DD") : "";
    setDatepickerDate(date);
    setReceiptDate(tempDate);
  };

  const onSubmit = (data) => {
    // setDisableSubmitBtn(true);
    console.log(data);
  };

  const resetFileUpload = (data) => {
    let file = document.getElementById(data);
    file.value = null;
    if (data === "electronicReceiptFile") {
      setElectronicReceiptFile(null);
      setElectronicReceiptFilePreview(null);
    } else if (data === "storeReceiptFile") {
      setStoreReceiptFile(null);
      setStoreReceiptFilePreview(null);
    }
  };

  const handleElectronicReceiptFile = (e) => {
    let tempFile = e.target.files[0];
    if (tempFile) {
      setElectronicReceiptFile(tempFile);
      setElectronicReceiptFilePreview(URL.createObjectURL(tempFile));
    }
  };

  const handleStoreReceiptFile = (e) => {
    let tempFile = e.target.files[0];
    if (tempFile) {
      setStoreReceiptFile(tempFile);
      setStoreReceiptFilePreview(URL.createObjectURL(tempFile));
    }
  };

  return (
    <div>
      <div className="page-title">
        <div className="container">
          <div className="page-title-content">
            <I18n t="receipt_upload" />
          </div>
        </div>
      </div>

      <div className="page-main">
        {loading && <Loading />}
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className="container">
            <div className="form" id="receipt-upload-form">
              <div className="form-body">
                <div className="form-input">
                  <>
                    <select
                      id="brand"
                      name="brand"
                      className="custom-select"
                      ref={register({ required: true })}
                      onChange={handleBrandChange}
                    >
                      <option value="">
                        {I18n.getTranslation(location, "receipt_brand")}
                      </option>
                      {Object.keys(brandList).map((item) => {
                        return (
                          <option value={brandList[item].mallId} key={item}>
                            {getBrandName(brandList[item].mallId)}
                          </option>
                        );
                      })}
                    </select>
                    <div className="message">
                      {errors.brand?.type === "required" &&
                        I18n.getTranslation(
                          location,
                          "please_select_brand_for_redeem"
                        )}
                    </div>
                  </>
                </div>

                {shopList.length > 0 && (
                  <div className="form-input">
                    <>
                      <select
                        id="shop"
                        name="shop"
                        className="custom-select"
                        ref={register({ required: true })}
                        onChange={handleShopChange}
                      >
                        <option value="">
                          {I18n.getTranslation(location, "receipt_shop")}
                        </option>
                        {Object.keys(shopList).map((item) => {
                          return (
                            <option value={shopList[item].shopId} key={item}>
                              {getShopName(shopList[item].shopId)}
                            </option>
                          );
                        })}
                      </select>
                      <div className="message">
                        {errors.shop?.type === "required" &&
                          I18n.getTranslation(
                            location,
                            "please_select_shop_for_redeem"
                          )}
                      </div>
                    </>
                  </div>
                )}

                <div className="form-input">
                  <DatePicker
                    dateFormat="yyyy-MM-dd"
                    locale={datepickerLocale}
                    placeholderText={I18n.getTranslation(
                      location,
                      "date_of_purchase"
                    )}
                    selected={datepickerDate}
                    onChange={(date) => handleDateChange(date)}
                  />
                  <input
                    type="hidden"
                    name="receiptDate"
                    id="receiptDate"
                    value={receipDate}
                    ref={register({ required: true })}
                  />
                  <div className="message">
                    {errors.receiptDate?.type === "required" &&
                    receipDate === ""
                      ? I18n.getTranslation(
                          location,
                          "please_enter_date_of_purchase"
                        )
                      : ""}
                  </div>
                  <div className="calendar-icon"></div>
                </div>

                <div className="form-input">
                  <input
                    type="text"
                    name="amount"
                    id="amount"
                    placeholder={I18n.getTranslation(location, "amount_hkd")}
                    ref={register({ required: true })}
                  />
                  <div className="message">
                    {errors.amount?.type === "required" &&
                      I18n.getTranslation(location, "please_enter_amount")}
                  </div>
                </div>

                <div className="form-input">
                  <div
                    className={`form-file-upload ${
                      electronicReceiptFilePreview !== null ? "active" : null
                    }`}
                  >
                    <label
                      htmlFor="electronicReceiptFile"
                      className="file-upload-label"
                    >
                      <i className="file-upload-icon"></i>
                      <img
                        className="file-upload-preview"
                        src={electronicReceiptFilePreview}
                        alt=""
                      />
                    </label>
                    <div
                      className="file-upload-remove"
                      onClick={(e) => {
                        e.preventDefault();
                        resetFileUpload("electronicReceiptFile");
                      }}
                    ></div>
                    <div className="file-upload-label-name">
                      <I18n t="electronic_receipt" />
                    </div>
                    <input
                      id="electronicReceiptFile"
                      name="electronicReceiptFile"
                      type="file"
                      accept="image/*"
                      defaultValue={electronicReceiptFile}
                      onChange={handleElectronicReceiptFile}
                      ref={register()}
                    />
                  </div>
                  <div
                    className={`form-file-upload ${
                      storeReceiptFilePreview !== null ? "active" : null
                    }`}
                  >
                    <label
                      htmlFor="storeReceiptFile"
                      className="file-upload-label"
                    >
                      <i className="file-upload-icon"></i>
                      <img
                        className="file-upload-preview"
                        src={storeReceiptFilePreview}
                        alt=""
                      />
                    </label>
                    <div
                      className="file-upload-remove"
                      onClick={(e) => {
                        e.preventDefault();
                        resetFileUpload("storeReceiptFile");
                      }}
                    ></div>
                    <div className="file-upload-label-name">
                      <I18n t="store_receipt" />
                    </div>
                    <input
                      id="storeReceiptFile"
                      name="storeReceiptFile"
                      type="file"
                      accept="image/*"
                      defaultValue={storeReceiptFile}
                      onChange={handleStoreReceiptFile}
                      ref={register()}
                    />
                  </div>
                </div>

                <div className="terms form-input">
                  <span>
                    <I18n t="terms_and_conditions" />:
                  </span>
                  <br />
                  <br />
                  <p>
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                    Praesent sodales diam ligula, id eleifend diam molestie
                    quis. Aliquam at lectus et neque hendrerit iaculis. Ut
                    vehicula lorem sed sem egestas rutrum. Fusce quis leo arcu.
                    Nullam at orci finibus, porta est non, accumsan sapien.
                    Maecenas congue, leo quis euismod tempus, eros purus cursus
                    ligula, et consequat arcu ex vel ex. Mauris id neque eros.
                  </p>
                  <br />
                </div>

                <div className="form-input">
                  <div className="custom-control custom-checkbox">
                    <input
                      type="checkbox"
                      name="agreeTerms"
                      id="agreeTerms"
                      autoComplete="off"
                      className="custom-control-input"
                      ref={register({ required: true })}
                    />
                    <label
                      className="custom-control-label"
                      htmlFor="agreeTerms"
                    >
                      <I18n t="agree_terms" />
                    </label>
                  </div>
                  <div className="message">
                    {errors.agreeTerms?.type === "required" &&
                      I18n.getTranslation(location, "please_accept_terms")}
                  </div>
                </div>
              </div>

              <div className="form-footer">
                <div className="form-footer-button">
                  <button
                    type="submit"
                    className="btn btn-submit uppercase"
                    disabled={disableSubmitBtn}
                  >
                    <I18n t="submit" />
                  </button>
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}

export default ReceiptUpload;
