import React, { useState, useEffect } from "react";
import { useUser } from "../../store/UseUser";
import { getTransactionHistory, getBrandList } from "../../utils/httpUtils";
import { Link } from "react-router-dom";
import I18n from "../../i18n";
import Loading from "../../components/Loading";
import imageNoBonus from "../../assets/images/no_bonus.gif";
import RenderEarnPointsList from "./RenderEarnPointsList";
import "./EarnPointsHistory.css";

function EarnPointsHistory(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  document.body.className = "earn-points-history";
  const { accessToken } = useUser();

  const [loading, setLoading] = useState(true);
  const [transactionHistoryEmpty, setTransactionHistoryEmpty] = useState(false);
  const [transactionHistoryList, setTransactionHistoryList] = useState([]);
  const [brandList, setBrandList] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);

      if (sessionStorage.getItem("memberToken") === "99999999") {
        let responseData = require("../../assets/json/mockSpendingTransaction.json");
        setTransactionHistoryEmpty(false);
        setTransactionHistoryList(responseData);
        setLoading(false);
      } else {
        getTransactionHistory(accessToken, {
          numberOfLastRecord: 50,
        }).then((response) => {
          let responseData = response.data.content.memberSpendingTransactionList;
          if (responseData.length === 0) {
            setTransactionHistoryEmpty(true);
          } else {
            setTransactionHistoryEmpty(false);
            setTransactionHistoryList(responseData);
          }
          setLoading(false);
        });

        getBrandList()
          .then((response) => {
            setBrandList(response.data.content.mallList);
          })
          .catch(function (error) {
            console.log(error);
          });
      }
    };
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [locale]);

  return (
    <div>
      <div className="page-title">
        <div className="container">
          <div className="page-title-content">
            <I18n t="earn_points_history" />
          </div>
        </div>
      </div>

      <div className="page-main">
        <div className="container">
          {loading && <Loading />}
          {transactionHistoryEmpty && (
            <div className="no-record-holder">
              <div className="row">
                <div className="col-12">
                  <img src={imageNoBonus} alt="" />
                  <br />
                  <span>
                    <I18n t="no_earn_points_history" />
                  </span>
                </div>
              </div>
            </div>
          )}
          {!transactionHistoryEmpty && transactionHistoryList.length > 0 && (
            <RenderEarnPointsList
              history={transactionHistoryList}
              brand={brandList}
              lang={locale}
            />
          )}
        </div>
      </div>

      <div className="page-black">
        <Link to="./points-balance" className="btn-back"></Link>
      </div>
    </div>
  );
}

export default EarnPointsHistory;
