import React, { useState, useEffect } from "react";
import { Link, useHistory } from "react-router-dom";
import I18n from "../../i18n";
import PinInput from "react-pin-input";
import PageTitleAnimation from "../../components/PageTitleAnimation";
import "./Verification.css";
import "../../Form.css";
import {
  activate,
  resendactivationsms,
  getSystemSettingJson,
} from "../../utils/httpUtils";

function Verification(props) {
  document.body.className = "verification";
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  let history = useHistory();
  let registerPhonePrefix = sessionStorage.getItem("registerPhonePrefix");
  let registerPhone = sessionStorage.getItem("registerPhone");

  const [codeLength, setCodeLength] = useState(null);

  useEffect(() => {
    getSystemSettingJson().then((response) => {
      setCodeLength(response.data.MEMBER.AUTHEN_CODE_LENGTH);
    });
  });

  const verify = (pinCode) => {
    activate({
      phonePrefix: registerPhonePrefix,
      phone: registerPhone,
      authCode: pinCode,
    }).then((response) => {
      if (response.data.status === "S000") {
        sessionStorage.removeItem("registerPhonePrefix");
        sessionStorage.removeItem("registerPhone");
        history.push("/" + locale + "/verification-success");
      } else {
        history.push("/" + locale + "/verification-failed");
      }
    });
  };

  const [resendMessage, setResendMessage] = useState("");
  const resendActivation = (e) => {
    e.preventDefault();
    resendactivationsms({
      phonePrefix: registerPhonePrefix,
      phone: registerPhone,
    }).then((response) => {
      if (response.data.status === "S000") {
        setResendMessage(
          I18n.getTranslation(location, "resend_verification_success")
        );
      } else {
        setResendMessage(response.data.message[0]);
        setTimeout(() => {
          setResendMessage("");
        }, 5000);
      }
    });
  };

  return (
    <div>
      <div className="page-title">
        <div className="container">
          <PageTitleAnimation />
          <div className="page-title-content">
            <I18n t="verification" />
          </div>
        </div>
      </div>
      <div className="page-main">
        <div className="container">
          <div className="form" id="verification-form">
            <div className="form-intro">
              <I18n t="verification_intro1" /> <br />
              <I18n t="verification_intro2" />
              <span>
                +{registerPhonePrefix} {registerPhone}
              </span>
            </div>

            <div className="form-body">
              {codeLength && (
                <PinInput
                  length={parseFloat(codeLength)}
                  focus
                  type="numeric"
                  onComplete={(value) => {
                    verify(value);
                  }}
                />
              )}
            </div>

            <div className="form-footer">
              <div className="form-footer-button">
                <Link to="/" onClick={resendActivation}>
                  <I18n t="resend" />
                </Link>

                <div className="message">
                  <br />
                  {resendMessage}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Verification;
