import React, { Component } from "react";
import ScrollToTop from "../ScrollToTop";
import Header from "../components/Header";
import Footer from "../components/Footer";
import Home from "../views/Home";
import Login from "../views/Login";
import Register from "../views/registration/Register";
import Verification from "../views/registration/Verification";
import VerificationSuccess from "../views/registration/VerificationSuccess";
import VerificationFailed from "../views/registration/VerificationFailed";
import ForgotPassword from "../views/password/ForgotPassword";
import ForgotPasswordSuccess from "../views/password/ForgotPasswordSuccess";
import ResetPassword from "../views/password/ResetPassword";
import MyProfile from "../views/MyProfile";
import PointsBalance from "../views/PointsBalance";
import EarnPointsHistory from "../views/history/EarnPointsHistory";
import TransactionHistory from "../views/history/TransactionHistory";
import MyRedemptionGift from "../views/myredemption/MyRedemptionGift";
import MyRedemptionGiftDetail from "../views/myredemption/MyRedemptionGiftDetail";
import MyRedemptionTicket from "../views/myredemption/MyRedemptionTicket";
import Gifts from "../views/gift/Gift";
import GiftDetail from "../views/gift/GiftDetail";
import OrderPreview from "../views/gift/OrderPreview";
import RedeemSuccess from "../views/gift/RedeemSuccess";
import Events from "../views/event/Event";
import EventDetail from "../views/event/EventDetail";
import EventOrderPreview from "../views/event/EventOrderPreview";
import EnrollSuccess from "../views/event/EnrollSuccess";
import QrcodeScan from "../views/qrcode/Scan";
import EarnPointsReview from "../views/qrcode/EarnPointsReview";
import EarnPointsFailed from "../views/qrcode/EarnPointsFailed";
import ReceiptUpload from "../views/receipt/Upload";
import Logout from "../views/Logout";
import {
  BrowserRouter as Router,
  Route,
  Redirect,
  Switch,
} from "react-router-dom";
import Cookies from "js-cookie";
import { config } from "../config/index";

const base = "/:locale(eng|tch|sch)?";
const defaultLang = config.defaultLang;

const AuthenticatedRoute = ({ component: Component, ...rest }) => (
  <Route
    {...rest}
    render={(props) =>
      (
        Cookies.get("isStayLogin") === "true"
          ? Cookies.get("memberToken")
          : sessionStorage.getItem("memberToken")
      ) ? (
        <>
          <Route component={Header} />
          <Component {...props} />
          <Route component={Footer} />
        </>
      ) : (
        <Redirect to={{ pathname: "/", state: { from: props.location } }} />
      )
    }
  />
);

const RedirectRoute = ({ component: Component, ...rest }) => (
  <Route
    {...rest}
    render={(props) =>
      (
        Cookies.get("isStayLogin") === "true"
          ? Cookies.get("memberToken")
          : sessionStorage.getItem("memberToken")
      ) ? (
        <Redirect
          from="*"
          to={`/${
            props.match.params.locale ? props.match.params.locale : defaultLang
          }/home`}
        />
      ) : (
        <Redirect
          from="*"
          to={`/${
            props.match.params.locale ? props.match.params.locale : defaultLang
          }/login`}
        />
      )
    }
  />
);

class App extends Component {
  render() {
    return (
      <Router basename={process.env.PUBLIC_URL}>
        <ScrollToTop />
        <Switch>
          <Route exact path="/">
            <Redirect
              to={
                (
                  Cookies.get("isStayLogin") === "true"
                    ? Cookies.get("memberToken")
                    : sessionStorage.getItem("memberToken")
                )
                  ? `${defaultLang}/home`
                  : `${defaultLang}/login`
              }
            />
          </Route>
          <Route exact path={`${base}/login`}>
            <Route component={Login} />
          </Route>
          <Route exact path={`${base}/register`}>
            <Route component={Header} />
            <Route component={Register} />
            <Route component={Footer} />
          </Route>
          <Route exact path={`${base}/verification`}>
            <Route component={Header} />
            <Route component={Verification} />
            <Route component={Footer} />
          </Route>
          <Route exact path={`${base}/verification-success`}>
            <Route component={Header} />
            <Route component={VerificationSuccess} />
            <Route component={Footer} />
          </Route>
          <Route exact path={`${base}/verification-failed`}>
            <Route component={Header} />
            <Route component={VerificationFailed} />
            <Route component={Footer} />
          </Route>
          <Route exact path={`${base}/forgot-password`}>
            <Route component={Header} />
            <Route component={ForgotPassword} />
            <Route component={Footer} />
          </Route>
          <Route exact path={`${base}/forgot-password-success`}>
            <Route component={Header} />
            <Route component={ForgotPasswordSuccess} />
            <Route component={Footer} />
          </Route>
          <Route exact path={`${base}/reset-password`}>
            <Route component={Header} />
            <Route component={ResetPassword} />
            <Route component={Footer} />
          </Route>
          <Route exact path={`${base}/qrcode/scan`}>
            <Route component={QrcodeScan} />
          </Route>
          <AuthenticatedRoute
            exact
            path={`${base}/earn-points-review`}
            component={EarnPointsReview}
          ></AuthenticatedRoute>
          <AuthenticatedRoute
            exact
            path={`${base}/earn-points-failed`}
            component={EarnPointsFailed}
          ></AuthenticatedRoute>
          <AuthenticatedRoute
            exact
            path={`${base}/my-profile`}
            component={MyProfile}
          ></AuthenticatedRoute>
          <AuthenticatedRoute
            exact
            path={`${base}/home`}
            component={Home}
          ></AuthenticatedRoute>
          <AuthenticatedRoute
            exact
            path={`${base}/points-balance`}
            component={PointsBalance}
          ></AuthenticatedRoute>
          <AuthenticatedRoute
            exact
            path={`${base}/earn-points-history`}
            component={EarnPointsHistory}
          ></AuthenticatedRoute>
          <AuthenticatedRoute
            exact
            path={`${base}/transaction-history`}
            component={TransactionHistory}
          ></AuthenticatedRoute>
          <AuthenticatedRoute
            exact
            path={`${base}/my-redemption-gift`}
            component={MyRedemptionGift}
          ></AuthenticatedRoute>
          <AuthenticatedRoute
            exact
            path={`${base}/my-redemption-gift-detail/:id`}
            component={MyRedemptionGiftDetail}
          ></AuthenticatedRoute>
          <AuthenticatedRoute
            exact
            path={`${base}/my-redemption-ticket`}
            component={MyRedemptionTicket}
          ></AuthenticatedRoute>
          <AuthenticatedRoute
            exact
            path={`${base}/gifts`}
            component={Gifts}
          ></AuthenticatedRoute>
          <AuthenticatedRoute
            exact
            path={`${base}/gifts-detail/:giftId`}
            component={GiftDetail}
          ></AuthenticatedRoute>
          <AuthenticatedRoute
            exact
            path={`${base}/order-preview`}
            component={OrderPreview}
          ></AuthenticatedRoute>
          <AuthenticatedRoute
            exact
            path={`${base}/redeem-success`}
            component={RedeemSuccess}
          ></AuthenticatedRoute>
          <AuthenticatedRoute
            exact
            path={`${base}/events`}
            component={Events}
          ></AuthenticatedRoute>
          <AuthenticatedRoute
            exact
            path={`${base}/events-detail/:eventId`}
            component={EventDetail}
          ></AuthenticatedRoute>
          <AuthenticatedRoute
            exact
            path={`${base}/event-order-preview`}
            component={EventOrderPreview}
          ></AuthenticatedRoute>
          <AuthenticatedRoute
            exact
            path={`${base}/enroll-success`}
            component={EnrollSuccess}
          ></AuthenticatedRoute>
          <AuthenticatedRoute
            exact
            path={`${base}/receipt/upload`}
            component={ReceiptUpload}
          ></AuthenticatedRoute>
          <AuthenticatedRoute
            exact
            path={`${base}/logout`}
            component={Logout}
          ></AuthenticatedRoute>
          <RedirectRoute />
        </Switch>
      </Router>
    );
  }
}

export default App;
