import React, { useRef, useState, useEffect } from "react";
import { useUser } from "../store/UseUser";
import { Link, useHistory } from "react-router-dom";
import I18n from "../i18n";
import { useForm } from "react-hook-form";
import Nouislider from "nouislider-react";
import "nouislider/distribute/nouislider.css";
import wNumb from "wnumb";
import "./MyProfile.css";
import "../Form.css";
import Loading from "../components/Loading";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import PageTitleAnimation from "../components/PageTitleAnimation";
import {
  lmsMemberProfile,
  editMemberProfile,
  changePassword,
  getEnumValue,
  getMasterDataJson,
} from "../utils/httpUtils";

function Register(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  const { accessToken } = useUser();
  let history = useHistory();

  document.body.className = "myProfile";
  const { register, watch, errors, handleSubmit } = useForm();

  const profileUpdatedToastOptions = {
    autoClose: 3000,
    position: toast.POSITION.TOP_CENTER,
    pauseOnHover: false,
    onClose: () => profileUpdatedRedirect(),
  };

  const passwordChangedToastOptions = {
    autoClose: 3000,
    position: toast.POSITION.TOP_CENTER,
    pauseOnHover: false,
    onClose: () => passwordChangedRedirect(),
  };

  const commonToastOptions = {
    autoClose: 3000,
    position: toast.POSITION.TOP_CENTER,
    pauseOnHover: false,
  };

  const profileUpdatedRedirect = () => {
    history.push("/" + locale + "/home");
  };

  const passwordChangedRedirect = () => {
    sessionStorage.removeItem("memberToken");
    history.push("/" + locale + "/login");
  };

  const commonToast = (message, options) => {
    toast.info(message, options);
    setDisableSubmitBtn(false);
  };

  const onSubmit = (data) => {
    setDisableSubmitBtn(true);
    editMemberProfile(accessToken, {
      email: data.email,
      title: data.title,
      age: data.age,
      district: data.district,
      city: data.city,
      referrer: data.refereeMembershipNumber,
      defaultLanguage: data.defaultLang,
    }).then((response) => {
      if (response.data.status === "S000") {
        if (data.oldPassword && data.password) {
          changePassword(accessToken, {
            oldPassword: data.oldPassword,
            newPassword: data.password,
          }).then((response) => {
            if (response.data.status === "S000") {
              commonToast(
                <I18n t="password_changed" />,
                passwordChangedToastOptions
              );
            } else {
              commonToast(response.data.message[0], commonToastOptions);
            }
          });
        } else {
          commonToast(<I18n t="profile_updated" />, profileUpdatedToastOptions);
        }
      } else {
        if (response.data.message[0]) {
          commonToast(response.data.message[0], commonToastOptions);
        } else {
          commonToast(<I18n t="profile_update_failed" />, commonToastOptions);
        }
      }
    });
  };
  const password = useRef({});
  password.current = watch("password", "");

  const [profileData, setProfileData] = useState([]);
  const [profileDataLoaded, setProfileDataLoaded] = useState(false);
  const [phonePrefixOptions, setPhonePrefixOptions] = useState([]);
  const [cities, setCities] = useState([]);
  const [districts, setDistricts] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      setProfileDataLoaded(false);
      if (sessionStorage.getItem("memberToken") === "99999999") {
        let mockMemberProfile = require("../assets/json/mockMemberProfile.json");
        setProfileData(mockMemberProfile);
        setProfileDataLoaded(true);
      } else {
        lmsMemberProfile(accessToken)
          .then((response) => {
            setProfileData(response.data.content);
            setProfileDataLoaded(true);
          })
          .catch(function (error) {
            history.push("/" + locale + "/logout");
          });
      }

      getEnumValue()
        .then((response) => {
          setPhonePrefixOptions(response.data.PhonePrefix);
        })
        .catch(function (error) {
          // eslint-disable-next-line no-console
          console.log(error);
        });

      getMasterDataJson()
        .then((response) => {
          setDistricts(response.data[7]);
          setCities(response.data[8]);
        })
        .catch(function (error) {
          // eslint-disable-next-line no-console
          console.log(error);
        });
    };
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const [disableSubmitBtn, setDisableSubmitBtn] = useState(false);

  var titleOptions = [
    { name: "", value: "1", key: "MR" },
    { name: "", value: "3", key: "MRS" },
    { name: "/", value: "2", key: "MS" },
  ];

  // Use state to store object
  const [birthMonth] = useState([
    { value: "1", name: I18n.getTranslation(location, "january") },
    { value: "2", name: I18n.getTranslation(location, "february") },
    { value: "3", name: I18n.getTranslation(location, "march") },
    { value: "4", name: I18n.getTranslation(location, "april") },
    { value: "5", name: I18n.getTranslation(location, "may") },
    { value: "6", name: I18n.getTranslation(location, "june") },
    { value: "7", name: I18n.getTranslation(location, "july") },
    { value: "8", name: I18n.getTranslation(location, "august") },
    { value: "9", name: I18n.getTranslation(location, "september") },
    { value: "10", name: I18n.getTranslation(location, "october") },
    { value: "11", name: I18n.getTranslation(location, "november") },
    { value: "12", name: I18n.getTranslation(location, "december") },
  ]);

  var sliderConfig = {
    start: profileData.age,
    min: 18,
    max: 60,
    step: 1,
  };

  var tooltips = wNumb({
    prefix: I18n.getTranslation(location, "age") + "&nbsp;",
    decimals: 0,
  });

  const [ageSliderRef, setAgeSliderRef] = useState(null);
  const onAgeSliderRefUpdate = () => {
    ageSliderRef.noUiSlider.updateOptions({
      tooltips: tooltips,
    });
  };
  // const [ageValue, setAgeValue] = useState(profileData.age);
  // const onAgeUpdate = (value) => {
  //   setAgeValue(parseFloat(value).toFixed(0));
  // };

  if (ageSliderRef) {
    onAgeSliderRefUpdate();
  }

  let citiesList =
    cities.length > 0 &&
    cities.map((item, i) => {
      return (
        <option key={i} value={item.masterDataId}>
          {locale === "eng" && item.nameLang1}
          {locale === "tch" && item.nameLang2}
          {locale === "sch" && item.nameLang3}
        </option>
      );
    }, this);

  let districtsList =
    districts.length > 0 &&
    districts.map((item, i) => {
      return (
        <option key={i} value={item.masterDataId}>
          {locale === "eng" && item.nameLang1}
          {locale === "tch" && item.nameLang2}
          {locale === "sch" && item.nameLang3}
        </option>
      );
    }, this);

  return (
    <div>
      <ToastContainer pauseOnFocusLoss={false} />

      <div className="page-title">
        <div className="container">
          <PageTitleAnimation />
          <div className="page-title-content">
            <I18n t="my_profile" />
          </div>
        </div>
      </div>
      <div className="page-main">
        {profileDataLoaded === false && <Loading />}
        {profileDataLoaded && profileData && (
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="container">
              <div className="form" id="my-profile-form">
                <div className="form-intro"></div>

                <div className="form-body">
                  <div className="input-phone">
                    <div className="form-input">
                      <select
                        name="phonePrefixSelect"
                        className="custom-select input-phone-dial-code disabled-select"
                        ref={register()}
                        defaultValue={profileData.phonePrefix}
                      >
                        {phonePrefixOptions.length > 0 && Object.keys(phonePrefixOptions).map((item) => {
                          return (
                            <option
                              value={phonePrefixOptions[item].value}
                              key={phonePrefixOptions[item].key}
                            >
                              {phonePrefixOptions[item].name}
                            </option>
                          );
                        })}

                        {phonePrefixOptions.length === 0 && <option
                          value="852"
                          key="852"
                        >
                          +852
                      </option>}
                      </select>
                      <input
                        type="hidden"
                        name="phonePrefix"
                        defaultValue={profileData.phonePrefix}
                        ref={register()}
                      />
                    </div>
                    <div className="form-input">
                      <input
                        type="text"
                        name="phone"
                        placeholder={I18n.getTranslation(
                          location,
                          "phone_number"
                        )}
                        autoComplete="off"
                        className={`form-control input-phone-number`}
                        ref={register()}
                        value={profileData.phone}
                        readOnly
                      />
                      <div className="error-icon"></div>
                    </div>
                  </div>

                  <div className="form-input">
                    <input
                      type="password"
                      name="oldPassword"
                      placeholder={I18n.getTranslation(
                        location,
                        "old_password"
                      )}
                      autoComplete="off"
                      className={`form-control ${errors.oldPassword?.type === "required" ? "error" : ""
                        }`}
                      ref={register({
                        pattern: /[A-Za-z0-9]{8}/,
                      })}
                    />
                    <div className="error-icon"></div>
                  </div>
                  <div className="message">
                    {errors.oldPassword?.type === "required" &&
                      I18n.getTranslation(location, "password_required")}
                    {errors.oldPassword?.type === "pattern" &&
                      I18n.getTranslation(location, "password_formt_required")}
                  </div>

                  <div className="form-input">
                    <input
                      type="password"
                      name="password"
                      placeholder={I18n.getTranslation(
                        location,
                        "new_password"
                      )}
                      autoComplete="off"
                      className={`form-control ${errors.password?.type === "required" ? "error" : ""
                        }`}
                      ref={register({
                        pattern: /[A-Za-z0-9]{8}/,
                      })}
                    />
                    <div className="error-icon"></div>
                  </div>
                  <div className="message">
                    {errors.password?.type === "required" &&
                      I18n.getTranslation(location, "password_required")}
                    {errors.password?.type === "pattern" &&
                      I18n.getTranslation(location, "password_formt_required")}
                  </div>

                  <div className="form-input">
                    <input
                      type="password"
                      name="passwordReType"
                      placeholder={I18n.getTranslation(
                        location,
                        "new_password_retype"
                      )}
                      autoComplete="off"
                      className="form-control"
                      ref={register({
                        validate: (value) =>
                          value === password.current ||
                          I18n.getTranslation(location, "password_must_same"),
                      })}
                    />
                  </div>
                  <div className="message">
                    {errors.passwordReType?.type === "required" &&
                      I18n.getTranslation(location, "password_required")}
                    {errors.passwordReType && errors.passwordReType.message}
                  </div>

                  <div className="form-sub-title">
                    <I18n t="personal_information" />
                  </div>

                  <div className="form-input">
                    <select
                      name="titleSelect"
                      className="custom-select disabled-select"
                      defaultValue={profileData.title}
                      ref={register()}
                    >
                      <option value="">
                        {I18n.getTranslation(location, "personal_title")}
                      </option>
                      {titleOptions.map(function (title) {
                        return (
                          <option value={title.value} key={title.key}>
                            {title.name}
                          </option>
                        );
                      })}
                    </select>
                    <input
                      type="hidden"
                      name="title"
                      defaultValue={profileData.title}
                      ref={register()}
                    />
                  </div>

                  <div className="form-input">
                    <input
                      type="text"
                      name="givennameEng"
                      placeholder={I18n.getTranslation(
                        location,
                        "given_name_eng"
                      )}
                      autoComplete="off"
                      className="form-control"
                      ref={register()}
                      value={profileData.firstNameLang1}
                      readOnly
                    />
                  </div>

                  <div className="form-input">
                    <input
                      type="text"
                      name="surnameEng"
                      placeholder={I18n.getTranslation(location, "surname_eng")}
                      autoComplete="off"
                      className="form-control"
                      ref={register()}
                      value={profileData.lastNameLang1}
                      readOnly
                    />
                  </div>

                  <div className="form-input">
                    <input
                      type="text"
                      name="givennameTch"
                      placeholder={I18n.getTranslation(
                        location,
                        "given_name_tch"
                      )}
                      autoComplete="off"
                      className="form-control"
                      ref={register()}
                      value={profileData.firstNameLang2}
                      readOnly
                    />
                  </div>

                  <div className="form-input">
                    <input
                      type="text"
                      name="surnameTch"
                      placeholder={I18n.getTranslation(location, "surname_tch")}
                      autoComplete="off"
                      className="form-control"
                      ref={register()}
                      value={profileData.lastNameLang2}
                      readOnly
                    />
                  </div>

                  <>
                    <div className="form-input">
                      <select
                        className="custom-select disabled-select"
                        name="citySelect"
                        defaultValue={profileData.city}
                        ref={register()}
                      >
                        <option value="">
                          {I18n.getTranslation(location, "city")}
                        </option>
                        {citiesList}
                      </select>
                      <input
                        type="hidden"
                        name="city"
                        defaultValue={profileData.city}
                        ref={register()}
                      />
                    </div>

                    <div className="form-input">
                      <select
                        className="custom-select disabled-select"
                        name="districtSelect"
                        defaultValue={profileData.district}
                        ref={register()}
                      >
                        <option value="">
                          {I18n.getTranslation(location, "district")}
                        </option>
                        {districtsList}
                      </select>
                      <input
                        type="hidden"
                        name="district"
                        defaultValue={profileData.district}
                        ref={register()}
                      />
                    </div>
                  </>

                  <div className="form-input">
                    <input
                      type="email"
                      name="email"
                      placeholder={I18n.getTranslation(location, "email")}
                      autoComplete="off"
                      className={`form-control ${errors.email?.type === "required" ? "error" : ""
                        }`}
                      ref={register({
                        required: true,
                        pattern: {
                          value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                        },
                      })}
                      defaultValue={profileData.email}
                    />
                    <div className="error-icon"></div>
                  </div>
                  <div className="message">
                    {errors.email?.type === "required" &&
                      I18n.getTranslation(location, "email_required")}
                    {errors.email?.type === "pattern" &&
                      I18n.getTranslation(location, "incorrect_email_format")}
                  </div>

                  <div className="form-input">
                    <select
                      name="birthMonth"
                      className="custom-select disabled-select"
                      ref={register()}
                      defaultValue={profileData.birthMonth}
                    >
                      <option value="">
                        {I18n.getTranslation(location, "birthday_month")}
                      </option>
                      {birthMonth.map((item, i) => {
                        return (
                          <option key={i} value={item.value}>
                            {item.name}
                          </option>
                        );
                      }, this)}
                    </select>
                    <input
                      type="hidden"
                      name="birthMonth"
                      defaultValue={profileData.birthMonth}
                      ref={register()}
                    />
                  </div>

                  <div className="form-input form-range-input">
                    <div className="slider-range-label slider-min">
                      <I18n t="age" /> <span>{sliderConfig.min}</span>
                    </div>
                    <div className="slider-range-label slider-max">
                      <I18n t="age" /> <span>{sliderConfig.max}</span>+
                    </div>

                    <div id="slider">
                      <>
                        <Nouislider
                          instanceRef={(instance) => {
                            if (instance) {
                              setAgeSliderRef(instance);
                            }
                          }}
                          start={sliderConfig.start}
                          step={sliderConfig.step}
                          pips={{ mode: "range", density: 5 }}
                          connect="lower"
                          range={{
                            min: sliderConfig.min,
                            "30%": 30,
                            "60%": 40,
                            max: sliderConfig.max,
                          }}
                          tooltips={tooltips}
                          // onSlide={onAgeUpdate}
                          disabled={true}
                        />
                        {profileData && (
                          <input
                            type="hidden"
                            name="age"
                            value={profileData.age}
                            ref={register()}
                          />
                        )}
                      </>
                    </div>
                  </div>

                  <div className="form-sub-title">
                    <I18n t="default_language" />
                  </div>

                  <div className="form-input">
                    <div className="custom-control custom-radio">
                      <input
                        type="radio"
                        name="defaultLang"
                        autoComplete="off"
                        className="custom-control-input"
                        defaultValue="1"
                        id="defaultLang1"
                        ref={register()}
                        defaultChecked={
                          profileData.defaultLanguage === 1 ? true : false
                        }
                      />
                      <label
                        className="custom-control-label"
                        htmlFor="defaultLang1"
                      >
                        <I18n t="english" />
                      </label>
                    </div>

                    <div className="custom-control custom-radio">
                      <input
                        type="radio"
                        name="defaultLang"
                        autoComplete="off"
                        className="custom-control-input"
                        defaultValue="2"
                        id="defaultLang2"
                        ref={register()}
                        defaultChecked={
                          profileData.defaultLanguage === 2 ? true : false
                        }
                      />
                      <label
                        className="custom-control-label"
                        htmlFor="defaultLang2"
                      >
                        <I18n t="traditional_chinese" />
                      </label>
                    </div>

                    <div className="custom-control custom-radio">
                      <input
                        type="radio"
                        name="defaultLang"
                        autoComplete="off"
                        className="custom-control-input"
                        defaultValue="3"
                        id="defaultLang3"
                        ref={register()}
                        defaultChecked={
                          profileData.defaultLanguage === 3 ? true : false
                        }
                      />
                      <label
                        className="custom-control-label"
                        htmlFor="defaultLang3"
                      >
                        <I18n t="simplified_chinese" />
                      </label>
                    </div>
                  </div>

                  <div className="form-sub-title">
                    <I18n t="referee" />
                  </div>

                  <div className="form-input">
                    <input
                      type="text"
                      name="refereeMembershipNumber"
                      placeholder={I18n.getTranslation(
                        location,
                        "referee_membership_number"
                      )}
                      autoComplete="off"
                      className="form-control"
                      ref={register()}
                      defaultValue={profileData.referrer}
                    />
                  </div>
                </div>

                <div className="form-footer">
                  <div className="form-footer-button">
                    <Link to="home" className="btn-back"></Link>
                    <button
                      type="submit"
                      className="btn btn-save uppercase"
                      disabled={disableSubmitBtn}
                    >
                      <I18n t="submit" />
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </form>
        )}
      </div>
    </div>
  );
}

export default Register;
