import React from "react";
import I18n from "../../i18n";
import ResultMessage from "../../components/ResultMessage";
import SuccessImg from "../../assets/images/success.gif";
import PageBgAnimation from "../../components/PageBgAnimation";

function EnrollSuccess(props) {
  const { location } = props;
  document.body.className = "result enroll-success";

  return (
    <div className="container">
      <PageBgAnimation bgColor="#FFD428" />
      <ResultMessage
        image={SuccessImg}
        title={I18n.getTranslation(location, "enroll_success_title")}
        content={I18n.getTranslation(location, "enroll_success_content")}
        buttonText={I18n.getTranslation(
          location,
          "enroll_success_button_text1"
        )}
        buttonLink="events"
        buttonLink2="home"
        buttonText2={I18n.getTranslation(
          location,
          "enroll_success_button_text2"
        )}
        buttonCount={2}
      />
    </div>
  );
}

export default EnrollSuccess;
