import React from "react";
import { Link } from "react-router-dom";
import "./ResultMessage.css";

let image = "";
let title = "";
let content = "";
let buttonCount = null;
let buttonText = "";
let buttonLink = "";
let buttonText2 = "";
let buttonLink2 = "";

export default class ResultMessage extends React.Component {
  constructor(props) {
    super(props);
    image = props.image;
    title = props.title;
    content = props.content;
    buttonCount = props.buttonCount;
    buttonText = props.buttonText;
    buttonLink = props.buttonLink;
    buttonText2 = props.buttonText2;
    buttonLink2 = props.buttonLink2;
  }

  render() {
    return (
      <div className="result-holder">
        <div className="result-image">
          <img src={image} alt="" />
        </div>

        <div className="result-desc">
          <h2>{title}</h2>
          <p dangerouslySetInnerHTML={{ __html: content }}></p>
        </div>

        {buttonCount === 1 && (
          <div className="result-button">
            <Link to={buttonLink}>{buttonText}</Link>
          </div>
        )}

        {buttonCount > 1 && (
          <div className="result-button">
            <Link to={buttonLink} className="result-button1">
              {buttonText}
            </Link>
            <br />
            <Link to={buttonLink2} className="result-button2">
              {buttonText2}
            </Link>
          </div>
        )}
      </div>
    );
  }
}
