import React, { useState, useEffect } from "react";
import { useUser } from "../../store/UseUser";
import {
  getBrandList,
  getGiftCategoryList,
  getGiftList,
} from "../../utils/httpUtils";
import { Link } from "react-router-dom";
import I18n from "../../i18n";
import Loading from "../../components/Loading";
import Nouislider from "nouislider-react";
import "nouislider/distribute/nouislider.css";
import wNumb from "wnumb";
import "./Gift.css";
import imageNoGiftTicket from "../../assets/images/no_gift_ticket.gif";
import noImage from "../../assets/images/sample/noimage.png";
import Card from "react-bootstrap/Card";
import Accordion from "react-bootstrap/Accordion";
import { useAccordionToggle } from "react-bootstrap/AccordionToggle";

function Gift(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  document.body.className = "gift";
  const { user } = useUser();
  const memberProfile = user;

  const [loading, setLoading] = useState(true);
  const [brandList, setBrandList] = useState([]);
  const [giftCategoryList, setGiftCategoryList] = useState([]);
  const [giftList, setGiftList] = useState([]);
  const [originalGiftList, setOriginalGiftList] = useState([]);
  const [sorting, setSorting] = useState(3);
  const [sortingText, setSortingText] = useState("points_high_to_low");
  const [showSorting, setShowSorting] = useState(false);
  const [showFilter, setShowFilter] = useState(false);
  const [giftFiltered, setGiftFiltered] = useState(false);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);

      if (sessionStorage.getItem("memberToken") === "99999999") {
        let brandJson = require("../../assets/json/mockBrand.json");
        let giftCategoryJson = require("../../assets/json/mockGiftCategory.json");
        let giftJson = require("../../assets/json/mockGift.json");

        let tempGiftList = giftJson.sort(function (a, b) {
          return (
            b.redeemBonusPoints[0].redeemBonusPointsBalance -
            a.redeemBonusPoints[0].redeemBonusPointsBalance
          );
        });

        setBrandList(brandJson);
        setGiftCategoryList(giftCategoryJson);
        setGiftList(tempGiftList);
        setOriginalGiftList(giftJson);

        setLoading(false);
      } else {
        getBrandList().then((response) => {
          setBrandList(response.data.content.mallList);
        });

        getGiftCategoryList({ mallId: 0 }).then((response) => {
          setGiftCategoryList(response.data.content.giftCategoryList);
        });

        getGiftList({
          redeemType: "B",
        }).then((response) => {
          let tempGiftList = response.data.content.rewardList;
          // Sort by display order first
          tempGiftList = tempGiftList.sort((a, b) =>
            a.displayOrder < b.displayOrder
              ? 1
              : b.displayOrder < a.displayOrder
                ? -1
                : 0
          );
          // Sort by default option => Points high to low
          tempGiftList = tempGiftList.sort(function (a, b) {
            return (
              b.redeemBonusPoints[0].redeemBonusPointsBalance -
              a.redeemBonusPoints[0].redeemBonusPointsBalance
            );
          });
          setGiftList(tempGiftList);
          setOriginalGiftList(tempGiftList);
          setLoading(false);
        });
      }
    };
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [locale]);

  const toggleSorting = (e) => {
    e.preventDefault();
    setShowSorting(!showSorting);
    setShowFilter(false);
  };

  const toggleFilter = (e) => {
    e.preventDefault();
    setShowSorting(false);
    setShowFilter(!showFilter);
  };

  const toggleCloseOverlay = () => {
    setShowSorting(false);
    setShowFilter(false);
  };

  const getSortingData = (sortingVal, sortingTextVal) => {
    let tempGiftList = giftList;
    // Sorting: Newest && by display order
    tempGiftList = tempGiftList.sort((a, b) =>
      a.displayOrder < b.displayOrder
        ? 1
        : b.displayOrder < a.displayOrder
          ? -1
          : 0
    );

    if (sortingVal === 1) {
      // Sorting: Hottest Pick
      tempGiftList = tempGiftList.sort(function (a, b) {
        return b.hotpick - a.hotpick;
      });
    } else if (sortingVal === 2) {
      // Sorting: Points low to high
      tempGiftList = tempGiftList.sort(function (a, b) {
        return (
          a.redeemBonusPoints[0].redeemBonusPointsBalance -
          b.redeemBonusPoints[0].redeemBonusPointsBalance
        );
      });
    } else if (sortingVal === 3) {
      // Sorting: Points high to low
      tempGiftList = tempGiftList.sort(function (a, b) {
        return (
          b.redeemBonusPoints[0].redeemBonusPointsBalance -
          a.redeemBonusPoints[0].redeemBonusPointsBalance
        );
      });
    }

    setGiftList(tempGiftList);
    setSorting(sortingVal);
    setSortingText(sortingTextVal);
    toggleCloseOverlay();
  };

  const getGiftPoint = (redeemBonusPointsVal) => {
    let tempPoint = "";
    for (var i = 0; i < redeemBonusPointsVal.length; i++) {
      if (
        redeemBonusPointsVal[i].memberLevelId ===
        String(memberProfile.memberLevelId)
      ) {
        tempPoint = redeemBonusPointsVal[i].redeemBonusPointsBalance;
        break;
      }
    }
    return tempPoint ? (
      <I18n
        t="pts"
        args={{
          points: tempPoint,
        }}
      />
    ) : (
        ""
      );
  };

  const getGiftInventory = (currentGift) => {
    var inventoryData = 0;
    var currentGiftMallId = currentGift.mallId;
    var currentGiftInventoryMode = currentGift.inventoryMode;
    var inventoryList = currentGift.inventory;

    if (currentGiftMallId === 0) {
      // Master
      if (currentGiftInventoryMode === "M") {
        // Master Inventory
        // mall_id 0 and inventory_mode M: inventoryList[mallId= 0].stockLevel - reservedStockLevel
        if (inventoryList.length) {
          for (let i = 0; i < inventoryList.length; i++) {
            if (inventoryList[i].mallId === 0) {
              inventoryData =
                inventoryList[i].stockLevel -
                inventoryList[i].reservedStockLevel;
            }
          }
        }
      } else if (
        currentGiftInventoryMode === "B" ||
        currentGiftInventoryMode === "S"
      ) {
        // Mall Inventory
        // mall_id 0 and inventory_mode B/S: loop mall_id in reserveInMall, for each mall_id, get stock level by inventoryList[mall_id].stockLevel - reservedStockLevel, sum all mall as total stock
        if (currentGift.reserveInMall) {
          var reserveInMallList = currentGift.reserveInMall.mallId;
          var filteredList = currentGift.inventory.filter(function (a) {
            for (let i = 0; i < reserveInMallList.length; i++) {
              if (reserveInMallList[i] === a.mallId) {
                return reserveInMallList[i] === a.mallId;
              }
            }
            return false;
          });

          var totalStock = 0;
          if (filteredList.length) {
            for (let i = 0; i < filteredList.length; i++) {
              totalStock +=
                filteredList[i].stockLevel - filteredList[i].reservedStockLevel;
            }
          }
          inventoryData = totalStock;
        }
      }
    } else {
      // Mall
      if (
        currentGift.inventoryMode === "B" ||
        currentGift.inventoryMode === "S"
      ) {
        // Mall Inventory
        // mall_id > 0 and inventory_mode B/S: inventoryList[mall_id].stockLevel - reservedStockLevel
        if (inventoryList.length) {
          for (let i = 0; i < inventoryList.length; i++) {
            if (inventoryList[i].mallId === currentGiftMallId) {
              inventoryData =
                inventoryList[i].stockLevel -
                inventoryList[i].reservedStockLevel;
            }
          }
        }
      }
    }

    return inventoryData;
  };

  const CustomToggle = ({ children, eventKey, active }) => {
    const [condition, setCondition] = useState(active);
    const customOnClick = useAccordionToggle(eventKey, () => {
      setCondition(!condition);
    });
    return (
      <div
        onClick={customOnClick}
        className={`card-header ${condition ? "show" : ""}`}
      >
        {children}
      </div>
    );
  };

  const sliderConfig = {
    start: [0, 1000],
    step: 1,
    connect: true,
    range: {
      min: 0,
      "30%": 300,
      "60%": 500,
      max: 1000,
    },
    pips: {
      mode: "range",
      density: 5,
    },
  };

  const tooltips = [wNumb({ decimals: 0 }), wNumb({ decimals: 0 })];

  const [rangeSliderRef, setRangeSliderRef] = useState(null);
  const onRangeSliderRefUpdate = () => {
    rangeSliderRef.noUiSlider.updateOptions({
      tooltips: tooltips,
    });
  };
  const [pointsRangeMinValue, setPointsRangeMinValue] = useState("0");
  const [pointsRangeMaxValue, setPointsRangeMaxValue] = useState("1000");
  const onRangeUpdate = (values) => {
    setPointsRangeMinValue(parseFloat(values[0]).toFixed(0));
    setPointsRangeMaxValue(parseFloat(values[1]).toFixed(0));
  };

  if (rangeSliderRef) {
    onRangeSliderRefUpdate();
  }

  const [checkedBrand, setCheckedBrand] = useState([]);
  const handleBrandChange = (e) => {
    const checkedBrandArray = checkedBrand;

    if (e.target.checked) {
      checkedBrandArray.push(e.target.value);
    } else {
      const index = checkedBrandArray.indexOf(e.target.value);
      checkedBrandArray.splice(index, 1);
    }
    setCheckedBrand(checkedBrandArray);
  };

  const [checkedCategory, setCheckedCategory] = useState([]);
  const handleCategoryChange = (e) => {
    const checkedCategoryArray = checkedCategory;
    if (e.target.checked) {
      checkedCategoryArray.push(e.target.value);
    } else {
      const index = checkedCategoryArray.indexOf(e.target.value);
      checkedCategoryArray.splice(index, 1);
    }
    setCheckedCategory(checkedCategoryArray);
  };

  const resetFilteredList = () => {
    setGiftFiltered(false);
    setCheckedBrand([]);
    document
      .querySelectorAll("input[name=brand]")
      .forEach((el) => (el.checked = false));
    document
      .querySelectorAll("input[name=category]")
      .forEach((el) => (el.checked = false));
    setCheckedCategory([]);
    setPointsRangeMinValue(sliderConfig.range.min);
    setPointsRangeMaxValue(sliderConfig.range.max);
    rangeSliderRef.noUiSlider.set(sliderConfig.start);
    setGiftList(originalGiftList);
    toggleCloseOverlay();
    resetAccordion();
  };

  const resetAccordion = useAccordionToggle("1", () => { });

  const getFilteredList = () => {
    setGiftFiltered(true);
    var filterBrand = checkedBrand;
    var filterCategory = checkedCategory;
    var filteredList = originalGiftList;

    // Brand filter
    if (filterBrand.length > 0) {
      filteredList = filteredList.filter(function (s) {
        if (s.mallId === 0) {
          // Master
          if (s.inventoryMode === "M") {
            // mall_id 0 and inventory_mode M: always show
            return true;
          } else if (s.inventoryMode === "B" || s.inventoryMode === "S") {
            // mall_id 0 and inventory_mode B/S: show if reserveInMall contains selected mall_id
            if (s.reserveInMall !== null) {
              for (let i = 0; i < filterBrand.length; i++) {
                for (let j = 0; j < s.reserveInMall.mallId.length; j++) {
                  if (Number(filterBrand[i]) === s.reserveInMall.mallId[j]) {
                    return Number(filterBrand[i]) === s.reserveInMall.mallId[j];
                  }
                }
              }
            }
          }
        } else {
          // Mall
          // mall_id > 0 and inventory_mode B/S: show if mall_id equal selected mall_id
          for (let i = 0; i < filterBrand.length; i++) {
            if (Number(filterBrand[i]) === s.mallId) {
              return Number(filterBrand[i]) === s.mallId;
            }
          }
        }
        return false;
      });
    }

    // Category filter
    if (filterCategory.length > 0) {
      filteredList = filteredList.filter(function (s) {
        for (var i = 0; i < filterCategory.length; i++) {
          if (Number(filterCategory[i]) === s.giftCategoryId) {
            return Number(filterCategory[i]) === s.giftCategoryId;
          }
        }
        return false;
      });
    }

    // Range filter
    if (pointsRangeMinValue !== null && pointsRangeMaxValue !== null) {
      filteredList = filteredList.filter(function (s) {
        let pointsBalance = parseInt(
          s.redeemBonusPoints[0].redeemBonusPointsBalance
        );
        if (
          pointsBalance >= parseInt(pointsRangeMinValue) &&
          pointsBalance <= parseInt(pointsRangeMaxValue)
        ) {
          return (
            pointsBalance >= parseInt(pointsRangeMinValue) &&
            pointsBalance <= parseInt(pointsRangeMaxValue)
          );
        }
        return false;
      });
    }

    setGiftList(filteredList);
    toggleCloseOverlay();
  };

  const handleErrorImg = (e) => {
    e.target.src = noImage;
  };

  return (
    <div>
      <div className="page-tab">
        <div className="container-fluid">
          <div className="page-tab-holder">
            <div className="container">
              <ul className="page-tab-item">
                <li className="active">
                  <Link to="#">
                    <I18n t="gift" />
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>

      <div className="page-nav">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <div className="list-sorting">
                <Link
                  className={`btn-toggle-sorting ${showSorting ? "collapsed" : ""
                    }`}
                  to="#"
                  onClick={toggleSorting}
                >
                  <I18n t={sortingText} />
                </Link>

                <div
                  className={`list-sorting-panel ${showSorting === true ? "show" : ""
                    }`}
                >
                  <div className="custom-control custom-radio">
                    <input
                      type="radio"
                      name="sorting"
                      className="custom-control-input"
                      value="0"
                      id="newest"
                      onChange={() => getSortingData(0, "newest")}
                      checked={sorting === 0}
                    />
                    <label className="custom-control-label" htmlFor="newest">
                      <I18n t="newest" />
                    </label>
                  </div>

                  <div className="custom-control custom-radio">
                    <input
                      type="radio"
                      name="sorting"
                      className="custom-control-input"
                      value="1"
                      id="hottestPick"
                      onChange={() => getSortingData(1, "hottest_pick")}
                      checked={sorting === 1}
                    />
                    <label
                      className="custom-control-label"
                      htmlFor="hottestPick"
                    >
                      <I18n t="hottest_pick" />{" "}
                      <span className="icon-crown"></span>
                    </label>
                  </div>

                  <div className="custom-control custom-radio">
                    <input
                      type="radio"
                      name="sorting"
                      className="custom-control-input"
                      value="2"
                      id="pointsLowToHigh"
                      onChange={() => getSortingData(2, "points_low_to_high")}
                      checked={sorting === 2}
                    />
                    <label
                      className="custom-control-label"
                      htmlFor="pointsLowToHigh"
                    >
                      <I18n t="points_low_to_high" />
                    </label>
                  </div>

                  <div className="custom-control custom-radio">
                    <input
                      type="radio"
                      name="sorting"
                      className="custom-control-input"
                      value="3"
                      id="pointsHighToLow"
                      onChange={() => getSortingData(3, "points_high_to_low")}
                      checked={sorting === 3}
                    />
                    <label
                      className="custom-control-label"
                      htmlFor="pointsHighToLow"
                    >
                      <I18n t="points_high_to_low" />
                    </label>
                  </div>
                </div>
              </div>

              <div className="list-filter">
                <Link
                  className={`btn-toggle-filter ${giftFiltered === true ? "filtered" : ""
                    }`}
                  to="#"
                  onClick={toggleFilter}
                ></Link>
              </div>

              <div
                className={`list-filter-panel ${showFilter === true ? "show" : ""
                  }`}
              >
                <Accordion defaultActiveKey="0">
                  <Card>
                    <div className="brand">
                      <CustomToggle eventKey="0" active={true}>
                        <I18n t="brand" />
                      </CustomToggle>
                      <Accordion.Collapse eventKey="0">
                        <Card.Body>
                          {Object.keys(brandList).map((item) => {
                            return (
                              <div
                                className="custom-control custom-checkbox"
                                key={brandList[item].mallId}
                              >
                                <input
                                  id={`brand-` + brandList[item].mallId}
                                  type="checkbox"
                                  name="brand"
                                  className="custom-control-input"
                                  value={brandList[item].mallId}
                                  onChange={handleBrandChange}
                                />
                                <label
                                  htmlFor={`brand-` + brandList[item].mallId}
                                  className="custom-control-label"
                                >
                                  <img
                                    src={
                                      brandList[item].logo
                                        ? brandList[item].logo
                                        : noImage
                                    }
                                    onError={handleErrorImg}
                                    alt=""
                                  />
                                </label>
                              </div>
                            );
                          })}
                        </Card.Body>
                      </Accordion.Collapse>
                    </div>
                  </Card>
                </Accordion>

                <Accordion>
                  <Card>
                    <div className="category">
                      <CustomToggle eventKey="1" active={false}>
                        <I18n t="category" />
                      </CustomToggle>
                      <Accordion.Collapse eventKey="1">
                        <Card.Body>
                          {Object.keys(giftCategoryList).map((item) => {
                            return (
                              <div
                                className="custom-control custom-checkbox"
                                key={giftCategoryList[item].giftCategoryId}
                              >
                                <input
                                  id={
                                    `category-` +
                                    giftCategoryList[item].giftCategoryId
                                  }
                                  type="checkbox"
                                  name="category"
                                  className="custom-control-input"
                                  value={giftCategoryList[item].giftCategoryId}
                                  onChange={handleCategoryChange}
                                />
                                <label
                                  htmlFor={
                                    `category-` +
                                    giftCategoryList[item].giftCategoryId
                                  }
                                  className="custom-control-label"
                                >
                                  {giftCategoryList[item].giftCategoryName}
                                </label>
                              </div>
                            );
                          })}
                        </Card.Body>
                      </Accordion.Collapse>
                    </div>
                  </Card>
                </Accordion>

                <Accordion>
                  <Card>
                    <CustomToggle eventKey="2" active={false}>
                      <I18n t="points_range" />
                    </CustomToggle>
                    <Accordion.Collapse eventKey="2">
                      <Card.Body>
                        <div className="form-input form-range-input">
                          <div id="slider">
                            <>
                              <Nouislider
                                instanceRef={(instance) => {
                                  if (instance) {
                                    setRangeSliderRef(instance);
                                  }
                                }}
                                start={sliderConfig.start}
                                step={sliderConfig.step}
                                pips={sliderConfig.pips}
                                connect={sliderConfig.connect}
                                range={sliderConfig.range}
                                tooltips={tooltips}
                                onSlide={onRangeUpdate}
                              />
                              {pointsRangeMinValue && (
                                <input
                                  type="hidden"
                                  name="pointsRangeMin"
                                  value={pointsRangeMinValue}
                                />
                              )}
                              {pointsRangeMaxValue && (
                                <input
                                  type="hidden"
                                  name="pointsRangeMax"
                                  value={pointsRangeMaxValue}
                                />
                              )}
                            </>
                          </div>
                        </div>
                      </Card.Body>
                    </Accordion.Collapse>
                  </Card>
                </Accordion>

                <div className="filter-button">
                  <button
                    className="btn btn-reset uppercase"
                    onClick={resetFilteredList}
                  >
                    <I18n t="reset" />
                  </button>
                  <button
                    className="btn btn-apply uppercase"
                    onClick={getFilteredList}
                  >
                    <I18n t="apply" />
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="page-main">
        {loading && <Loading />}
        <div
          className={`filter-overlay ${showSorting === true || showFilter === true ? "show" : ""
            }`}
          onClick={toggleCloseOverlay}
        ></div>

        <div className="container" id="gift-list">
          {giftList.length === 0 && (
            <div className="row">
              <div className="col-12">
                <div className="no-gift">
                  <img src={imageNoGiftTicket} alt="" />
                  <br />
                  <span>
                    <I18n t="no_item_found" />
                  </span>
                  <span>
                    <I18n t="please_stay_tuned" />
                  </span>
                </div>
              </div>
            </div>
          )}

          {giftList.length > 0 && (
            <div className="row">
              {Object.keys(giftList).map((item) => {
                return (
                  <div
                    className="col-6 col-md-3 col-lg-3 gift-item"
                    key={giftList[item].giftId}
                  >
                    <div className="gift-image">
                      {giftList[item].hotpick && (
                        <div className="gift-featured"></div>
                      )}
                      {getGiftInventory(giftList[item]) > 0 &&
                        giftList[item].unlimitedInventory === null && (
                          <div className="gift-inventory">
                            <I18n
                              t="giftInventory"
                              args={{
                                value: getGiftInventory(giftList[item]),
                              }}
                            />
                          </div>
                        )}

                      {getGiftInventory(giftList[item]) === 0 &&
                        giftList[item].unlimitedInventory === null && (
                          <div className="gift-inventory">
                            <I18n t="out_of_stock" />
                          </div>
                        )}
                      <Link to={`gifts-detail/` + giftList[item].giftId}>
                        <img
                          src={
                            giftList[item].image
                              ? giftList[item].image
                              : noImage
                          }
                          alt=""
                        />
                      </Link>
                    </div>
                    <div className="gift-intro">
                      <h3 className="gift-point">
                        {getGiftPoint(giftList[item].redeemBonusPoints)}
                      </h3>
                      <p className="gift-desc">
                        {giftList[item].giftName}
                        <br />
                        <span>{giftList[item].giftDescription}</span>
                      </p>
                    </div>
                  </div>
                );
              })}
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default Gift;
