import React, { useState, useEffect } from "react";
import { useUser } from "../../store/UseUser";
import { getRewardRedemptionHistory } from "../../utils/httpUtils";
import { Link } from "react-router-dom";
import I18n from "../../i18n";
import Loading from "../../components/Loading";
import imageMyGiftEmpty from "../../assets/images/mygift_empty.gif";
import moment from "moment";
import Slider from "react-slick";
import "../../assets/css/slick.css";
import "../../assets/css/slick-theme.css";
import "./MyRedemption.css";
import noImage from "../../assets/images/sample/noimage.png";
import { isMobileOnly, isTablet } from "react-device-detect";

function MyRedemptionTicket(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  document.body.className = "my-redemption my-redemption-ticket";
  const { accessToken } = useUser();

  const [loading, setLoading] = useState(true);

  const [myTicketListEmpty, setMyTicketListEmpty] = useState(false);
  const [myTicketList, setMyTicketList] = useState([]);

  const [myTicketHistoryList, setMyTicketHistoryList] = useState([]);
  const [myTicketHistoryListEmpty, setMyTicketHistoryListEmpty] = useState(
    false
  );

  var slidesToShow = 4;
  var slidesToScroll = 4;

  if (isTablet) {
    slidesToShow = 2;
    slidesToScroll = 2;
  } else if (isMobileOnly) {
    slidesToShow = 1;
    slidesToScroll = 1;
  }

  const sliderSettings = {
    dots: false,
    infinite: false,
    slidesToShow: slidesToShow,
    slidesToScroll: slidesToScroll,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);

      getRewardRedemptionHistory(accessToken, {
        numberOfLastRecord: 50,
      }).then((response) => {
        let responseData = response.data.content.giftRedemptionTransactionList.sort(
          (a, b) => moment(b.reserveDatetime) - moment(a.reserveDatetime)
        );
        if (responseData.length === 0) {
          setMyTicketListEmpty(true);
          setMyTicketHistoryListEmpty(true);
        } else {
          let tempMyTicketList = responseData.filter(function (a) {
            return a.status === "P";
          });
          setMyTicketList(tempMyTicketList);

          let tempMyTicketHistoryList = responseData.filter(function (a) {
            return a.status === "R";
          });
          setMyTicketHistoryList(tempMyTicketHistoryList);
        }
        setLoading(false);
      });
    };
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [locale]);

  const getMyTicketHistoryStatus = (e) => {
    switch (e) {
      case "P":
        return <I18n t="reserved" />;
      case "R":
        return <I18n t="redeemed" />;
      case "C":
        return <I18n t="cancelled" />;
      case "F":
        return <I18n t="refunded" />;
      default:
        return <I18n t="expired" />;
    }
  };

  const handleErrorImg = (e) => {
    e.target.src = noImage;
  };

  return (
    <div>
      <div className="page-tab">
        <div className="container-fluid">
          <div className="page-tab-holder">
            <div className="container">
              <ul className="page-tab-item">
                <li>
                  <Link to="my-redemption-gift">
                    <I18n t="gifts" />
                  </Link>
                </li>
                <li className="active">
                  <Link to="my-redemption-ticket">
                    <I18n t="ticket" />
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>

      <div className="page-main">
        <div className="container">
          {loading && <Loading />}
          {myTicketListEmpty && (
            <div className="my-redemption-ticket-empty">
              <img src={imageMyGiftEmpty} alt="" />
              <p>
                <I18n t="my_ticket_empty" />
              </p>
              <Link to="events" className="btn uppercase">
                <I18n t="event_enroll" />
              </Link>
            </div>
          )}
          {!myTicketListEmpty && (
            <>
              <div className="my-redemption-ticket-holder">
                <h1 className="my-redemption-title">
                  <I18n t="my_ticket" />
                </h1>
              </div>
              <div className="my-redemption-ticket-list">
                <Slider {...sliderSettings}>
                  {Object.keys(myTicketList).map((item) => {
                    return (
                      <div
                        className="ticket-item"
                        key={myTicketList[item].giftRedemptionTransactionId}
                      >
                        <div className="ticket-item-image">
                          <img
                            src={
                              myTicketList[item].itemImage
                                ? myTicketList[item].itemImage
                                : noImage
                            }
                            onError={handleErrorImg}
                            alt=""
                          />
                        </div>
                        <div className="ticket-item-info">
                          <Link
                            to={`my-redemption-ticket-detail/${myTicketList[item].giftRedemptionTransactionId}`}
                          >
                            <div className="ticket-item-content">
                              {myTicketList[item].itemName}
                            </div>
                          </Link>
                          <div className="ticket-item-expiry-date">
                            <I18n
                              t="expiryOn"
                              args={{
                                value: myTicketList[item].reserveExpiryDate,
                              }}
                            />
                          </div>
                        </div>
                      </div>
                    );
                  })}
                </Slider>
              </div>
            </>
          )}
        </div>

        {myTicketHistoryListEmpty && (
          <div
            className={`container-fluid section-title history-title ${
              !myTicketHistoryListEmpty ? "" : "show"
            }`}
          >
            <div className="container">
              <span>
                <I18n t="history" />
              </span>
            </div>
          </div>
        )}

        {myTicketHistoryListEmpty && (
          <div className="ticket-history-list">
            <div className="container">
              {Object.keys(myTicketHistoryList).map((item) => {
                return (
                  <div
                    className="ticket-history-list-item"
                    key={myTicketHistoryList[item].giftRedemptionTransactionId}
                  >
                    <div className="row">
                      <div className="col-8 col-sm-8 col-md-8 col-lg-8">
                        <div className="ticket-history-list-item-expiry-date">
                          <I18n
                            t="expiryOn"
                            args={{
                              value:
                                myTicketHistoryList[item].reserveExpiryDate,
                            }}
                          />
                        </div>
                        <div className="ticket-history-list-item-desc">
                          {myTicketHistoryList[item].itemName}
                        </div>
                      </div>
                      <div className="col-4 col-sm-4 col-md-4 col-lg-4">
                        <div className="ticket-history-list-item-image">
                          <div className="ticket-image">
                            <img
                              src={
                                myTicketHistoryList[item].itemImage
                                  ? myTicketHistoryList[item].itemImage
                                  : noImage
                              }
                              onError={handleErrorImg}
                              alt=""
                            />
                          </div>
                          <div className="ticket-status">
                            {getMyTicketHistoryStatus(
                              myTicketHistoryList[item].status
                            )}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                );
              })}
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default MyRedemptionTicket;
