import React, { useState } from "react";
import { useUser } from "../store/UseUser";
import logo from "../assets/images/logo.svg";
import iconHome from "../assets/images/icon-home.svg";
import iconLogout from "../assets/images/icon-logout.svg";
import iconPointsBalance from "../assets/images/icon-points-balance.svg";
import iconMyRedemption from "../assets/images/icon-my-redemption.svg";
import iconGifts from "../assets/images/icon-gifts.svg";
import iconEvents from "../assets/images/icon-events.svg";
import iconMyProfile from "../assets/images/icon-my-profile.svg";
import iconAboutUs from "../assets/images/icon-about-us.svg";
import iconMessage from "../assets/images/icon-message.svg";
import { Link } from "react-router-dom";
import "./Header.css";
import I18n from "../i18n";

function Header(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  const { accessToken } = useUser();
  const [isMenuActive, setMenuActive] = useState(false);

  const toggleMenu = () => {
    setMenuActive(!isMenuActive);
    var bodyClass = document.getElementsByTagName("body")[0];
    if (!isMenuActive) {
      bodyClass.classList.add("menu-active");
    } else {
      bodyClass.classList.remove("menu-active");
    }
  };

  const stripLocale = (pathname, lang) => {
    if (!lang) {
      return pathname;
    }
    if (process.env.PUBLIC_URL) {
      pathname = pathname
        .replace(process.env.PUBLIC_URL, "")
        .replace(`/${locale}`, "");
    } else {
      pathname = pathname.replace(`/${locale}`, "");
    }

    return pathname;
  };

  return (
    <header>
      <div className="container">
        <div className="navbar-header">
          {accessToken && (
            <div
              className={`toggle-button ${
                isMenuActive === true ? "menu-active" : ""
              }`}
              id="toggle"
              onClick={toggleMenu}
            >
              <span className="bar top"></span>
              <span className="bar middle"></span>
              <span className="bar bottom"></span>
            </div>
          )}
          <div className="logo">
            <Link to={`/${locale}/${accessToken ? `home` : `login`}`}>
              <img src={logo} alt="iRewards" />
            </Link>
          </div>
          {accessToken && (
            <div className="tools-button">
              <Link to="inbox">
                <img src={iconMessage} alt="" />
              </Link>
            </div>
          )}
        </div>
        {accessToken && (
          <nav
            id="overlay"
            className={`overlay ${isMenuActive === true ? "nav-active" : ""}`}
          >
            <div className="btn-close-holder">
              <button className="btn-close" onClick={toggleMenu}></button>
            </div>
            <div className="overlay-menu">
              <div className="container">
                <div className="row">
                  <div className="col-12">
                    <div className="overlay-top-holder">
                      <Link to="home" className="btn-circle btn-home">
                        <img src={iconHome} alt="" />
                      </Link>
                      <Link
                        to={`/${locale}/logout`}
                        className="btn-circle btn-logout"
                      >
                        <img src={iconLogout} alt="" />
                      </Link>
                    </div>
                  </div>
                </div>
                <div className="row">
                  <div className="col-6 col-sm-4 col-md-4 col-lg-2">
                    <Link
                      to={`/${locale}/points-balance`}
                      onClick={toggleMenu}
                      className="menu-item menu-points-balance"
                    >
                      <div className="menu-icon">
                        <img
                          src={iconPointsBalance}
                          alt={I18n.getTranslation(location, "points_balance")}
                        />
                      </div>
                      <div className="menu-title">
                        <I18n t="points_balance" />
                      </div>
                    </Link>
                  </div>
                  <div className="col-6 col-sm-4 col-md-4 col-lg-2">
                    <Link
                      to={`/${locale}/my-redemption-gift`}
                      onClick={toggleMenu}
                      className="menu-item menu-my-redemption"
                    >
                      <div className="menu-icon">
                        <img
                          src={iconMyRedemption}
                          alt={I18n.getTranslation(location, "my_redemption")}
                        />
                      </div>
                      <div className="menu-title">
                        <I18n t="my_redemption" />
                      </div>
                    </Link>
                  </div>
                  <div className="col-6 col-sm-4 col-md-4 col-lg-2">
                    <Link
                      to={`/${locale}/gifts`}
                      onClick={toggleMenu}
                      className="menu-item menu-gifts"
                    >
                      <div className="menu-icon">
                        <img
                          src={iconGifts}
                          alt={I18n.getTranslation(location, "gifts")}
                        />
                      </div>
                      <div className="menu-title">
                        <I18n t="gifts" />
                      </div>
                    </Link>
                  </div>
                  <div className="col-6 col-sm-4 col-md-4 col-lg-2">
                    <Link
                      to={`/${locale}/events`}
                      onClick={toggleMenu}
                      className="menu-item menu-events"
                    >
                      <div className="menu-icon">
                        <img
                          src={iconEvents}
                          alt={I18n.getTranslation(location, "events")}
                        />
                      </div>
                      <div className="menu-title">
                        <I18n t="events" />
                      </div>
                    </Link>
                  </div>
                  <div className="col-6 col-sm-4 col-md-4 col-lg-2">
                    <Link
                      to={`/${locale}/my-profile`}
                      onClick={toggleMenu}
                      className="menu-item menu-my-profile"
                    >
                      <div className="menu-icon">
                        <img
                          src={iconMyProfile}
                          alt={I18n.getTranslation(location, "my_profile")}
                        />
                      </div>
                      <div className="menu-title">
                        <I18n t="my_profile" />
                      </div>
                    </Link>
                  </div>
                  <div className="col-6 col-sm-4 col-md-4 col-lg-2">
                    <Link to="/" className="menu-item menu-about-us">
                      <div className="menu-icon">
                        <img
                          src={iconAboutUs}
                          alt={I18n.getTranslation(location, "about_us")}
                        />
                      </div>
                      <div className="menu-title">
                        <I18n t="about_us" />
                      </div>
                    </Link>
                  </div>
                </div>
                <div className="row">
                  <div className="col-12">
                    <div className="overlay-bottom-holder">
                      <Link
                        to={`/eng${stripLocale(pathname, locale)}`}
                        className={`btn-lang btn-eng ${
                          locale === "eng" ? "active" : ""
                        }`}
                        onClick={toggleMenu}
                      >
                        <div className="btn-lang-icon">EN</div>
                        <div className="btn-lang-title">English</div>
                      </Link>

                      <Link
                        to={`/tch${stripLocale(pathname, locale)}`}
                        className={`btn-lang btn-tch ${
                          locale === "tch" ? "active" : ""
                        }`}
                        onClick={toggleMenu}
                      >
                        <div className="btn-lang-icon"></div>
                        <div className="btn-lang-title"></div>
                      </Link>

                      <Link
                        to={`/sch${stripLocale(pathname, locale)}`}
                        className={`btn-lang btn-sch ${
                          locale === "sch" ? "active" : ""
                        }`}
                        onClick={toggleMenu}
                      >
                        <div className="btn-lang-icon"></div>
                        <div className="btn-lang-title"></div>
                      </Link>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </nav>
        )}
      </div>
    </header>
  );
}

export default Header;
