import React, { useState, useEffect } from "react";
import PointsBalanceComponent from "../components/PointsBalanceComponent";
import { useUser } from "../store/UseUser";
import { getBonusPointsHistory } from "../utils/httpUtils";
import { Link, useHistory } from "react-router-dom";
import "./Home.css";
import iconUserProfile from "../assets/images/icon-user-profile.svg";
import iconUserRedemption from "../assets/images/icon-user-redemption.svg";
import iconUserQrcode from "../assets/images/icon-user-qrcode.svg";
import imageGift from "../assets/images/gift.gif";
import imageTicket from "../assets/images/ticket.gif";
import imageNoBonus from "../assets/images/no_bonus.gif";
// import birthdayRewardIcon from "../assets/images/icon-birthday-reward.svg";
// import extraRewardIcon from "../assets/images/icon-extra-reward.svg";
import giftRedemptionIcon from "../assets/images/icon-gift-redemption.svg";
import spendingRewardIcon from "../assets/images/icon-spending-reward.svg";
import activityParticipationIcon from "../assets/images/icon-activity-participation.svg";
import rewardAdjustIcon from "../assets/images/icon-reward-adjust.svg";
import I18n from "../i18n";
import moment from "moment";
import QRCode from "react-qr-code";
import Loading from "../components/Loading";

function Home(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  document.body.className = "home";
  const { user, accessToken } = useUser();
  const memberProfile = user;
  const bonusPointsBalanceList = memberProfile.bonusPointsBalance;
  let history = useHistory();

  const RenderBonusPointsBalance = (data) => {
    if (data) {
      return Object.keys(data).map((item, index) => {
        return (
          <div className="list-item" key={index}>
            <div className="container">
              <I18n
                t="pointsExpiredOn"
                args={{
                  balance: data[item].balance,
                  date: data[item].expiryDate,
                }}
              />
            </div>
          </div>
        );
      });
    }
  };

  const getTotalPointsBalance = () => {
    var tempBalance;
    if (memberProfile.bonusPointsBalance) {
      tempBalance = 0;
      for (const bp of memberProfile.bonusPointsBalance) {
        tempBalance += bp.balance;
      }
    }
    return tempBalance;
  };

  let totalPointsBalance = getTotalPointsBalance();

  const [memberHistoryEmpty, setMemberHistoryEmpty] = useState(false);
  const [memberHistoryList, setMemberHistoryList] = useState([]);
  const [memberHistoryListLoaded, setMemberHistoryListLoaded] = useState(false);

  useEffect(() => {
    const fetchData = async () => {
      setMemberHistoryListLoaded(false);

      if (sessionStorage.getItem("memberToken") === "99999999") {
        setMemberHistoryEmpty(false);
        let responseData = require("../assets/json/mockPointsBalance.json");
        setMemberHistoryList(responseData);
        setMemberHistoryListLoaded(true);
      } else {
        getBonusPointsHistory(accessToken, {
          numberOfLastRecord: 50,
        })
          .then((response) => {
            let responseData = response.data.content.memberBonusTransactionList.sort(
              (a, b) => moment(b.createDate) - moment(a.createDate)
            );
            if (responseData.length === 0) {
              setMemberHistoryEmpty(true);
            } else {
              setMemberHistoryEmpty(false);
              setMemberHistoryList(responseData.slice(0, 6));
            }
            setMemberHistoryListLoaded(true);
          })
          .catch(function (error) {
            // history.push("/" + locale + "/logout");
          });
      }
    };
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const getActivitiesIcon = (e) => {
    if (e.action) {
      switch (e.action) {
        case "A":
          return rewardAdjustIcon;
        case "S":
          return spendingRewardIcon;
        case "D":
          return giftRedemptionIcon;
        case "G":
          return giftRedemptionIcon;
        case "R":
          return giftRedemptionIcon;
        case "C":
          return giftRedemptionIcon;
        case "T":
          return activityParticipationIcon;
        default:
          return giftRedemptionIcon;
      }
    } else {
      switch (e.type) {
        case "B":
          return rewardAdjustIcon;
        case "G":
          return giftRedemptionIcon;
        case "S":
          return giftRedemptionIcon;
        default:
          return rewardAdjustIcon;
      }
    }
  };

  const getActivitiesTitle = (e) => {
    if (e.action) {
      switch (e.action) {
        case "A":
          if (e.matchedRulesJson) {
            return e.matchedRulesJson.extraOffer ? (
              e.matchedRulesJson.extraOffer[0].objectName
            ) : (
                <I18n t="adjust" />
              );
          } else if (e.adjustReasonName) {
            return e.adjustReasonName ? (
              e.adjustReasonName
            ) : (
                <I18n t="adjust" />
              );
          }
          break;
        case "S":
          if (e.matchedRulesJson) {
            return e.matchedRulesJson.extraOffer ? (
              e.matchedRulesJson.extraOffer[0].objectName
            ) : (
                <I18n t="spending_add_points" />
              );
          }
          break;
        case "D":
          if (e.matchedRulesJson) {
            return e.matchedRulesJson.extraOffer ? (
              e.matchedRulesJson.extraOffer[0].objectName
            ) : (
                <I18n t="direct_add_point" />
              );
          }
          break;
        case "G":
          if (e.matchedRulesJson) {
            return e.matchedRulesJson.extraOffer ? (
              e.matchedRulesJson.extraOffer[0].objectName
            ) : (
                <I18n t="redeem_spending_gift" />
              );
          }
          break;
        case "R":
          if (e.matchedRulesJson) {
            return e.matchedRulesJson.extraOffer ? (
              e.matchedRulesJson.extraOffer[0].objectName
            ) : (
                <I18n t="redeem_bonus_points_gift" />
              );
          }
          break;
        case "C":
          if (e.matchedRulesJson) {
            return e.matchedRulesJson.extraOffer ? (
              e.matchedRulesJson.extraOffer[0].objectName
            ) : (
                <I18n t="redeem_bonus_points_coupon" />
              );
          }
          break;
        case "T":
          if (e.matchedRulesJson) {
            return e.matchedRulesJson.extraOffer ? (
              e.matchedRulesJson.extraOffer[0].objectName
            ) : (
                <I18n t="event_participation" />
              );
          }
          break;
        default:
          return "-";
      }
    } else {
      switch (e.type) {
        case "B":
          return <I18n t="earn_points" />;
        case "G":
          if (e.matchedRulesJson) {
            return e.matchedRulesJson.extraOffer ? (
              e.matchedRulesJson.extraOffer[0].objectName
            ) : (
                <I18n t="spendingredeem_gift_add_points" />
              );
          } else {
            return <I18n t="redeem_gift" />;
          }
        case "S":
          if (e.matchedRulesJson) {
            return e.matchedRulesJson.extraOffer ? (
              e.matchedRulesJson.extraOffer[0].objectName
            ) : (
                <I18n t="reserve_gift" />
              );
          } else {
            return <I18n t="reserve_gift" />;
          }
        default:
          return "-";
      }
    }
  };

  const getActivitiesActionType = (e) => {
    if (e.action) {
      switch (e.action) {
        case "A":
          return <I18n t="adjust" />;
        case "S":
          return <I18n t="spending_add_points" />;
        case "D":
          return <I18n t="direct_add_point" />;
        case "G":
          return <I18n t="redeem_spending_gift" />;
        case "R":
          return <I18n t="redeem_bonus_points_gift" />;
        case "C":
          return <I18n t="redeem_bonus_points_coupon" />;
        case "T":
          return <I18n t="event_participation" />;
        default:
          return "-";
      }
    } else {
      switch (e.type) {
        case "B":
          return <I18n t="earn_points" />;
        case "G":
          return <I18n t="redeem_gift" />;
        case "S":
          return <I18n t="reserve_gift" />;
        default:
          return "-";
      }
    }
  };

  const getActivitiesPoint = (e) => {
    let points = 0;
    if (e.points === 0 || e.earnedPoints === 0) {
      return (
        <I18n
          t="pts"
          args={{
            points: points,
          }}
        />
      );
    } else {
      let points = e.points ? e.points : e.earnedPoints;
      if (points > 0) {
        return (
          <I18n
            t="pts"
            args={{
              points: "+" + points,
            }}
          />
        );
      } else if (points < 0) {
        return (
          <I18n
            t="pts"
            args={{
              points: points,
            }}
          />
        );
      } else {
        return (
          <I18n
            t="pts"
            args={{
              points: 0,
            }}
          />
        );
      }
    }
  };

  let qrCode = memberProfile.memberId;
  const [isPopupActive, setIsPopupActive] = useState(false);
  const [isPopupQrcodeActive, setIsPopupQrcodeActive] = useState(false);
  const togglePopupClose = (e) => {
    e.preventDefault();
    setIsPopupActive(false);
  };
  const toggleQrcodePopup = (e) => {
    e.preventDefault();
    setIsPopupActive(true);
    setIsPopupQrcodeActive(true);
    window.scrollTo(0, 0);
  };

  return (
    <div>
      {totalPointsBalance >= 0 && (
        <PointsBalanceComponent totalPointsBalance={totalPointsBalance} />
      )}

      <div className="container-fluid home-user-info">
        <div className="container">
          <div className="row">
            <div className="col-6">
              {memberProfile && (
                <>
                  <span className="home-user-info-member-name">
                    Welcome back, {memberProfile.firstNameLang1}  {memberProfile.lastNameLang1}
                  </span>
                  <br />
                  <span className="home-user-info-member-id">
                    Your Member ID:  {memberProfile.memberId}
                  </span>
                </>
              )}
            </div>
            <div className="col-6">
              <ul className="home-user-info-navigation">
                <li>
                  <Link to="my-profile">
                    <img src={iconUserProfile} alt="" />
                  </Link>
                </li>
                <li>
                  <Link to="my-redemption-gift">
                    <img src={iconUserRedemption} alt="" />
                  </Link>
                </li>
                <li>
                  <Link to="/#" onClick={toggleQrcodePopup}>
                    <img src={iconUserQrcode} alt="" />
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>

      <div className="container-fluid home-points-expire section-title">
        <div className="container">
          <span>
            <I18n t="points_expire" />
          </span>
        </div>
      </div>

      <div className="container-fluid home-points-expire-list">
        {RenderBonusPointsBalance(bonusPointsBalanceList)}
      </div>

      <div className="container-fluid home-main-navigation">
        <div className="container">
          <div className="row">
            <div className="col-12 col-sm-6 home-main-left home-main-navigation-split">
              <div className="row">
                <div className="col-6 col-sm-6">
                  <span className="nav-title1 uppercase">
                    <I18n t="gift" />
                  </span>
                  <span className="nav-title2">
                    <I18n t="redemption" />
                  </span>
                  <span className="nav-content">
                    <I18n t="home_gift_intro1" />
                    <br />
                    <I18n t="home_gift_intro2" />
                  </span>
                  <Link to="gifts" className="nav-button uppercase">
                    <I18n t="gift_list" />
                  </Link>
                </div>
                <div className="col-6 col-sm-6 nav-icon">
                  <img src={imageGift} alt="" />
                </div>
              </div>
            </div>
            <div className="col-12 col-sm-6 home-main-right">
              <div className="row">
                <div className="col-6 col-sm-6">
                  <div className="event-content-holder">
                    <span className="nav-title1 uppercase">
                      <I18n t="event" />
                    </span>
                    <span className="nav-title2">
                      <I18n t="enrollment" />
                    </span>
                    <span className="nav-content">
                      <I18n t="home_event_intro1" />
                      <br />
                      <I18n t="home_event_intro2" />
                    </span>
                    <Link to="events" className="nav-button uppercase">
                      <I18n t="ticket_list" />
                    </Link>
                  </div>
                </div>
                <div className="col-6 col-sm-6 nav-icon">
                  <img src={imageTicket} alt="" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="container-fluid home-points-expire section-title">
        <div className="container">
          <span>
            <I18n t="recent_activities" />
          </span>
        </div>
      </div>

      {memberHistoryEmpty === true && (
        <div className="member-recent-history-empty">
          <img src={imageNoBonus} alt="" />
          <p>
            <I18n t="recent_activities_history_empty" />
          </p>
        </div>
      )}

      {memberHistoryEmpty === false && (
        <div className="container home-recent-activities-list">
          {!memberHistoryListLoaded && <Loading />}
          {memberHistoryList.length > 0 &&
            Object.keys(memberHistoryList).map((item, index) => {
              return (
                <div className="list-item row" key={index}>
                  <div className="list-item-icon-holder">
                    <div
                      className={`list-item-icon ${memberHistoryList[item].points > 0 ||
                        memberHistoryList[item].points.earnedPoints > 0
                        ? "points-plus"
                        : ""
                        }`}
                    >
                      <img
                        src={getActivitiesIcon(memberHistoryList[item])}
                        alt=""
                      />
                    </div>
                  </div>
                  <div className="list-item-content">
                    <div className="list-item-title">
                      {getActivitiesTitle(memberHistoryList[item])}
                    </div>
                    <div className="list-item-type">
                      {getActivitiesActionType(memberHistoryList[item])}
                    </div>
                    <div className="list-item-date">
                      {memberHistoryList[item].createDate}
                    </div>
                  </div>
                  <div className="list-item-right">
                    <div
                      className={`list-item-points-change ${memberHistoryList[item].points > 0 ||
                        memberHistoryList[item].earnedPoints > 0
                        ? "plus"
                        : ""
                        }`}
                    >
                      {getActivitiesPoint(memberHistoryList[item])}
                    </div>
                  </div>
                </div>
              );
            })}
        </div>
      )}

      <div
        id="page-popup"
        className={isPopupActive === true ? "popup-active" : ""}
      >
        <div className="btn-close-holder">
          <Link className="btn-close" to="/#" onClick={togglePopupClose}></Link>
        </div>
        <div
          id="qrcode"
          className={`page-popup-holder ${isPopupQrcodeActive === true ? "popup-qrcode-active" : ""
            }`}
        >
          <div className="container">
            <div className="page-popup-title">
              <span id="qrcode-member-name">
                {memberProfile.firstNameLang1}
                {memberProfile.lastNameLang1}
              </span>
              <span id="qrcode-member-id">{memberProfile.memberId}</span>
            </div>
            <div className="page-popup-content">
              <div className="qrCodeHolder">
                <div className="arrow-top"></div>
                <div id="memberQrCode">
                  <QRCode value={String(qrCode)} size={200} />
                </div>
                <div className="arrow-bottom"></div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="earn-points">
        <Link to={`./qrcode/scan`} className="uppercase">
          {<I18n t="earn_points" />}
        </Link>
      </div>
    </div>
  );
}

export default Home;
