import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import I18n from "../../i18n";
import { useForm } from "react-hook-form";
import PageTitleAnimation from "../../components/PageTitleAnimation";
import "./ForgotPassword.css";
import "../../Form.css";
import { forgotPassword, getEnumValue } from "../../utils/httpUtils";

function ForgotPassword(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  document.body.className = "forgot-password";
  let history = useHistory();
  const { register, errors, handleSubmit } = useForm();
  const [disableSubmitBtn, setDisableSubmitBtn] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const [phonePrefixOptions, setPhonePrefixOptions] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      getEnumValue()
        .then((response) => {
          setPhonePrefixOptions(response.data.PhonePrefix);
        })
        .catch(function (error) {
          // eslint-disable-next-line no-console
          console.log(error);
        });
    };
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const onSubmit = (data) => {
    setDisableSubmitBtn(true);
    forgotPassword({
      email: data.email,
      phonePrefix: data.phonePrefix,
      phone: data.phone,
    }).then((response) => {
      if (response.data.status === "S000") {
        setDisableSubmitBtn(true);
        setErrorMessage("");
        history.push("/" + locale + "/forgot-password-success");
      } else {
        setDisableSubmitBtn(false);
        setErrorMessage(response.data.message[0]);
        setTimeout(() => {
          setErrorMessage("");
        }, 5000);
      }
    });
  };

  return (
    <div>
      <div className="page-title">
        <div className="container">
          <PageTitleAnimation />
          <div className="page-title-content">
            <I18n t="forgot_password" />
          </div>
        </div>
      </div>
      <div className="page-main">
        <div className="container">
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="form" id="forgot-password-form">
              <div className="form-intro"></div>

              <div className="form-body">
                <div className="input-phone">
                  <div className="form-input">
                    <select
                      name="phonePrefix"
                      className="custom-select input-phone-dial-code"
                      ref={register()}
                    >
                      {Object.keys(phonePrefixOptions).map((item) => {
                        return (
                          <option
                            value={phonePrefixOptions[item].value}
                            key={phonePrefixOptions[item].key}
                          >
                            {phonePrefixOptions[item].name}
                          </option>
                        );
                      })}
                    </select>
                  </div>
                  <div className="form-input">
                    <input
                      type="text"
                      name="phone"
                      placeholder={I18n.getTranslation(
                        location,
                        "phone_number"
                      )}
                      autoComplete="off"
                      className={`form-control input-phone-number ${
                        errors.phone?.type === "required" ? "error" : ""
                      }`}
                      ref={register({ required: true, pattern: /[0-9]/ })}
                    />
                    <div className="error-icon"></div>
                  </div>
                  <div className="message">
                    {errors.phone?.type === "required" &&
                      I18n.getTranslation(location, "phone_number_required")}
                    {errors.phone?.type === "pattern" &&
                      I18n.getTranslation(location, "must_be_digit")}
                  </div>
                </div>

                <div className="form-input">
                  <input
                    type="email"
                    name="email"
                    placeholder={I18n.getTranslation(location, "email")}
                    autoComplete="off"
                    className={`form-control ${
                      errors.email?.type === "required" ? "error" : ""
                    }`}
                    ref={register({
                      required: true,
                      pattern: {
                        value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                      },
                    })}
                  />
                  <div className="error-icon"></div>
                </div>
                <div className="message">
                  {errors.email?.type === "required" &&
                    I18n.getTranslation(location, "email_required")}
                  {errors.email?.type === "pattern" &&
                    I18n.getTranslation(location, "incorrect_email_format")}
                </div>

                {errorMessage && <div className="message">{errorMessage}</div>}
              </div>

              <div className="form-footer">
                <div className="form-footer-button">
                  <button
                    type="submit"
                    className="btn btn-submit uppercase btn-secondary"
                    disabled={disableSubmitBtn}
                  >
                    <I18n t="submit" />
                  </button>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}

export default ForgotPassword;
