import React from "react";
import { Link } from "react-router-dom";
import I18n from "../../i18n";
import "./Verification.css";
import ResultMessage from "../../components/ResultMessage";
import FailImg from "../../assets/images/fail.gif";
import PageBgAnimation from "../../components/PageBgAnimation";

function VerificationFailed(props) {
  const { location } = props;
  document.body.className = "result result-failed";

  function verificationBack(e) {
    e.preventDefault();
    props.history.goBack();
  }

  return (
    <div className="container">
      <PageBgAnimation bgColor="#D4CECA" />
      <ResultMessage
        image={FailImg}
        title={I18n.getTranslation(location, "verification_fail_title")}
        content={
          I18n.getTranslation(location, "verification_fail_content1") +
          "<br />" +
          I18n.getTranslation(location, "verification_fail_content2")
        }
        buttonText={I18n.getTranslation(
          location,
          "verification_fail_buttonText"
        )}
      />
      <div className="result-holder">
        <div className="result-button">
          <Link to="/" onClick={verificationBack}>
            <I18n t="verification_fail_buttonText" />
          </Link>
        </div>
      </div>
    </div>
  );
}

export default VerificationFailed;
