import React, { useState } from "react";
import Collapse from "react-bootstrap/Collapse";
import moment from "moment";
import I18n from "../../i18n";

export default (props) => {
  const dateFormat = (date) => {
    return moment(date).format("YYYY-MM-DD");
  };

  const getBrandName = (id) => {
    let brand = props.brand;
    brand = brand.filter(function (v) {
      return v.mallId === id;
    });

    if (brand[0]) {
      if (props.lang === "eng") {
        return brand[0].nameLang1;
      } else if (props.lang === "tch") {
        return brand[0].nameLang2;
      } else if (props.lang === "sch") {
        return brand[0].nameLang3;
      }
    } else {
      return <I18n t="super_mall" />;
    }
  };

  const getActionName = (action) => {
    switch (action) {
      case "A":
        return <I18n t="adjust" />;
      case "S":
        return <I18n t="spending_add_points" />;
      case "D":
        return <I18n t="direct_add_point" />;
      case "G":
        return <I18n t="redeem_spending_gift" />;
      case "R":
        return <I18n t="redeem_bonus_points_gift" />;
      case "C":
        return <I18n t="redeem_bonus_points_coupon" />;
      case "T":
        return <I18n t="event_participation" />;
      default:
        return "-";
    }
  };

  function renderRecord(history) {
    return history.map((item, parentInex) => {
      const [isOpen, setOpen] = useState(parentInex === 0 ? true : false);
      function toggle(e) {
        e.preventDefault();
        setOpen(!isOpen);
        return;
      }
      return (
        <div className="card record" key={parentInex}>
          <header role="tab" className="card-header">
            <a
              href="/#"
              onClick={toggle}
              aria-controls={`collapse-` + parentInex}
              aria-expanded={isOpen}
              className={`btn btn-secondary btn-block ${
                !isOpen ? "collapsed" : ""
              }`}
            >
              <span className="date">{dateFormat(item.createDate)}</span>
            </a>
          </header>
          <Collapse in={isOpen}>
            <div id={`collapse-` + parentInex}>
              <div className="card-body">
                <div className="receipt">
                  <table className="history-record-table">
                    <tbody>
                      <tr>
                        <td>
                          <I18n t="action" />
                        </td>
                        <td>{getActionName(item.action)}</td>
                      </tr>
                      <tr>
                        <td>
                          <I18n t="brand" />
                        </td>
                        <td>{getBrandName(item.mallId)}</td>
                      </tr>
                      {item.matchedRulesJson &&
                        item.matchedRulesJson.extraOffer && (
                          <tr>
                            {item.action !== "T" && (
                              <td>
                                <I18n t="gift" />
                              </td>
                            )}
                            {item.action === "T" && (
                              <td>
                                <I18n t="event" />
                              </td>
                            )}
                            <td>
                              {item.matchedRulesJson.extraOffer[0].objectName}
                            </td>
                          </tr>
                        )}
                      <tr>
                        <td>
                          <I18n t="points" />
                        </td>
                        <td>
                          {item.points > 0 ? "+" : ""}
                          {
                            <I18n
                              t="pts"
                              args={{
                                points: item.points,
                              }}
                            />
                          }
                        </td>
                      </tr>
                      {item.adjustReasonName ? (
                        <tr>
                          <td>
                            <I18n t="adjust_reason" />
                          </td>
                          <td>{item.adjustReasonName}</td>
                        </tr>
                      ) : null}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </Collapse>
        </div>
      );
    });
  }
  return <div className="record-list">{renderRecord(props.history)}</div>;
};
