import React from "react";
import I18n from "../../i18n";
import "./Verification.css";
import ResultMessage from "../../components/ResultMessage";
import SuccessImg from "../../assets/images/success.gif";
import PageBgAnimation from "../../components/PageBgAnimation";

function VerificationSuccess(props) {
  const { location } = props;
  document.body.className = "result result-success";

  return (
    <div className="container">
      <PageBgAnimation bgColor="#FFD428" />
      <ResultMessage
        image={SuccessImg}
        title={I18n.getTranslation(location, "verification_success_title")}
        content={I18n.getTranslation(location, "verification_success_content")}
        buttonText={I18n.getTranslation(
          location,
          "verification_success_buttonText"
        )}
        buttonLink="login"
        buttonCount={1}
      />
    </div>
  );
}

export default VerificationSuccess;
