import React from "react";
import I18n from "../../i18n";
import "./ForgotPassword.css";
import ResultMessage from "../../components/ResultMessage";
import SuccessImg from "../../assets/images/success.gif";
import PageBgAnimation from "../../components/PageBgAnimation";

function ForgotPasswordSuccess(props) {
  const { location } = props;
  document.body.className = "result result-success";

  return (
    <div className="container">
      <PageBgAnimation bgColor="#FFD428" />
      <ResultMessage
        image={SuccessImg}
        title={I18n.getTranslation(location, "forgot_password_success_title")}
        content={
          I18n.getTranslation(location, "forgot_password_success_content1") +
          "<br />" +
          I18n.getTranslation(location, "forgot_password_success_content2")
        }
        buttonText={I18n.getTranslation(
          location,
          "forgot_password_success_button_text"
        )}
      />
    </div>
  );
}

export default ForgotPasswordSuccess;
