import React from "react";
import { Link } from "react-router-dom";
import I18n from "../i18n";
import "./PointsBalance.css";

let totalPointsBalance = 0;

export default class PointsBalanceComponent extends React.Component {
  constructor(props) {
    super(props);
    totalPointsBalance = props.totalPointsBalance
      ? props.totalPointsBalance
      : 0;
    this.state = { smallFontSize: false };
  }

  componentDidMount() {
    const checkSize = (val) => {
      if (val) {
        if (val.toString().length >= 7) {
          this.setState({
            smallFontSize: true,
          });
        }
      }
    };

    checkSize(totalPointsBalance);
  }

  render() {
    return (
      <div className="container-fluid home-points-balance">
        <div className="container text-center">
          <div id="home-points-balance-holder">
            <strong className="home-points-balance-title">
              <I18n t="points_balance" />
            </strong>
            <div className="home-points-balance-circle-holder">
              <div className="quarter top"></div>
              <div className="quarter bottom-left"></div>
              <div className="home-points-balance-circle">
                <div className="quarter top"></div>
                <div className="quarter bottom-left"></div>
                <div className="home-points-balance-circle-inner">
                  <Link
                    to="points-balance"
                    className="icon-points-balance"
                  ></Link>
                  <span
                    className={`${this.state.smallFontSize ? "small" : ""}`}
                  >
                    {totalPointsBalance}
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
