import React, { createContext, useContext, useEffect, useState } from "react";
import { lmsMemberProfile } from "../utils/httpUtils";
import Cookies from "js-cookie";

const inOneHour = new Date(new Date().getTime() + 60 * 60 * 1000);

const initialState = {
  user: {},
  accessToken: undefined,
};

const UserContext = createContext(initialState);

export function UserProvider({ children }) {
  let memberToken = null;
  let mockMemberProfile = require("../assets/json/mockMemberProfile.json");

  if (Cookies.get("isStayLogin") === "true") {
    memberToken = Cookies.get("memberToken");
  } else {
    memberToken = sessionStorage.getItem("memberToken");
  }
  const [accessToken, setAccessToken] = useState(memberToken);
  const [user, setUser] = useState({});

  useEffect(() => {
    const handleAccessTokenChange = async () => {
      if (accessToken) {
        if (accessToken === "99999999") {
          setUser(mockMemberProfile);
          sessionStorage.setItem("memberToken", accessToken);
        } else {
          lmsMemberProfile(accessToken).then((response) => {
            setUser(response.data.content);
          });
          if (Cookies.get("isStayLogin") === "true") {
            Cookies.set("memberToken", accessToken, { expires: inOneHour });
            Cookies.set("isStayLogin", "true", { expires: inOneHour });
          } else {
            sessionStorage.setItem("memberToken", accessToken);
          }
        }
      } else {
        if (Cookies.get("isStayLogin")) {
          Cookies.remove("isStayLogin");
          Cookies.remove("memberToken");
        } else {
          sessionStorage.removeItem("memberToken");
        }
        setUser({});
      }
    };

    handleAccessTokenChange();
  }, [accessToken]);

  return (
    <UserContext.Provider
      value={{
        user,
        setUser,
        accessToken,
        setAccessToken,
      }}
    >
      {children}
    </UserContext.Provider>
  );
}

export const useUser = () => useContext(UserContext);
