import React, { useState, useEffect } from "react";
import { useUser } from "../../store/UseUser";
import {
  lmsMemberProfile,
  getBrandList,
  getShopList,
  getGiftList,
} from "../../utils/httpUtils";
import { Link, useHistory } from "react-router-dom";
import I18n from "../../i18n";
import Loading from "../../components/Loading";
import "./Gift.css";
import iconCrown from "../../assets/images/icon-crown.svg";
import noImage from "../../assets/images/sample/noimage.png";
import Slider from "react-slick";
import "../../assets/css/slick.css";
import "../../assets/css/slick-theme.css";
import { useForm } from "react-hook-form";

function GiftDetail(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  document.body.className = "gift-detail";
  const { accessToken } = useUser();
  let history = useHistory();
  let giftId = props.match.params.giftId;
  if (!giftId) {
    history.push("/" + locale + "/gifts");
  }

  const { register, errors, handleSubmit } = useForm();
  const [loading, setLoading] = useState(true);
  const [gift, setGift] = useState([]);
  const [redeemBonusPointsBalance, setRedeemBonusPointsBalance] = useState(0);
  const [brandList, setBrandList] = useState([]);
  const [shopList, setShopList] = useState([]);
  const [hiddenBrand, setHiddenBrand] = useState(false);
  const [hiddenShop, setHiddenShop] = useState(false);
  const [shop, setShop] = useState(0);
  const [brand, setBrand] = useState(0);
  const [giftInventory, setGiftInventory] = useState(0);
  const [qty, SetQty] = useState(1);
  const imageSliderSettings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    centerMode: false,
  };
  const [disableSubmitBtn, setDisableSubmitBtn] = useState(false);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      let userMemberLevelId = null;
      if (sessionStorage.getItem("memberToken") === "99999999") {
        let giftJson = require("../../assets/json/mockGift.json");
        getRedeemBonusPoints(giftJson[0], 0);
        setGiftInventory(getGiftInventory(giftJson[0]));
        setHiddenBrand(false);
        setHiddenShop(false);
        setBrand(0);
        setShop(0);
        setGift(giftJson[0]);
        setLoading(false);
      } else {
        lmsMemberProfile(accessToken).then((response) => {
          userMemberLevelId = response.data.content.memberLevelId;

          getGiftList({
            rewardId: giftId,
          }).then((response) => {
            if (response.data.content.rewardList.length === 0) {
              history.push("/" + locale + "/gifts");
            } else {
              setGift(response.data.content.rewardList[0]);
              let currentGift = response.data.content.rewardList[0];
              getRedeemBonusPoints(currentGift, userMemberLevelId);

              getBrandList().then((response) => {
                var tempList = response.data.content.mallList;
                if (currentGift.mallId === 0) {
                  if (currentGift.inventoryMode === "M") {
                    // mall_id 0 and inventory_mode M:
                    // no selection box,
                    // reserve by mallId = 0, shopId  = 0
                    setHiddenBrand(true);
                    setHiddenShop(true);
                    setBrand(0);
                    setShop(0);
                  } else if (currentGift.inventoryMode === "B") {
                    // mall_id 0 and inventory_mode B:
                    // only brand selection box using reserveInMall as options, get label by mallList API
                    // reserve by mallId = selected value, shopId = 0
                    setHiddenBrand(false);
                    setHiddenShop(true);
                    if (currentGift.reserveInMall) {
                      let reserveInMallList = currentGift.reserveInMall.mallId;
                      let filteredList = tempList.filter(function (a) {
                        for (let i = 0; i < reserveInMallList.length; i++) {
                          if (reserveInMallList[i] === a.mallId) {
                            return reserveInMallList[i] === a.mallId;
                          }
                        }
                        return false;
                      });
                      setBrandList(filteredList);
                      setShop(0);
                    }
                  } else if (currentGift.inventoryMode === "S") {
                    // mall_id 0 and inventory_mode S:
                    // both brand selection box and shop selection box
                    // brand selection box using reserveInMall as options, get label by mallList API
                    // shop selection box using shopList API by mallId selected in brand selection box
                    // reserve by mallId = selected mall value, shopId = selected shop value
                    setHiddenBrand(false);
                    setHiddenShop(false);
                    if (currentGift.reserveInMall) {
                      let reserveInMallList = currentGift.reserveInMall.mallId;
                      let filteredList = tempList.filter(function (a) {
                        for (let i = 0; i < reserveInMallList.length; i++) {
                          if (reserveInMallList[i] === a.mallId) {
                            return reserveInMallList[i] === a.mallId;
                          }
                        }
                        return false;
                      });
                      setBrandList(filteredList);
                    }
                  }
                } else {
                  if (currentGift.inventoryMode === "B") {
                    // mall_id > 0 and inventory_mode B:
                    // no selection box
                    // reserve by gift's mallId, shopId = 0
                    setHiddenBrand(true);
                    setHiddenShop(true);
                    setBrandList(tempList);
                    setBrand(currentGift.mallId);
                    setShop(0);
                  } else if (currentGift.inventoryMode === "S") {
                    // mall_id > 0 and inventory_mode S:
                    // only shop selection box using shopList API by mallId
                    // reserve by mallId = gift's mall_id, shopId = selected shop value using
                    setHiddenBrand(true);
                    setHiddenShop(false);
                    setBrandList(tempList);
                    setBrand(currentGift.mallId);

                    getShopList({
                      mallId: currentGift.mallId,
                    }).then((response) => {
                      setShopList(response.data.content.shopList);
                    });
                  } else {
                    tempList = tempList.filter(function (a) {
                      return a.mallId === currentGift.mallId;
                    });
                    setBrandList(tempList);
                  }
                }

                setGiftInventory(getGiftInventory(currentGift));
                setLoading(false);
              });
            }
          });
        });
      }
    };
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [locale]);

  const getRedeemBonusPoints = (gift, userMemberLevelId) => {
    for (var i = 0; i < gift.redeemBonusPoints.length; i++) {
      if (
        gift.redeemBonusPoints[i].memberLevelId === String(userMemberLevelId)
      ) {
        setRedeemBonusPointsBalance(
          gift.redeemBonusPoints[i].redeemBonusPointsBalance
        );
        break;
      }
    }
  };

  const getBrandName = (e) => {
    let brandName = "";
    let brandId = Number(e);
    if (brandId === 0) {
      brandName = <I18n t="super_mall" />;
    } else {
      for (let i = 0; i < brandList.length; i++) {
        if (brandList[i].mallId === brandId) {
          switch (locale) {
            case "eng":
              brandName = brandList[i].nameLang1;
              break;
            case "tch":
              brandName = brandList[i].nameLang2;
              break;
            case "sch":
              brandName = brandList[i].nameLang3;
              break;
            default:
              brandName = brandList[i].nameLang1;
              break;
          }
        }
      }
    }
    return brandName;
  };

  const getGiftInventory = (currentGift) => {
    var inventoryData = 0;
    var currentGiftMallId = currentGift.mallId;
    var currentGiftInventoryMode = currentGift.inventoryMode;
    var inventoryList = currentGift.inventory;

    if (currentGiftMallId === 0) {
      if (currentGiftInventoryMode === "M") {
        // mall_id 0 and inventory_mode M:
        // check if "inventoryList[mallId= 0].stockLevel - reservedStockLevel" > 0
        if (inventoryList.length) {
          for (let i = 0; i < inventoryList.length; i++) {
            if (inventoryList[i].mallId === 0) {
              inventoryData =
                inventoryList[i].stockLevel -
                inventoryList[i].reservedStockLevel;
              break;
            }
          }
        }
      } else if (
        currentGiftInventoryMode === "B" ||
        currentGiftInventoryMode === "S"
      ) {
        // mall_id 0 and inventory_mode B/S:
        // for each options in brand selection box, check if "inventoryList[mall_id].stockLevel - reservedStockLevel" > 0, disable the option if not > 0
        if (currentGift.reserveInMall) {
          var reserveInMallList = currentGift.reserveInMall.mallId;
          var filteredList = currentGift.inventory.filter(function (a) {
            for (let i = 0; i < reserveInMallList.length; i++) {
              if (reserveInMallList[i] === a.mallId) {
                return reserveInMallList[i] === a.mallId;
              }
            }
            return false;
          });

          var totalStock = 0;
          if (filteredList.length) {
            for (let i = 0; i < filteredList.length; i++) {
              totalStock +=
                filteredList[i].stockLevel - filteredList[i].reservedStockLevel;
            }
          }
          inventoryData = totalStock;
        }
      }
    } else {
      if (
        currentGift.inventoryMode === "B" ||
        currentGift.inventoryMode === "S"
      ) {
        // mall_id > 0 and inventory_mode B/S:
        // check if "inventoryList[mall_id].stockLevel - reservedStockLevel" > 0
        if (inventoryList.length) {
          for (let i = 0; i < inventoryList.length; i++) {
            if (inventoryList[i].mallId === currentGiftMallId) {
              inventoryData =
                inventoryList[i].stockLevel -
                inventoryList[i].reservedStockLevel;
              break;
            }
          }
        }
      }
    }

    return inventoryData;
  };

  const optionInventoryChecking = (mall) => {
    let currentMallStock = 0;
    let optionDisable = false;

    if (gift.mallId === 0) {
      if (gift.inventoryMode === "B" || gift.inventoryMode === "S") {
        if (gift.inventory.length > 0) {
          for (let i = 0; i < gift.inventory.length; i++) {
            if (gift.inventory[i].mallId === mall) {
              currentMallStock +=
                gift.inventory[i].stockLevel -
                gift.inventory[i].reservedStockLevel;
              break;
            }
          }
        }
      }
    }

    optionDisable = currentMallStock > 0 ? false : true;
    return optionDisable;
  };

  const handleErrorImg = (e) => {
    e.target.src = noImage;
  };

  const handleBrandChange = (e) => {
    setBrand(e.target.value);
  };

  const handleShopChange = (e) => {
    setShop(e.target.value);
  };

  const increaseRedeemCount = () => {
    let temp = qty;
    temp++;
    SetQty(temp);
  };

  const decreaseRedeemCount = () => {
    if (qty > 1) {
      let temp = qty;
      temp--;
      SetQty(temp);
    }
  };

  const handleQtyChange = (e) => {
    SetQty(e.target.value);
  };

  const onSubmit = (data) => {
    setDisableSubmitBtn(true);

    var redeemObject = {
      redeemMallId: data.brand ? data.brand : brand,
      redeemShop: data.shop ? data.shop : shop,
      redeemGiftId: gift.giftId,
      redeemGiftImage: gift.image,
      redeemGiftDetail: gift.giftDescription,
      redeemGiftQty: qty,
      redeemGiftPoint: redeemBonusPointsBalance,
    };

    sessionStorage.setItem("redeemObject", JSON.stringify(redeemObject));

    history.push({
      pathname: "/" + locale + "/order-preview",
      state: { redeemObject: redeemObject },
    });
  };

  return (
    <div>
      <div className="page-intro">
        <div className="container-fluid">
          <div className="container">
            <span className="gift-name">{gift.giftName}</span>
            <span className="gift-pts">
              <span>
                <I18n
                  t="pts"
                  args={{
                    points: redeemBonusPointsBalance,
                  }}
                />
              </span>
            </span>
            <div className="gift-top-info">
              <div className="gift-inventory">
                <I18n
                  t="giftInventory"
                  args={{
                    value: giftInventory,
                  }}
                />
              </div>
              {gift.hotpick && (
                <div className="gift-crown">
                  <img src={iconCrown} alt="" />
                </div>
              )}
            </div>
          </div>
        </div>
      </div>

      <form onSubmit={handleSubmit(onSubmit)}>
        <div className="page-main">
          {loading && <Loading />}
          <div className="container">
            {!hiddenBrand && brandList.length > 0 && (
              <>
                <select
                  id="brand"
                  name="brand"
                  className="custom-select"
                  ref={register({ required: true })}
                  onChange={handleBrandChange}
                >
                  <option value="">
                    {I18n.getTranslation(location, "please_select_brand")}
                  </option>
                  {Object.keys(brandList).map((item) => {
                    return (
                      <option
                        value={brandList[item].mallId}
                        key={item}
                        disabled={optionInventoryChecking(
                          brandList[item].mallId
                        )}
                      >
                        {getBrandName(brandList[item].mallId)}
                      </option>
                    );
                  })}
                </select>
                <div className="message">
                  {errors.brand?.type === "required" &&
                    I18n.getTranslation(
                      location,
                      "please_select_brand_for_redeem"
                    )}
                </div>
              </>
            )}

            {!hiddenShop && brand !== 0 && brand !== null && (
              <>
                <select
                  id="shop"
                  name="shop"
                  className="custom-select"
                  ref={register({ required: true })}
                  onChange={handleShopChange}
                >
                  <option value="">
                    {I18n.getTranslation(location, "please_select_shop")}
                  </option>
                  {Object.keys(shopList).map((item) => {
                    return (
                      <option value={shopList[item].shopId} key={item}>
                        {shopList[item].shopName}
                      </option>
                    );
                  })}
                </select>
                <div className="message">
                  {errors.shop?.type === "required" &&
                    I18n.getTranslation(
                      location,
                      "please_select_shop_for_redeem"
                    )}
                </div>
              </>
            )}

            <div className="gift-images">
              <Slider {...imageSliderSettings}>
                <div className="item">
                  <img
                    src={gift.image ? gift.image : noImage}
                    onError={handleErrorImg}
                    alt=""
                  />
                </div>
              </Slider>
            </div>

            {gift.giftDescription && (
              <div className="gift-detail">
                <h3>
                  <I18n t="detail" />
                </h3>
                <p>{gift.giftDescription}</p>
              </div>
            )}

            {gift.giftTnc && (
              <>
                <hr />
                <div className="gift-terms">
                  <h3>
                    <I18n t="terms_and_conditions" />
                  </h3>
                  <p>{gift.giftTnc}</p>
                </div>
              </>
            )}
          </div>
        </div>

        {giftInventory === 0 && brand !== "" && shop !== "" && (
          <div className="page-bottom">
            <div className="container-fluid">
              <div className="container text-center">
                <I18n t="out_of_stock" />
              </div>
            </div>
          </div>
        )}

        {giftInventory > 0 && (
          <div className="page-bottom">
            <div className="container-fluid">
              <div className="container">
                <div className="redeem-count-input">
                  <button
                    type="button"
                    className="btn btn-redeem-control btn-plus btn-secondary"
                    onClick={increaseRedeemCount}
                  ></button>
                  <input
                    type="number"
                    className="redeem-count form-control"
                    name="qty"
                    value={qty}
                    onChange={handleQtyChange}
                    ref={register({ required: true, min: 1 })}
                  />
                  <button
                    type="button"
                    className="btn btn-redeem-control btn-minus btn-secondary"
                    onClick={decreaseRedeemCount}
                  ></button>
                  <button
                    type="submit"
                    className="btn btn-redeem uppercase btn-secondary"
                    disabled={disableSubmitBtn}
                  >
                    <I18n t="redeem" />
                  </button>
                </div>
                <div className="message text-center">
                  <br />
                  {errors.qty &&
                    I18n.getTranslation(location, "min_number_of_gift")}
                </div>
              </div>
            </div>
          </div>
        )}
      </form>

      <div className="page-black">
        <Link to={`/${locale}/gifts`} className="btn-back"></Link>
      </div>
    </div>
  );
}

export default GiftDetail;
