import React, { useState, useEffect } from "react";
// import { useUser } from "../../store/UseUser";
import { getEventList } from "../../utils/httpUtils";
import { Link } from "react-router-dom";
import I18n from "../../i18n";
import Loading from "../../components/Loading";
import Nouislider from "nouislider-react";
import "nouislider/distribute/nouislider.css";
import wNumb from "wnumb";
import "./Event.css";
import imageNoGiftTicket from "../../assets/images/no_gift_ticket.gif";
import noImage from "../../assets/images/sample/noimage.png";
import Card from "react-bootstrap/Card";
import Accordion from "react-bootstrap/Accordion";
import { useAccordionToggle } from "react-bootstrap/AccordionToggle";

function Event(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  document.body.className = "event";
  // const { user } = useUser();

  const [loading, setLoading] = useState(true);
  const [eventList, setEventList] = useState([]);
  const [originalEventList, setOriginEventList] = useState([]);
  const [sorting, setSorting] = useState(2);
  const [sortingText, setSortingText] = useState("points_high_to_low");
  const [showSorting, setShowSorting] = useState(false);
  const [showFilter, setShowFilter] = useState(false);
  const [eventFiltered, setEventFiltered] = useState(false);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);

      getEventList()
        .then((response) => {
          let tempEventList = response.data.content.eventList;
          // Default get sorting Points high to low
          tempEventList = tempEventList.sort(function (a, b) {
            return b.bonusPointRequired - a.bonusPointRequired;
          });
          setEventList(tempEventList);
          setOriginEventList(tempEventList);
          setLoading(false);
        })
        .catch(function (error) {
          // eslint-disable-next-line no-console
          console.log(error);
        });
    };
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [locale]);

  const toggleSorting = (e) => {
    e.preventDefault();
    setShowSorting(!showSorting);
    setShowFilter(false);
  };

  const toggleFilter = (e) => {
    e.preventDefault();
    setShowSorting(false);
    setShowFilter(!showFilter);
  };

  const toggleCloseOverlay = () => {
    setShowSorting(false);
    setShowFilter(false);
  };

  const getSortingData = (sortingVal, sortingTextVal) => {
    let tempEventList = eventList;

    if (sortingVal === 0) {
      // Sorting: Newest && by display order
      tempEventList = tempEventList.sort((a, b) =>
        a.onlineDate < b.onlineDate ? 1 : b.onlineDate < a.onlineDate ? -1 : 0
      );
    } else if (sortingVal === 1) {
      // Sorting: Points low to high
      tempEventList = tempEventList.sort(function (a, b) {
        return a.bonusPointRequired - b.bonusPointRequired;
      });
    } else if (sortingVal === 2) {
      // Sorting: Points high to low
      tempEventList = tempEventList.sort(function (a, b) {
        return b.bonusPointRequired - a.bonusPointRequired;
      });
    }

    setEventList(tempEventList);
    setSorting(sortingVal);
    setSortingText(sortingTextVal);
    toggleCloseOverlay();
  };

  const CustomToggle = ({ children, eventKey, active }) => {
    const [condition, setCondition] = useState(active);
    const customOnClick = useAccordionToggle(eventKey, () => {
      setCondition(!condition);
    });
    return (
      <div
        onClick={customOnClick}
        className={`card-header ${condition ? "show" : ""}`}
      >
        {children}
      </div>
    );
  };

  const sliderConfig = {
    start: [0, 1000],
    step: 1,
    connect: true,
    range: {
      min: 0,
      "30%": 300,
      "60%": 500,
      max: 1000,
    },
    pips: {
      mode: "range",
      density: 5,
    },
  };

  const tooltips = [wNumb({ decimals: 0 }), wNumb({ decimals: 0 })];

  const [rangeSliderRef, setRangeSliderRef] = useState(null);
  const onRangeSliderRefUpdate = () => {
    rangeSliderRef.noUiSlider.updateOptions({
      tooltips: tooltips,
    });
  };
  const [pointsRangeMinValue, setPointsRangeMinValue] = useState("0");
  const [pointsRangeMaxValue, setPointsRangeMaxValue] = useState("1000");
  const onRangeUpdate = (values) => {
    setPointsRangeMinValue(parseFloat(values[0]).toFixed(0));
    setPointsRangeMaxValue(parseFloat(values[1]).toFixed(0));
  };

  if (rangeSliderRef) {
    onRangeSliderRefUpdate();
  }

  const resetFilteredList = () => {
    setEventFiltered(false);
    setPointsRangeMinValue(sliderConfig.range.min);
    setPointsRangeMaxValue(sliderConfig.range.max);
    rangeSliderRef.noUiSlider.set(sliderConfig.start);
    setEventList(originalEventList);
    toggleCloseOverlay();
    resetAccordion();
  };

  const resetAccordion = useAccordionToggle("1", () => {});

  const getFilteredList = () => {
    setEventFiltered(true);
    var filteredList = originalEventList;

    // Range filter
    if (pointsRangeMinValue !== null && pointsRangeMaxValue !== null) {
      filteredList = filteredList.filter(function (s) {
        let pointsBalance = parseInt(s.bonusPointRequired);
        if (
          pointsBalance >= parseInt(pointsRangeMinValue) &&
          pointsBalance <= parseInt(pointsRangeMaxValue)
        ) {
          return (
            pointsBalance >= parseInt(pointsRangeMinValue) &&
            pointsBalance <= parseInt(pointsRangeMaxValue)
          );
        }
        return false;
      });
    }

    setEventList(filteredList);
    toggleCloseOverlay();
  };

  const getEventQuota = (sessionList) => {
    if (sessionList) {
      let quota = 0;
      // let offlineQuota = 0;

      for (let i = 0; i < sessionList.length; i++) {
        quota += sessionList[i].quota;
        // offlineQuota += sessionList[i].offlineQuota;
      }

      let totalQuota = quota;
      return totalQuota;
    }
  };

  const getEventRemainQuota = (sessionList) => {
    if (sessionList) {
      let remainQuota = 0;
      // let remainOfflineQuota = 0;

      for (let i = 0; i < sessionList.length; i++) {
        remainQuota += sessionList[i].remainQuota;
        // remainOfflineQuota += sessionList[i].remainOfflineQuota;
      }

      let totalRemainQuota = remainQuota;
      return totalRemainQuota;
    }
  };

  const handleErrorImg = (e) => {
    e.target.src = noImage;
  };

  return (
    <div>
      <div className="page-tab">
        <div className="container-fluid">
          <div className="page-tab-holder">
            <div className="container">
              <ul className="page-tab-item">
                <li className="active">
                  <Link to="#">
                    <I18n t="ticket" />
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>

      <div className="page-nav">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <div className="list-sorting">
                <Link
                  className={`btn-toggle-sorting ${
                    showSorting ? "collapsed" : ""
                  }`}
                  to="#"
                  onClick={toggleSorting}
                >
                  <I18n t={sortingText} />
                </Link>

                <div
                  className={`list-sorting-panel ${
                    showSorting === true ? "show" : ""
                  }`}
                >
                  <div className="custom-control custom-radio">
                    <input
                      type="radio"
                      name="sorting"
                      className="custom-control-input"
                      value="0"
                      id="newest"
                      onChange={() => getSortingData(0, "newest")}
                      checked={sorting === 0}
                    />
                    <label className="custom-control-label" htmlFor="newest">
                      <I18n t="newest" />
                    </label>
                  </div>

                  <div className="custom-control custom-radio">
                    <input
                      type="radio"
                      name="sorting"
                      className="custom-control-input"
                      value="1"
                      id="pointsLowToHigh"
                      onChange={() => getSortingData(1, "points_low_to_high")}
                      checked={sorting === 1}
                    />
                    <label
                      className="custom-control-label"
                      htmlFor="pointsLowToHigh"
                    >
                      <I18n t="points_low_to_high" />
                    </label>
                  </div>

                  <div className="custom-control custom-radio">
                    <input
                      type="radio"
                      name="sorting"
                      className="custom-control-input"
                      value="2"
                      id="pointsHighToLow"
                      onChange={() => getSortingData(2, "points_high_to_low")}
                      checked={sorting === 2}
                    />
                    <label
                      className="custom-control-label"
                      htmlFor="pointsHighToLow"
                    >
                      <I18n t="points_high_to_low" />
                    </label>
                  </div>
                </div>
              </div>

              <div className="list-filter">
                <Link
                  className={`btn-toggle-filter ${
                    eventFiltered === true ? "filtered" : ""
                  }`}
                  to="#"
                  onClick={toggleFilter}
                ></Link>
              </div>

              <div
                className={`list-filter-panel ${
                  showFilter === true ? "show" : ""
                }`}
              >
                <Accordion defaultActiveKey="0">
                  <Card>
                    <CustomToggle eventKey="0" active={true}>
                      <I18n t="points_range" />
                    </CustomToggle>
                    <Accordion.Collapse eventKey="0">
                      <Card.Body>
                        <div className="form-input form-range-input">
                          <div id="slider">
                            <>
                              <Nouislider
                                instanceRef={(instance) => {
                                  if (instance) {
                                    setRangeSliderRef(instance);
                                  }
                                }}
                                start={sliderConfig.start}
                                step={sliderConfig.step}
                                pips={sliderConfig.pips}
                                connect={sliderConfig.connect}
                                range={sliderConfig.range}
                                tooltips={tooltips}
                                onSlide={onRangeUpdate}
                              />
                              {pointsRangeMinValue && (
                                <input
                                  type="hidden"
                                  name="pointsRangeMin"
                                  value={pointsRangeMinValue}
                                />
                              )}
                              {pointsRangeMaxValue && (
                                <input
                                  type="hidden"
                                  name="pointsRangeMax"
                                  value={pointsRangeMaxValue}
                                />
                              )}
                            </>
                          </div>
                        </div>
                      </Card.Body>
                    </Accordion.Collapse>
                  </Card>
                </Accordion>

                <div className="filter-button">
                  <button
                    className="btn btn-reset uppercase"
                    onClick={resetFilteredList}
                  >
                    <I18n t="reset" />
                  </button>
                  <button
                    className="btn btn-apply uppercase"
                    onClick={getFilteredList}
                  >
                    <I18n t="apply" />
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="page-main">
        {loading && <Loading />}
        <div
          className={`filter-overlay ${
            showSorting === true || showFilter === true ? "show" : ""
          }`}
          onClick={toggleCloseOverlay}
        ></div>

        <div className="container" id="event-list">
          {eventList.length === 0 && (
            <div className="row">
              <div className="col-12">
                <div className="no-event">
                  <img src={imageNoGiftTicket} alt="" />
                  <br />
                  <span>
                    <I18n t="no_event_found" />
                  </span>
                  <span>
                    <I18n t="please_stay_tuned" />
                  </span>
                </div>
              </div>
            </div>
          )}

          {eventList.length > 0 && (
            <div className="row">
              {Object.keys(eventList).map((item) => {
                return (
                  <div
                    className="col-6 col-md-3 col-lg-3 event-item"
                    key={eventList[item].eventId}
                  >
                    <div className="event-image">
                      <div className="event-inventory">
                        <span>
                          {getEventRemainQuota(
                            eventList[item].eventSessionList
                          )}
                        </span>{" "}
                        /{" "}
                        <I18n
                          t="quotaLeft"
                          args={{
                            value: getEventQuota(
                              eventList[item].eventSessionList
                            ),
                          }}
                        />
                      </div>

                      <Link to={`events-detail/` + eventList[item].eventId}>
                        <img
                          src={
                            eventList[item].image
                              ? eventList[item].image
                              : noImage
                          }
                          onError={handleErrorImg}
                          alt=""
                        />
                      </Link>
                    </div>
                    <div className="event-intro">
                      <h3 className="event-point">
                        {eventList[item].name} -{" "}
                        <I18n
                          t="pts"
                          args={{
                            points: eventList[item].bonusPointRequired,
                          }}
                        />
                      </h3>
                      <p className="event-desc">
                        <span
                          dangerouslySetInnerHTML={{
                            __html: eventList[item].description,
                          }}
                        ></span>
                      </p>
                    </div>
                  </div>
                );
              })}
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default Event;
