import developmentConfig from "./development";
import productionConfig from "./production";

var config;

console.log(process.env.NODE_ENV);

switch (process.env.NODE_ENV) {
  case "development":
    config = developmentConfig;
    break;
  case "production":
    config = productionConfig;
    break;
  default:
    config = developmentConfig;
    break;
}
export { config };
