import React from "react";
import { Link } from "react-router-dom";
import I18n from "../../i18n";
import "./EarnPoints.css";
import ResultMessage from "../../components/ResultMessage";
import FailImg from "../../assets/images/fail.gif";
import PageBgAnimation from "../../components/PageBgAnimation";

function EarnPointsFailed(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  document.body.className = "result result-failed";

  return (
    <div className="container">
      <PageBgAnimation bgColor="#D4CECA" />
      <ResultMessage
        image={FailImg}
        title={I18n.getTranslation(location, "earn_points_fail_title")}
        content={I18n.getTranslation(location, "earn_points_fail_content")}
        buttonText={I18n.getTranslation(
          location,
          "earn_points_fail_buttonText"
        )}
      />
      <div className="result-holder">
        <div className="result-button">
          <Link to={`/${locale}/qrcode/scan`}>
            <I18n t="verification_fail_buttonText" />
          </Link>
        </div>
      </div>
    </div>
  );
}

export default EarnPointsFailed;
