import { createI18n } from "react-router-i18n";
import translationJsonEN from "./locales/eng.json";
import translationJsonTC from "./locales/tch.json";
import translationJsonSC from "./locales/sch.json";

const locales = ["eng", "tch", "sch"];

var translationObjectEN = translationJsonEN;
var translationFuncEN = {
  pts: ({ points }) => `${points}Pts`,
  pointsExpiredOn: ({ balance, date }) =>
    `${balance} Points expired on ${date}`,
  giftInventory: ({ value }) => `${value} left`,
  expiryOn: ({ value }) => `Expiry on ${value}`,
  quotaLeft: ({ value }) => `${value} left`,
};
var translationEN = { ...translationObjectEN, ...translationFuncEN };

var translationObjectTC = translationJsonTC;
var translationFuncTC = {
  pts: ({ points }) => `${points}`,
  pointsExpiredOn: ({ balance, date }) => `${balance}${date}`,
  giftInventory: ({ value }) => `${value}`,
  expiryOn: ({ value }) => ` ${value}`,
  quotaLeft: ({ value }) => `${value} `,
};
var translationTC = { ...translationObjectTC, ...translationFuncTC };

var translationObjectSC = translationJsonSC;
var translationFuncSC = {
  pts: ({ points }) => `${points}`,
  pointsExpiredOn: ({ balance, date }) => `${balance}${date}`,
  giftInventory: ({ value }) => `${value}`,
  expiryOn: ({ value }) => ` ${value}`,
  quotaLeft: ({ value }) => `${value} `,
};
var translationSC = { ...translationObjectSC, ...translationFuncSC };

const translations = {
  eng: translationEN,
  tch: translationTC,
  sch: translationSC,
};

const I18n = createI18n(locales, translations);

export default I18n;
