import React, { useState, useEffect } from "react";
// import { useUser } from "../../store/UseUser";
import { getEventList } from "../../utils/httpUtils";
import { Link, useHistory } from "react-router-dom";
import I18n from "../../i18n";
import Loading from "../../components/Loading";
import "./Event.css";
import noImage from "../../assets/images/sample/noimage.png";
import Slider from "react-slick";
import "../../assets/css/slick.css";
import "../../assets/css/slick-theme.css";
import { useForm } from "react-hook-form";
import moment from "moment";
import "moment/locale/zh-hk";
import "moment/locale/zh-cn";

function EventDetail(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  document.body.className = "event-detail";
  // const { accessToken } = useUser();
  let history = useHistory();
  let eventId = props.match.params.eventId;
  if (!eventId) {
    history.push("/" + locale + "/events");
  }

  if (locale === "tch") {
    moment.locale("zh-hk");
  } else if (locale === "sch") {
    moment.locale("zh-cn");
  } else if (locale === "eng") {
    moment.locale("en");
  }

  const { register, errors, handleSubmit } = useForm();
  const [loading, setLoading] = useState(true);
  const [event, setEvent] = useState([]);
  const [eventSelectedSession, setEventSelectedSession] = useState(null);
  const [qty, SetQty] = useState(1);
  const imageSliderSettings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    centerMode: false,
  };
  const [disableSubmitBtn, setDisableSubmitBtn] = useState(false);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      getEventList({
        eventId: eventId,
      })
        .then((response) => {
          let tempEvent = response.data.content.eventList;
          tempEvent = tempEvent.filter(function (i) {
            return i.eventId === Number(eventId);
          });
          setEvent(tempEvent[0]);
          setLoading(false);
        })
        .catch(function (error) {
          // eslint-disable-next-line no-console
          console.log(error);
        });
    };
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [locale]);

  const getEventRemainQuota = (sessionList) => {
    if (sessionList) {
      let remainQuota = 0;
      // let remainOfflineQuota = 0;

      for (let i = 0; i < sessionList.length; i++) {
        if (sessionList[i].eventSessionId === Number(eventSelectedSession)) {
          remainQuota += sessionList[i].remainQuota;
          // remainOfflineQuota += sessionList[i].remainOfflineQuota;
          break;
        }
      }

      let totalRemainQuota = remainQuota;
      return totalRemainQuota;
    }
  };

  const getEventQuota = (sessionList) => {
    if (sessionList) {
      let quota = 0;
      // let offlineQuota = 0;

      for (let i = 0; i < sessionList.length; i++) {
        if (sessionList[i].eventSessionId === Number(eventSelectedSession)) {
          quota += sessionList[i].quota;
          // offlineQuota += sessionList[i].offlineQuota;
          break;
        }
      }

      let totalQuota = quota;
      return totalQuota;
    }
  };

  const getEventSessionDateTime = (startTime, endTime) => {
    let eventSessionDate = moment(startTime).format("YYYY-MM-DD");
    let eventSessionDay = moment(startTime).format("ddd");
    let eventSessionStartTime = moment(startTime).format("HH:mm");
    let eventSessionEndTime = moment(endTime).format("HH:mm");
    return (
      eventSessionDate +
      " (" +
      eventSessionDay +
      ") " +
      eventSessionStartTime +
      " - " +
      eventSessionEndTime
    );
  };

  const handleEventSelectedChange = (e) => {
    setEventSelectedSession(e.target.value);
  };

  const handleErrorImg = (e) => {
    e.target.src = noImage;
  };

  const increaseEnrollCount = () => {
    let temp = qty;
    temp++;
    SetQty(temp);
  };

  const decreaseEnrollCount = () => {
    if (qty > 1) {
      let temp = qty;
      temp--;
      SetQty(temp);
    }
  };

  const handleQtyChange = (e) => {
    SetQty(e.target.value);
  };

  const onSubmit = (data) => {
    setDisableSubmitBtn(true);

    var enrollObject = {
      eventId: eventId,
      eventEnrollQty: qty,
      eventSelectedSession: eventSelectedSession,
      eventBonusPointRequired: event.bonusPointRequired,
    };

    sessionStorage.setItem("enrollObject", JSON.stringify(enrollObject));

    history.push({
      pathname: "/" + locale + "/event-order-preview",
      state: { enrollObject: enrollObject },
    });
  };

  return (
    <div>
      <div className="page-intro">
        <div className="container-fluid">
          <div className="container">
            <span className="event-name">{event.name}</span>
            <span className="event-pts">
              <span>
                <I18n
                  t="pts"
                  args={{
                    points: event.bonusPointRequired,
                  }}
                />
              </span>
            </span>
            <div className="event-top-info">
              {eventSelectedSession && (
                <div className="event-inventory">
                  {getEventRemainQuota(event.eventSessionList)} /{" "}
                  <I18n
                    t="quotaLeft"
                    args={{
                      value: getEventQuota(event.eventSessionList),
                    }}
                  />
                </div>
              )}
            </div>
          </div>
        </div>
      </div>

      <form onSubmit={handleSubmit(onSubmit)}>
        <div className="page-main">
          {loading && <Loading />}
          <div className="container">
            {event !== null && (
              <>
                <select
                  id="eventSelectedSession"
                  name="eventSelectedSession"
                  className="custom-select"
                  ref={register({ required: true })}
                  onChange={handleEventSelectedChange}
                >
                  <option value="">
                    {I18n.getTranslation(
                      location,
                      "please_select_event_time_slot"
                    )}
                  </option>
                  {event.eventSessionList &&
                    Object.keys(event.eventSessionList).map((item) => {
                      return (
                        <option
                          value={event.eventSessionList[item].eventSessionId}
                          key={item}
                        >
                          {getEventSessionDateTime(
                            event.eventSessionList[item].startTime,
                            event.eventSessionList[item].endTime
                          )}
                        </option>
                      );
                    })}
                </select>
                <div className="message">
                  {errors.eventSelectedSession?.type === "required" &&
                    I18n.getTranslation(
                      location,
                      "please_select_event_time_slot"
                    )}
                </div>
              </>
            )}

            <div className="event-images">
              <Slider {...imageSliderSettings}>
                <div className="item">
                  <img
                    src={event.image ? event.image : noImage}
                    onError={handleErrorImg}
                    alt=""
                  />
                </div>
              </Slider>
            </div>

            {event.description && (
              <div className="event-detail">
                <h3>
                  <I18n t="detail" />
                </h3>
                <p
                  dangerouslySetInnerHTML={{
                    __html: event.description,
                  }}
                ></p>
              </div>
            )}

            {event.tnc && (
              <>
                <hr />
                <div className="event-terms">
                  <h3>
                    <I18n t="terms_and_conditions" />
                  </h3>
                  <p
                    dangerouslySetInnerHTML={{
                      __html: event.tnc,
                    }}
                  ></p>
                </div>
              </>
            )}
          </div>
        </div>

        {eventSelectedSession && (
          <div className="page-bottom">
            <div className="container-fluid">
              <div className="container">
                <div className="enroll-count-input">
                  <button
                    type="button"
                    className="btn btn-enroll-control btn-plus btn-secondary"
                    onClick={increaseEnrollCount}
                  ></button>
                  <input
                    type="number"
                    className="enroll-count form-control"
                    name="qty"
                    value={qty}
                    onChange={handleQtyChange}
                    ref={register({ required: true, min: 1 })}
                  />
                  <button
                    type="button"
                    className="btn btn-enroll-control btn-minus btn-secondary"
                    onClick={decreaseEnrollCount}
                  ></button>
                  <button
                    type="submit"
                    className="btn btn-enroll uppercase btn-secondary"
                    disabled={disableSubmitBtn}
                  >
                    <I18n t="enroll" />
                  </button>
                </div>
                <div className="message text-center">
                  <br />
                  {errors.qty &&
                    I18n.getTranslation(location, "min_number_of_gift")}
                </div>
              </div>
            </div>
          </div>
        )}
      </form>

      <div className="page-black">
        <Link to={`/${locale}/events`} className="btn-back"></Link>
      </div>
    </div>
  );
}

export default EventDetail;
