import React, { useRef, useState } from "react";
import { useHistory } from "react-router-dom";
import I18n from "../../i18n";
import { useForm } from "react-hook-form";
import PageTitleAnimation from "../../components/PageTitleAnimation";
import "./ResetPassword.css";
import "../../Form.css";
import { changePassword } from "../../utils/httpUtils";

function ResetPassword(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  document.body.className = "reset-password";
  let history = useHistory();
  const { register, watch, errors, handleSubmit } = useForm();
  const [disableSubmitBtn, setDisableSubmitBtn] = useState(false);
  const [errorMessage, setErrorMessage] = useState("");
  const onSubmit = (data) => {
    setDisableSubmitBtn(true);
    changePassword({
      oldPassword: data.oldPassword,
      newPassword: data.password,
    }).then((response) => {
      if (response.data.status === "S000") {
        setDisableSubmitBtn(true);
        setErrorMessage("");
        history.push("/" + locale + "/home");
      } else {
        setDisableSubmitBtn(false);
        setErrorMessage(response.data.message[0]);
        setTimeout(() => {
          setErrorMessage("");
        }, 5000);
      }
    });
  };
  const password = useRef({});
  password.current = watch("password", "");

  return (
    <div>
      <div className="page-title">
        <div className="container">
          <PageTitleAnimation />
          <div className="page-title-content">
            <I18n t="reset_your_password" />
          </div>
        </div>
      </div>
      <div className="page-main">
        <div className="container">
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="form" id="reset-password-form">
              <div class="form-intro">
                <I18n t="reset_your_password_intro" />
              </div>

              <div className="form-body">
                <div className="form-input">
                  <input
                    type="password"
                    name="password"
                    placeholder={I18n.getTranslation(location, "password")}
                    autoComplete="off"
                    className={`form-control ${
                      errors.password?.type === "required" ? "error" : ""
                    }`}
                    ref={register({
                      required: true,
                      pattern: /[A-Za-z0-9]{8}/,
                    })}
                  />
                  <div className="error-icon"></div>
                </div>
                <div className="message">
                  {errors.password?.type === "required" &&
                    I18n.getTranslation(location, "password_required")}
                  {errors.password?.type === "pattern" &&
                    I18n.getTranslation(location, "password_formt_required")}
                </div>

                <div className="form-input">
                  <input
                    type="password"
                    name="passwordReType"
                    placeholder={I18n.getTranslation(
                      location,
                      "password_retype"
                    )}
                    autoComplete="off"
                    className="form-control"
                    ref={register({
                      required: true,
                      validate: (value) =>
                        value === password.current ||
                        I18n.getTranslation(location, "password_must_same"),
                    })}
                  />
                </div>
                <div className="message">
                  {errors.passwordReType?.type === "required" &&
                    I18n.getTranslation(location, "password_required")}
                  {errors.passwordReType && errors.passwordReType.message}
                </div>

                {errorMessage && <div className="message">{errorMessage}</div>}
              </div>

              <div className="form-footer">
                <div className="form-footer-button">
                  <button
                    type="submit"
                    className="btn btn-submit uppercase btn-secondary"
                    disabled={disableSubmitBtn}
                  >
                    <I18n t="submit" />
                  </button>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}

export default ResetPassword;
