import React, { useState } from "react";
import Collapse from "react-bootstrap/Collapse";
import moment from "moment";
import I18n from "../../i18n";

export default (props) => {
  const dateFormat = (date) => {
    return moment(date).format("YYYY-MM-DD");
  };

  const getBrandName = (id) => {
    let brand = props.brand;
    brand = brand.filter(function (v) {
      return v.mallId === id;
    });

    if (brand[0]) {
      if (props.lang === "eng") {
        return brand[0].nameLang1;
      } else if (props.lang === "tch") {
        return brand[0].nameLang2;
      } else if (props.lang === "sch") {
        return brand[0].nameLang3;
      }
    } else {
      return <I18n t="super_mall" />;
    }
  };

  function renderRecord(history) {
    return history.map((item, parentInex) => {
      const [isOpen, setOpen] = useState(parentInex === 0 ? true : false);
      function toggle(e) {
        e.preventDefault();
        setOpen(!isOpen);
        return;
      }
      return (
        <div className="card record" key={parentInex}>
          <header role="tab" className="card-header">
            <a
              href="/#"
              onClick={toggle}
              aria-controls={`collapse-` + parentInex}
              aria-expanded={isOpen}
              className={`btn btn-secondary btn-block ${
                !isOpen ? "collapsed" : ""
              }`}
            >
              <span className="date">{dateFormat(item.createDate)}</span>
              {item.earnedPoints && (
                <span className="points points-plus">+{item.earnedPoints}</span>
              )}
            </a>
          </header>
          <Collapse in={isOpen}>
            <div id={`collapse-` + parentInex}>
              <div className="card-body">
                <div className="receipt">
                  {item.receiptList.map((receipt, index) => {
                    return (
                      <div key={index}>
                        <h6>#{index + 1}</h6>
                        <table className="history-record-table">
                          <tbody>
                            <tr>
                              <td>
                                <I18n t="brand" />
                              </td>
                              <td>{getBrandName(receipt.mallId)}</td>
                            </tr>
                            <tr>
                              <td>
                                <I18n t="shop" />
                              </td>
                              <td>{receipt.shopName}</td>
                            </tr>
                            <tr>
                              <td>
                                <I18n t="date_of_purchase" />
                              </td>
                              <td>{dateFormat(receipt.receiptDate)}</td>
                            </tr>
                            <tr>
                              <td>
                                <I18n t="amount_hkd" />
                              </td>
                              <td>{receipt.amount}</td>
                            </tr>
                          </tbody>
                        </table>
                        <br />
                      </div>
                    );
                  })}
                </div>
              </div>
            </div>
          </Collapse>
        </div>
      );
    });
  }
  return <div className="record-list">{renderRecord(props.history)}</div>;
};
