import React, { useState, useEffect } from "react";
import { useUser } from "../../store/UseUser";
import { getEventList, enroll } from "../../utils/httpUtils";
import { Link, useHistory } from "react-router-dom";
import I18n from "../../i18n";
import Loading from "../../components/Loading";
import "./Event.css";
import noImage from "../../assets/images/sample/noimage.png";
import { useForm } from "react-hook-form";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import moment from "moment";
import "moment/locale/zh-hk";
import "moment/locale/zh-cn";

function EventOrderPreview(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  document.body.className = "order-preview";
  const { accessToken } = useUser();
  const { register, errors, handleSubmit } = useForm();
  const [enrollObject, setEnrollObject] = useState([]);
  const [loading, setLoading] = useState(true);
  const [event, setEvent] = useState([]);
  const [eventDate, setEventDate] = useState(null);
  const [eventTime, setEventTime] = useState(null);
  const [disableSubmitBtn, setDisableSubmitBtn] = useState(false);
  let history = useHistory();

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      let tempEventObject = props.location.state
        ? props.location.state.enrollObject
        : JSON.parse(sessionStorage.getItem("enrollObject"));
      setEnrollObject(tempEventObject);

      getEventList({
        eventId: tempEventObject.eventId,
      }).then((response) => {
        let tempEvent = response.data.content.eventList.filter(function (e) {
          return e.eventId === Number(tempEventObject.eventId);
        });
        setEvent(tempEvent[0]);
        setLoading(false);
        getSelectedEventSession(
          tempEvent[0].eventSessionList,
          tempEventObject.eventSelectedSession
        );
      });
    };
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [locale]);

  const handleErrorImg = (e) => {
    e.target.src = noImage;
  };

  const onSubmit = (data) => {
    setDisableSubmitBtn(true);
    enroll(accessToken, {
      sessionId: enrollObject.eventSelectedSession,
      qty: enrollObject.eventEnrollQty,
      password: data.password,
    }).then((response) => {
      if (response.data.status === "S000") {
        history.push({
          pathname: "/" + locale + "/enroll-success",
        });
      } else {
        enrollToast(response.data.message[0]);
        setDisableSubmitBtn(false);
      }
    });
    sessionStorage.removeItem("enrollObject");
  };

  const pointReduced = (pts, qty) => {
    return String(pts * qty);
  };

  const toastOptions = {
    autoClose: 3000,
    position: toast.POSITION.TOP_CENTER,
    pauseOnHover: false,
  };

  const enrollToast = (message) => {
    toast.info(message, toastOptions);
  };

  const getSelectedEventSession = (eventSessionList, eventSessionId) => {
    let eventSelectedSessionList = eventSessionList.filter(function (i) {
      return i.eventSessionId === Number(eventSessionId);
    });
    getEventSessionDateTime(
      eventSelectedSessionList[0].startTime,
      eventSelectedSessionList[0].endTime
    );
  };

  const getEventSessionDateTime = (startTime, endTime) => {
    let eventSessionDate = moment(startTime).format("YYYY-MM-DD");
    let eventSessionDay = moment(startTime).format("ddd");
    let eventSessionStartTime = moment(startTime).format("HH:mm");
    let eventSessionEndTime = moment(endTime).format("HH:mm");
    setEventDate(eventSessionDate + " (" + eventSessionDay + ")");
    setEventTime(eventSessionStartTime + " - " + eventSessionEndTime);
  };

  return (
    <div>
      <ToastContainer pauseOnFocusLoss={false} />

      <div className="page-title">
        <div className="container">
          <div className="page-title-content">
            <I18n t="order_preview" />
          </div>
        </div>
      </div>

      <form onSubmit={handleSubmit(onSubmit)}>
        <div className="page-main">
          {loading && <Loading />}
          <div className="container">
            <div className="order-preview-holder">
              <div className="order-image">
                <img
                  src={event.image ? event.image : noImage}
                  onError={handleErrorImg}
                  alt=""
                />
              </div>
              <div className="order-detail">
                <h5>
                  <strong>{event.name}</strong>
                </h5>
                {event.description !== null && event.description !== "" && (
                  <p
                    dangerouslySetInnerHTML={{
                      __html: event.description,
                    }}
                  ></p>
                )}
                <table className="order-detail-table">
                  <tbody>
                    <tr>
                      <td>
                        <I18n t="event_date" />
                      </td>
                      <td>{eventDate}</td>
                    </tr>
                    <tr>
                      <td>
                        <I18n t="event_time" />
                      </td>
                      <td>{eventTime}</td>
                    </tr>
                    <tr>
                      <td>
                        <I18n t="quantity" />
                      </td>
                      <td>{enrollObject.eventEnrollQty}</td>
                    </tr>
                    <tr>
                      <td>
                        <I18n t="points" />
                      </td>
                      <td>{event.bonusPointRequired}</td>
                    </tr>
                    <tr>
                      <td>
                        <I18n t="point_reduced" />
                      </td>
                      <td>
                        {pointReduced(
                          event.bonusPointRequired,
                          enrollObject.eventEnrollQty
                        )}
                      </td>
                    </tr>
                  </tbody>
                </table>

                <div className="order-preview-line"></div>

                <p>
                  <I18n t="event_order_preview_continue" />
                </p>

                <div className="form-input">
                  <input
                    type="password"
                    name="password"
                    placeholder={I18n.getTranslation(location, "password")}
                    autoComplete="off"
                    className={`form-control ${
                      errors.password?.type === "required" ? "error" : ""
                    }`}
                    ref={register({
                      required: true,
                      pattern: /[A-Za-z0-9]{8}/,
                    })}
                  />
                  <div className="error-icon"></div>
                </div>
                <div className="message">
                  {errors.password?.type === "required" &&
                    I18n.getTranslation(location, "password_required")}
                  {errors.password?.type === "pattern" &&
                    I18n.getTranslation(location, "password_formt_required")}
                </div>

                <div className="order-preview-footer-button">
                  <Link
                    to={`/${locale}/events-detail/${enrollObject.eventId}`}
                    className="btn-back"
                  ></Link>
                  <button
                    type="submit"
                    className="btn btn-confirm uppercase"
                    disabled={disableSubmitBtn}
                  >
                    <I18n t="confirm" />
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </form>
    </div>
  );
}

export default EventOrderPreview;
