import React, { Component } from "react";
import Routers from "./router/Router";
import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { UserProvider } from "./store/UseUser";

class App extends Component {
  render() {
    return (
      <UserProvider>
        <Routers />
      </UserProvider>
    );
  }
}

export default App;
