import axios from "axios";
import sha256 from "sha256";
import { config } from "../config/index";
import Cookies from "js-cookie";

const xApiKey = config.xApiKey;
const lmsBaseURL = config.lmsUrl;
const timeout = config.httpTimeOut;
const defaultLang = config.defaultLang;

function buildLmsRequest() {
  var lang = window.location.pathname.replace(process.env.PUBLIC_URL, "").split("/")[1];
  var lmsRequest = axios.create({
    baseURL: lmsBaseURL + (lang ? lang : defaultLang),
    timeout: timeout,
    headers: {
      "x-api-key": xApiKey,
      "Content-Type": "application/json",
      "Cache-Control": "no-cache",
    },
  });
  return lmsRequest;
}

function buildMemberRequest(membertoken) {
  var lang = window.location.pathname.replace(process.env.PUBLIC_URL, "").split("/")[1];
  var curTimestamp = Date.now();
  var apiSign = sha256(xApiKey + ":" + curTimestamp + ":" + membertoken);
  var lmsMemberRequest;
  lmsMemberRequest = axios.create({
    baseURL: lmsBaseURL + (lang ? lang : defaultLang),
    timeout: timeout,
    headers: {
      "x-api-key": xApiKey,
      "Content-Type": "application/json",
      "Cache-Control": "no-cache",
      "x-api-token": membertoken,
      "x-timestamp": curTimestamp,
      "x-api-sign": apiSign,
    },
  });

  if (membertoken) {
    lmsMemberRequest.interceptors.response.use(
      (response) => {
        console.log("buildMemberRequest interceptors: " + response.data.status);
        if (response.data.status === "F006") {
          Cookies.remove("memberToken");
          sessionStorage.removeItem("memberToken");
        }
        return response;
      },
      (error) => {
        return Promise.reject(error);
      }
    );
  }

  return lmsMemberRequest;
}

export const getEnumValue = () => buildLmsRequest().get("/enumvalue.json");
export const getMasterDataJson = () =>
  buildLmsRequest().get("/masterdata.json");
export const getSystemSettingJson = () =>
  buildLmsRequest().post("/systemsetting.json");

