import React, { useState, useEffect } from "react";
import { useUser } from "../../store/UseUser";
import {
  getShopList,
  getBrandList,
  getGiftList,
  giftReserveByBonusPoint,
} from "../../utils/httpUtils";
import { Link, useHistory } from "react-router-dom";
import I18n from "../../i18n";
import Loading from "../../components/Loading";
import "./Gift.css";
import noImage from "../../assets/images/sample/noimage.png";
import { useForm } from "react-hook-form";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

function OrderPreview(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  document.body.className = "order-preview";
  const { accessToken } = useUser();
  const { register, errors, handleSubmit } = useForm();
  const [redeemObject, setRedeemObject] = useState([]);
  const [loading, setLoading] = useState(false);
  const [gift, setGift] = useState([]);
  const [brandList, setBrandList] = useState([]);
  const [shopList, setShopList] = useState([]);
  const [disableSubmitBtn, setDisableSubmitBtn] = useState(false);
  let history = useHistory();

  useEffect(() => {
    const fetchData = async () => {
      // setLoading(true);
      let tempRedeemObject = props.location.state
        ? props.location.state.redeemObject
        : JSON.parse(sessionStorage.getItem("redeemObject"));
      setRedeemObject(tempRedeemObject);

      getGiftList({
        rewardId: redeemObject.redeemGiftId,
      }).then((response) => {
        setGift(response.data.content.rewardList[0]);
        let giftListResponse = response.data.content.rewardList[0];
        getBrandList().then((response) => {
          var tempList = response.data.content.mallList;
          if (giftListResponse.mallId === 0) {
            if (giftListResponse.reserveInMall) {
              var reserveInMallList = giftListResponse.reserveInMall.mallId;
              var filteredList = tempList.filter(function (a) {
                for (let i = 0; i < reserveInMallList.length; i++) {
                  if (reserveInMallList[i] === a.mallId) {
                    return reserveInMallList[i] === a.mallId;
                  }
                }
                return false;
              });
              setBrandList(filteredList);
            } else {
              setBrandList(tempList);
            }
          } else {
            tempList = tempList.filter(function (a) {
              return a.mallId === giftListResponse.mallId;
            });
            setBrandList(tempList);
          }
        });

        getShopList({
          mallId: redeemObject.redeemMallId,
        }).then((response) => {
          setShopList(response.data.content.shopList);
        });

        setLoading(false);
      });
    };
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [locale]);

  const getBrandName = (e) => {
    let brandName = "";
    let brandId = Number(e);
    if (brandId === 0) {
      brandName = <I18n t="super_mall" />;
    } else {
      for (let i = 0; i < brandList.length; i++) {
        if (brandList[i].mallId === brandId) {
          switch (locale) {
            case "eng":
              brandName = brandList[i].nameLang1;
              break;
            case "tch":
              brandName = brandList[i].nameLang2;
              break;
            case "sch":
              brandName = brandList[i].nameLang3;
              break;
            default:
              brandName = brandList[i].nameLang1;
              break;
          }
        }
      }
    }
    return brandName;
  };

  const getShopName = (e) => {
    let shopName = "";
    let shopId = Number(e);
    if (shopId === 0) {
      shopName = <I18n t="super_mall" />;
    } else {
      for (let i = 0; i < shopList.length; i++) {
        if (shopList[i].shopId === shopId) {
          shopName = shopList[i].shopName;
          break;
        }
      }
    }
    return shopName;
  };

  const handleErrorImg = (e) => {
    e.target.src = noImage;
  };

  const onSubmit = (data) => {
    setDisableSubmitBtn(true);
    // giftReserveByBonusPoint(accessToken, {
    //   mallId: redeemObject.redeemMallId,
    //   rewardId: redeemObject.redeemGiftId,
    //   qty: redeemObject.redeemGiftQty,
    //   password: data.password,
    //   shopId: redeemObject.redeemShop,
    // }).then((response) => {
    //   if (response.data.status === "S000") {
    //     history.push({
    //       pathname: "/" + locale + "/redeem-success",
    //     });
    //   } else {
    //     redeemToast(response.data.message[0]);
    //     setDisableSubmitBtn(false);
    //   }
    // });

    history.push({
      pathname: "/" + locale + "/redeem-success",
    });
    sessionStorage.removeItem("redeemObject");
  };

  const pointReduced = (pts, qty) => {
    return String(pts * qty);
  };

  const toastOptions = {
    autoClose: 3000,
    position: toast.POSITION.TOP_CENTER,
    pauseOnHover: false,
  };

  const redeemToast = (message) => {
    toast.info(message, toastOptions);
  };

  return (
    <div>
      <ToastContainer pauseOnFocusLoss={false} />

      <div className="page-title">
        <div className="container">
          <div className="page-title-content">
            <I18n t="order_preview" />
          </div>
        </div>
      </div>

      <form onSubmit={handleSubmit(onSubmit)}>
        <div className="page-main">
          {loading && <Loading />}
          <div className="container">
            <div className="order-preview-holder">
              <div className="order-image">
                <img
                  src={gift.image ? gift.image : noImage}
                  onError={handleErrorImg}
                  alt=""
                />
              </div>
              <div className="order-detail">
                {redeemObject.redeemGiftDetail !== null &&
                  redeemObject.redeemGiftDetail !== "" && (
                    <p>{redeemObject.redeemGiftDetail}</p>
                  )}
                <table className="order-detail-table">
                  <tbody>
                    <tr>
                      <td>
                        <I18n t="brand" />
                      </td>
                      <td>{getBrandName(redeemObject.redeemMallId)}</td>
                    </tr>
                    <tr>
                      <td>
                        <I18n t="shop" />
                      </td>
                      <td>{getShopName(redeemObject.redeemShop)}</td>
                    </tr>
                    <tr>
                      <td>
                        <I18n t="quantity" />
                      </td>
                      <td>{redeemObject.redeemGiftQty}</td>
                    </tr>
                    <tr>
                      <td>
                        <I18n t="gift_point" />
                      </td>
                      <td>{redeemObject.redeemGiftPoint}</td>
                    </tr>
                    <tr>
                      <td>
                        <I18n t="point_reduced" />
                      </td>
                      <td>
                        {pointReduced(
                          redeemObject.redeemGiftPoint,
                          redeemObject.redeemGiftQty
                        )}
                      </td>
                    </tr>
                  </tbody>
                </table>

                <div className="order-preview-line"></div>

                <p>
                  <I18n t="order_preview_continue" />
                </p>

                <div className="form-input">
                  <input
                    type="password"
                    name="password"
                    placeholder={I18n.getTranslation(location, "password")}
                    autoComplete="off"
                    className={`form-control ${
                      errors.password?.type === "required" ? "error" : ""
                    }`}
                    ref={register({
                      required: true,
                      pattern: /[A-Za-z0-9]{8}/,
                    })}
                  />
                  <div className="error-icon"></div>
                </div>
                <div className="message">
                  {errors.password?.type === "required" &&
                    I18n.getTranslation(location, "password_required")}
                  {errors.password?.type === "pattern" &&
                    I18n.getTranslation(location, "password_formt_required")}
                </div>

                <div className="order-preview-footer-button">
                  <Link
                    to={`/${locale}/gifts-detail/${redeemObject.redeemGiftId}`}
                    className="btn-back"
                  ></Link>
                  <button
                    type="submit"
                    className="btn btn-confirm uppercase"
                    disabled={disableSubmitBtn}
                  >
                    <I18n t="confirm" />
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </form>
    </div>
  );
}

export default OrderPreview;
