import React from "react";
import logo from "../assets/images/meLogo.svg";
import "./Footer.css";

function Footer() {
  return (
    <footer id="sticky-footer">
      <div className="container text-left">
        <img src={logo} alt="Media Explorer Limited" className="footer-logo" />
        <span>
          Powered by{" "}
          <a href="http://www.me.hk/" target="_blank" rel="noopener noreferrer">
            Media Explorer Limited
          </a>
          .
        </span>
      </div>
    </footer>
  );
}

export default Footer;
