import React from "react";
import Sketch from "react-p5";

let bgColor = "";
let windowW = document.documentElement.clientWidth;
let windowH = document.documentElement.clientHeight * 0.9;
let centerW = windowW / 2;
let centerH = windowH / 2;
let circle;
let circles = [];
let shortSide;

export default class PageBgAnimation extends React.Component {
  constructor(props) {
    super(props);
    bgColor = props.bgColor;
  }

  render() {
    const setup = (p, parentRef) => {
      class Particle {
        constructor(x, y, w, h, r) {
          this.pos = p.createVector(x, y);
          this.w = w;
          this.h = h;
          this.r = r;
        }
        display() {
          p.noFill();
          p.noStroke();
          p.fill(0);
          p.ellipse(this.pos.x, this.pos.y, this.r, this.r);
        }
      }

      class MagicCircle {
        constructor(len, x, y, r, c) {
          this.pArr = [];
          this.pos = p.createVector(x, y);
          this.tempPos = p.createVector(x, y);
          this.r = r;
          this.len = len;
          this.tx = p.random(500);
          this.ty = p.random(500);
          this.tPos = p.random(500);
          this.txSpeed = p.random() / 30;
          this.tySpeed = p.random() / 30;
          this.rotation = 0;
          this.rotateSpeed1 = p.random(0.1, 1);
          this.rotateSpeed2 = p.random(0.1, 1);
          this.color = c;
          this.fPoints = p.random(
            (shortSide / 2) * 0.01,
            (shortSide / 2) * 0.4
          );
          this.fPos = p.random((shortSide / 2) * 0.02, (shortSide / 2) * 0.3);
          for (let i = 0; i < len; i++) {
            let p = new Particle(x, y, 2, 10, 4);
            this.pArr.push(p);
          }
        }

        update() {
          this.tx += this.txSpeed;
          this.ty += this.tySpeed;
          this.rotation = this.rotation % 360;
          this.rotation += this.rotateSpeed1;
          // points position in circle
          for (let i = 0; i < this.len; i++) {
            let rad = p.radians((360 / this.len) * i);
            let x = p.cos(rad) * (this.r + p.noise(this.tx + i) * this.fPoints);
            let y = p.sin(rad) * (this.r + p.noise(this.ty + i) * this.fPoints);
            this.pArr[i].pos.x = x;
            this.pArr[i].pos.y = y;
          }
          // circle position
          this.tPos = this.tPos % 360;
          this.tPos += this.rotateSpeed2;
          this.pos.x =
            p.cos(p.radians(this.tPos)) * this.fPos * p.noise(this.tx / 10) +
            this.tempPos.x;
          this.pos.y =
            p.sin(p.radians(this.tPos)) * this.fPos * p.noise(this.ty / 10) +
            this.tempPos.y;
        }

        display() {
          p.push();
          p.translate(this.pos.x, this.pos.y);
          p.rotate(p.radians(this.rotation));
          p.stroke(this.color);
          p.strokeWeight(1);
          p.noFill();
          p.beginShape();
          p.curveVertex(
            this.pArr[this.len - 1].pos.x,
            this.pArr[this.len - 1].pos.y
          );
          for (let i = 0; i < this.len; i++) {
            p.curveVertex(this.pArr[i].pos.x, this.pArr[i].pos.y);
          }
          p.curveVertex(this.pArr[0].pos.x, this.pArr[0].pos.y);
          p.curveVertex(this.pArr[1].pos.x, this.pArr[1].pos.y);
          p.endShape();
          p.pop();
        }
      }

      shortSide = p.min(windowW, windowH);
      p.createCanvas(windowW, windowH).parent(parentRef);

      p.background(bgColor);
      for (let i = 0; i < 6; i++) {
        circle = new MagicCircle(
          p.floor(p.random(8, 10)),
          centerW,
          centerH,
          p.random(shortSide / 3, shortSide / 3),
          p.color(255, 255, 255, 100)
        );
        circles.push(circle);
      }
    };

    const draw = (p) => {
      p.background(bgColor);
      p.noFill();
      p.frameRate(1);
      circles.forEach(function (circle) {
        circle.update();
        circle.display();
      });
    };

    return <Sketch setup={setup} draw={draw} />;
  }
}
