import React, { useState, useEffect } from "react";
import PointsBalanceComponent from "../components/PointsBalanceComponent";
import { useUser } from "../store/UseUser";
import { getBonusPointsHistory } from "../utils/httpUtils";
import { Link } from "react-router-dom";
import "./PointsBalance.css";
import imageBonus from "../assets/images/bonus.gif";
import imageNoBonus from "../assets/images/no_bonus.gif";
import earnPointsHistoryIcon from "../assets/images/icon-earn-points-history.svg";
import transactionHistoryIcon from "../assets/images/icon-transaction-history.svg";
// import birthdayRewardIcon from "../assets/images/icon-birthday-reward.svg";
// import extraRewardIcon from "../assets/images/icon-extra-reward.svg";
import giftRedemptionIcon from "../assets/images/icon-gift-redemption.svg";
import spendingRewardIcon from "../assets/images/icon-spending-reward.svg";
import activityParticipationIcon from "../assets/images/icon-activity-participation.svg";
import rewardAdjustIcon from "../assets/images/icon-reward-adjust.svg";
import I18n from "../i18n";
import moment from "moment";
import Loading from "../components/Loading";

function PointsBalance(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  document.body.className = "points-balance";
  const { user, accessToken } = useUser();
  const memberProfile = user;
  const bonusPointsBalanceList = memberProfile.bonusPointsBalance;
  const RenderBonusPointsBalance = (data) => {
    if (data) {
      return Object.keys(data).map((item, index) => {
        return (
          <div className="list-item" key={index}>
            <div className="container">
              <I18n
                t="pointsExpiredOn"
                args={{
                  balance: data[item].balance,
                  date: data[item].expiryDate,
                }}
              />
            </div>
          </div>
        );
      });
    }
  };

  const getTotalPointsBalance = () => {
    var tempBalance;
    if (memberProfile.bonusPointsBalance) {
      tempBalance = 0;
      for (const bp of memberProfile.bonusPointsBalance) {
        tempBalance += bp.balance;
      }
    }
    return tempBalance;
  };

  let totalPointsBalance = getTotalPointsBalance();

  const today = moment();
  // const lastMonth = moment().startOf("day").subtract(1, "month");
  const lastThreeMonth = moment().startOf("day").subtract(3, "month");
  const lastSixMonth = moment().startOf("day").subtract(6, "month");
  const lastYear = moment().startOf("day").subtract(1, "year");
  const [memberHistoryEmpty, setMemberHistoryEmpty] = useState(false);
  const [memberHistoryList, setMemberHistoryList] = useState([]);
  const [memberOriginHistoryList, setMemberOriginHistoryList] = useState([]);
  const [memberHistoryListLoaded, setMemberHistoryListLoaded] = useState(false);

  useEffect(() => {
    const fetchData = async () => {
      setMemberHistoryListLoaded(false);
      if (sessionStorage.getItem("memberToken") === "99999999") {
        setMemberHistoryEmpty(false);
        let responseData = require("../assets/json/mockPointsBalance.json");
        setMemberHistoryList(responseData);
        setMemberOriginHistoryList(responseData);
        setMemberHistoryListLoaded(true);
      } else {
        getBonusPointsHistory(accessToken, {
          numberOfLastRecord: 50,
        }).then((response) => {
          let responseData = response.data.content.memberBonusTransactionList.sort(
            (a, b) => moment(b.createDate) - moment(a.createDate)
          );
          if (responseData.length === 0) {
            setMemberHistoryEmpty(true);
          } else {
            setMemberHistoryEmpty(false);
            setMemberHistoryList(
              responseData.filter(function (a) {
                return moment(a.createDate).isBetween(lastThreeMonth, today);
              })
            );
            setMemberOriginHistoryList(responseData);
          }
          setMemberHistoryListLoaded(true);
        });
      }
    };
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const getActivitiesIcon = (e) => {
    if (e.action) {
      switch (e.action) {
        case "A":
          return rewardAdjustIcon;
        case "S":
          return spendingRewardIcon;
        case "D":
          return giftRedemptionIcon;
        case "G":
          return giftRedemptionIcon;
        case "R":
          return giftRedemptionIcon;
        case "C":
          return giftRedemptionIcon;
        case "T":
          return activityParticipationIcon;
        default:
          return giftRedemptionIcon;
      }
    } else {
      switch (e.type) {
        case "B":
          return rewardAdjustIcon;
        case "G":
          return giftRedemptionIcon;
        case "S":
          return giftRedemptionIcon;
        default:
          return rewardAdjustIcon;
      }
    }
  };

  const getActivitiesTitle = (e) => {
    if (e.action) {
      switch (e.action) {
        case "A":
          if (e.matchedRulesJson) {
            return e.matchedRulesJson.extraOffer ? (
              e.matchedRulesJson.extraOffer[0].objectName
            ) : (
                <I18n t="adjust" />
              );
          } else if (e.adjustReasonName) {
            return e.adjustReasonName ? (
              e.adjustReasonName
            ) : (
                <I18n t="adjust" />
              );
          }
          break;
        case "S":
          if (e.matchedRulesJson) {
            return e.matchedRulesJson.extraOffer ? (
              e.matchedRulesJson.extraOffer[0].objectName
            ) : (
                <I18n t="spending_add_points" />
              );
          }
          break;
        case "D":
          if (e.matchedRulesJson) {
            return e.matchedRulesJson.extraOffer ? (
              e.matchedRulesJson.extraOffer[0].objectName
            ) : (
                <I18n t="direct_add_point" />
              );
          }
          break;
        case "G":
          if (e.matchedRulesJson) {
            return e.matchedRulesJson.extraOffer ? (
              e.matchedRulesJson.extraOffer[0].objectName
            ) : (
                <I18n t="redeem_spending_gift" />
              );
          }
          break;
        case "R":
          if (e.matchedRulesJson) {
            return e.matchedRulesJson.extraOffer ? (
              e.matchedRulesJson.extraOffer[0].objectName
            ) : (
                <I18n t="redeem_bonus_points_gift" />
              );
          }
          break;
        case "C":
          if (e.matchedRulesJson) {
            return e.matchedRulesJson.extraOffer ? (
              e.matchedRulesJson.extraOffer[0].objectName
            ) : (
                <I18n t="redeem_bonus_points_coupon" />
              );
          }
          break;
        case "T":
          if (e.matchedRulesJson) {
            return e.matchedRulesJson.extraOffer ? (
              e.matchedRulesJson.extraOffer[0].objectName
            ) : (
                <I18n t="event_participation" />
              );
          }
          break;
        default:
          return "-";
      }
    } else {
      switch (e.type) {
        case "B":
          return <I18n t="earn_points" />;
        case "G":
          if (e.matchedRulesJson) {
            return e.matchedRulesJson.extraOffer ? (
              e.matchedRulesJson.extraOffer[0].objectName
            ) : (
                <I18n t="spendingredeem_gift_add_points" />
              );
          } else {
            return <I18n t="redeem_gift" />;
          }
        case "S":
          if (e.matchedRulesJson) {
            return e.matchedRulesJson.extraOffer ? (
              e.matchedRulesJson.extraOffer[0].objectName
            ) : (
                <I18n t="reserve_gift" />
              );
          } else {
            return <I18n t="reserve_gift" />;
          }
        default:
          return "-";
      }
    }
  };

  const getActivitiesActionType = (e) => {
    if (e.action) {
      switch (e.action) {
        case "A":
          return <I18n t="adjust" />;
        case "S":
          return <I18n t="spending_add_points" />;
        case "D":
          return <I18n t="direct_add_point" />;
        case "G":
          return <I18n t="redeem_spending_gift" />;
        case "R":
          return <I18n t="redeem_bonus_points_gift" />;
        case "C":
          return <I18n t="redeem_bonus_points_coupon" />;
        case "T":
          return <I18n t="event_participation" />;
        default:
          return "-";
      }
    } else {
      switch (e.type) {
        case "B":
          return <I18n t="earn_points" />;
        case "G":
          return <I18n t="redeem_gift" />;
        case "S":
          return <I18n t="reserve_gift" />;
        default:
          return "-";
      }
    }
  };

  const getActivitiesPoint = (e) => {
    let points = 0;
    if (e.points === 0 || e.earnedPoints === 0) {
      return (
        <I18n
          t="pts"
          args={{
            points: points,
          }}
        />
      );
    } else {
      let points = e.points ? e.points : e.earnedPoints;
      if (points > 0) {
        return (
          <I18n
            t="pts"
            args={{
              points: "+" + points,
            }}
          />
        );
      } else if (points < 0) {
        return (
          <I18n
            t="pts"
            args={{
              points: points,
            }}
          />
        );
      } else {
        return (
          <I18n
            t="pts"
            args={{
              points: 0,
            }}
          />
        );
      }
    }
  };

  const [filterDate, setFilterDate] = useState(0);
  const [filterDateText, setFilterDateText] = useState("last_three_months");
  const [showDateFilter, setShowDateFilter] = useState(false);
  const [filterType, setFilterType] = useState(0);
  const [filterTypeText, setFilterTypeText] = useState("all_activities");
  const [showActivitiesFilter, setShowActivitiesFilter] = useState(false);
  const toggleDateFilter = (e) => {
    e.preventDefault();
    setShowDateFilter(!showDateFilter);
    setShowActivitiesFilter(false);
  };

  const filterByDate = (dateValue, datetext) => {
    setFilterDate(dateValue);
    setFilterDateText(datetext);
    filterActivitiesList(dateValue, filterType);
    toggleCloseOverlay();
  };

  const toggleActivitiesFilter = (e) => {
    e.preventDefault();
    setShowActivitiesFilter(!showActivitiesFilter);
    setShowDateFilter(false);
  };

  const filterByType = (typeValue, typeText) => {
    setFilterType(typeValue);
    setFilterTypeText(typeText);
    filterActivitiesList(filterDate, typeValue);
    toggleCloseOverlay();
  };

  const toggleCloseOverlay = () => {
    setShowDateFilter(false);
    setShowActivitiesFilter(false);
  };

  const filterActivitiesList = (dateValue, typeValue) => {
    let tempList = memberOriginHistoryList;
    tempList = filterActivitiesListByDate(dateValue, tempList);
    tempList = filterActivitiesListByType(typeValue, tempList);
    if (tempList.length === 0) {
      setMemberHistoryEmpty(true);
    } else {
      setMemberHistoryEmpty(false);
    }
    setMemberHistoryList(tempList);
  };

  const filterActivitiesListByDate = (dateValue, dataList) => {
    let tempList = dataList;
    if (dateValue === 0) {
      tempList = dataList.filter(function (a) {
        return moment(a.createDate).isBetween(lastThreeMonth, today);
      });
    } else if (dateValue === 1) {
      tempList = dataList.filter(function (a) {
        return moment(a.createDate).isBetween(lastSixMonth, today);
      });
    } else if (dateValue === 2) {
      tempList = dataList.filter(function (a) {
        return moment(a.createDate).isBetween(lastYear, today);
      });
    }
    return tempList;
  };

  const filterActivitiesListByType = (typeValue, dataList) => {
    let tempList = dataList;
    if (typeValue === 0) {
      tempList = dataList;
    } else if (typeValue === 1) {
      tempList = dataList.filter(function (a) {
        let points = a.points ? a.points : a.earnedPoints;
        return points > 0;
      });
    } else if (typeValue === 2) {
      tempList = dataList.filter(function (a) {
        let points = a.points ? a.points : a.earnedPoints;
        return points <= 0;
      });
    }
    return tempList;
  };

  return (
    <div className="point-balance">
      {totalPointsBalance >= 0 && (
        <PointsBalanceComponent totalPointsBalance={totalPointsBalance} />
      )}

      <div className="container-fluid home-points-expire section-title">
        <div className="container">
          <span>
            <I18n t="points_expire" />
          </span>
        </div>
      </div>

      <div className="container-fluid home-points-expire-list">
        {RenderBonusPointsBalance(bonusPointsBalanceList)}
      </div>

      <div className="container-fluid home-main-navigation">
        <div className="container">
          <div className="row">
            <div className="col-12 col-sm-12 home-main">
              <div className="row">
                <div className="col-7 col-sm-4 offset-sm-2 col-lg-3 offset-lg-3">
                  <span className="nav-title1 uppercase">
                    <I18n t="earn" />
                  </span>
                  <span className="nav-title2">
                    <I18n t="bonus_points" />
                  </span>
                  <span
                    className="nav-content"
                    dangerouslySetInnerHTML={{
                      __html: I18n.getTranslation(
                        location,
                        "earn_points_by_scanning"
                      ),
                    }}
                  ></span>
                  <div className="earn-points">
                    <Link to="./qrcode/scan" className="nav-button uppercase">
                      <I18n t="earn_points" />
                    </Link>
                  </div>
                </div>
                <div className="col-5 col-sm-4 col-lg-3 nav-icon">
                  <img src={imageBonus} alt="" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className="container-fluid history-type">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <img src={earnPointsHistoryIcon} alt="" />
              <Link to="./earn-points-history">
                <I18n t="earn_points_history" />
              </Link>
            </div>
          </div>
        </div>
      </div>

      <div className="container-fluid history-type">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <img src={transactionHistoryIcon} alt="" />
              <Link to="./transaction-history">
                <I18n t="transaction_history" />
              </Link>
            </div>
          </div>
        </div>
      </div>

      <div className="container-fluid terms-link">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <Link to="#">
                <I18n t="terms_and_conditions" />
              </Link>
            </div>
          </div>
        </div>
      </div>

      <div className="container-fluid section-title">
        <div className="container">
          <span>
            <I18n t="activities_history" />
          </span>
        </div>
      </div>

      <div className="container-fluid activities-history-filter">
        <div className="container">
          <div className="list-filter list-date">
            <Link
              className={`btn-toggle-date ${showDateFilter === true ? "collapsed" : ""
                }`}
              to="#"
              onClick={toggleDateFilter}
            >
              <I18n t={filterDateText} />
            </Link>

            <div
              className={`list-filter-panel list-date-panel ${showDateFilter === true ? "show" : ""
                }`}
            >
              <div className="custom-control custom-radio">
                <input
                  type="radio"
                  name="filterDate"
                  className="custom-control-input"
                  value="0"
                  id="filterDateThreeMonths"
                  onChange={() => filterByDate(0, "last_three_months")}
                  checked={filterDate === 0}
                />
                <label
                  className="custom-control-label"
                  htmlFor="filterDateThreeMonths"
                >
                  <I18n t="last_three_months" />
                </label>
              </div>

              <div className="custom-control custom-radio">
                <input
                  type="radio"
                  name="filterDate"
                  className="custom-control-input"
                  value="1"
                  id="filterDateSixMonths"
                  onChange={() => filterByDate(1, "last_six_months")}
                  checked={filterDate === 1}
                />
                <label
                  className="custom-control-label"
                  htmlFor="filterDateSixMonths"
                >
                  <I18n t="last_six_months" />
                </label>
              </div>

              <div className="custom-control custom-radio">
                <input
                  type="radio"
                  name="filterDate"
                  className="custom-control-input"
                  value="2"
                  id="filterDateLastYear"
                  onChange={() => filterByDate(2, "last_year")}
                  checked={filterDate === 2}
                />
                <label
                  className="custom-control-label"
                  htmlFor="filterDateLastYear"
                >
                  <I18n t="last_year" />
                </label>
              </div>
            </div>
          </div>

          <div className="list-filter list-activities">
            <Link
              className={`btn-toggle-activities ${showActivitiesFilter === true ? "collapsed" : ""
                }`}
              to="#"
              onClick={toggleActivitiesFilter}
            >
              <I18n t={filterTypeText} />
            </Link>

            <div
              className={`list-filter-panel list-activities-panel ${showActivitiesFilter === true ? "show" : ""
                }`}
            >
              <div className="custom-control custom-radio">
                <input
                  type="radio"
                  name="filterType"
                  className="custom-control-input"
                  value="0"
                  id="allActivities"
                  onChange={() => filterByType(0, "all_activities")}
                  checked={filterType === 0}
                />
                <label className="custom-control-label" htmlFor="allActivities">
                  <I18n t="all_activities" />
                </label>
              </div>

              <div className="custom-control custom-radio">
                <input
                  type="radio"
                  name="filterType"
                  className="custom-control-input"
                  value="1"
                  id="pointsIncrease"
                  onChange={() => filterByType(1, "points_increase")}
                  checked={filterType === 1}
                />
                <label
                  className="custom-control-label"
                  htmlFor="pointsIncrease"
                >
                  <I18n t="points_increase" />
                </label>
              </div>

              <div className="custom-control custom-radio">
                <input
                  type="radio"
                  name="filterType"
                  className="custom-control-input"
                  value="2"
                  id="pointsDecrease"
                  onChange={() => filterByType(2, "points_decrease")}
                  checked={filterType === 2}
                />
                <label
                  className="custom-control-label"
                  htmlFor="pointsDecrease"
                >
                  <I18n t="points_decrease" />
                </label>
              </div>
            </div>
          </div>
        </div>
      </div>

      {memberHistoryEmpty === true && (
        <div className="member-recent-history-empty">
          <img src={imageNoBonus} alt="" />
          <p>
            <I18n t="recent_activities_history_empty" />
          </p>
        </div>
      )}

      {memberHistoryEmpty === false && (
        <div className="container-fluid home-activities-history-list">
          <div
            className={`filter-overlay ${showDateFilter === true || showActivitiesFilter === true
                ? "show"
                : ""
              }`}
            onClick={toggleCloseOverlay}
          ></div>

          <div className="container">
            {!memberHistoryListLoaded && <Loading />}
            {memberHistoryListLoaded &&
              memberHistoryList.length > 0 &&
              Object.keys(memberHistoryList).map((item, index) => {
                return (
                  <div className="list-item row" key={index}>
                    <div className="list-item-icon-holder">
                      <div
                        className={`list-item-icon ${memberHistoryList[item].points > 0 ||
                            memberHistoryList[item].points.earnedPoints > 0
                            ? "points-plus"
                            : ""
                          }`}
                      >
                        <img
                          src={getActivitiesIcon(memberHistoryList[item])}
                          alt=""
                        />
                      </div>
                    </div>
                    <div className="list-item-content">
                      <div className="list-item-title">
                        {getActivitiesTitle(memberHistoryList[item])}
                      </div>
                      <div className="list-item-type">
                        {getActivitiesActionType(memberHistoryList[item])}
                      </div>
                      <div className="list-item-date">
                        {memberHistoryList[item].createDate}
                      </div>
                    </div>
                    <div className="list-item-right">
                      <div
                        className={`list-item-points-change ${memberHistoryList[item].points > 0 ||
                            memberHistoryList[item].earnedPoints > 0
                            ? "plus"
                            : ""
                          }`}
                      >
                        {getActivitiesPoint(memberHistoryList[item])}
                      </div>
                    </div>
                  </div>
                );
              })}
          </div>
        </div>
      )}
    </div>
  );
}

export default PointsBalance;
