import React, { useRef, useState, useEffect } from "react";
import { Link, useHistory } from "react-router-dom";
import I18n from "../../i18n";
import PageTitleAnimation from "../../components/PageTitleAnimation";
import { useForm } from "react-hook-form";
import Nouislider from "nouislider-react";
import "nouislider/distribute/nouislider.css";
import wNumb from "wnumb";
import "./Register.css";
import "../../Form.css";
import {
  lmsUserRegister,
  getEnumValue,
  getMasterDataJson,
} from "../../utils/httpUtils";

function Register(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  let history = useHistory();

  document.body.className = "register";
  const { register, watch, errors, handleSubmit } = useForm();
  const [phonePrefixOptions, setPhonePrefixOptions] = useState([]);
  const [cities, setCities] = useState([]);
  const [districts, setDistricts] = useState([]);
  const [cityValue, setCityValue] = useState("");
  const [districtValue, setDistrictValue] = useState("");
  const [errorMessage, setErrorMessage] = useState("");

  useEffect(() => {
    const fetchData = async () => {
      getEnumValue()
        .then((response) => {
          setPhonePrefixOptions(response.data.PhonePrefix);
        })
        .catch(function (error) {
          // eslint-disable-next-line no-console
          console.log(error);
        });

      getMasterDataJson()
        .then((response) => {
          setDistricts(response.data[7]);
          setCities(response.data[8]);
        })
        .catch(function (error) {
          // eslint-disable-next-line no-console
          console.log(error);
        });
    };
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const onSubmit = (data) => {
    lmsUserRegister({
      phonePrefix: data.phonePrefix,
      phone: data.phone,
      email: data.email,
      title: data.title,
      firstNameLang1: data.givennameEng,
      firstNameLang2: data.givennameTch,
      lastNameLang1: data.surnameEng,
      lastNameLang2: data.surnameTch,
      birthMonth: data.birthMonth,
      age: data.age,
      district: data.district,
      city: data.city,
      referrer: data.refereeMembershipNumber,
      defaultLanguage: data.defaultLang,
      password: data.password,
    }).then((response) => {
      if (response.data.status === "S000") {
        setPhoneDataSession(data.phonePrefix, data.phone);
        history.push("/" + locale + "/verification");
      } else {
        setErrorMessage(response.data.message[0]);
      }
    });
  };
  const setPhoneDataSession = (phonePrefix, phone) => {
    if (!sessionStorage.getItem("registerPhonePrefix")) {
      sessionStorage.setItem("registerPhonePrefix", phonePrefix);
    }
    if (!sessionStorage.getItem("registerPhone")) {
      sessionStorage.setItem("registerPhone", phone);
    }
  };
  const password = useRef({});
  password.current = watch("password", "");

  var titleOptions = [
    { name: "", value: "1", key: "MR" },
    { name: "", value: "3", key: "MRS" },
    { name: "/", value: "2", key: "MS" },
  ];

  // Use state to store object
  const [birthMonth] = useState([
    { value: "1", name: I18n.getTranslation(location, "january") },
    { value: "2", name: I18n.getTranslation(location, "february") },
    { value: "3", name: I18n.getTranslation(location, "march") },
    { value: "4", name: I18n.getTranslation(location, "april") },
    { value: "5", name: I18n.getTranslation(location, "may") },
    { value: "6", name: I18n.getTranslation(location, "june") },
    { value: "7", name: I18n.getTranslation(location, "july") },
    { value: "8", name: I18n.getTranslation(location, "august") },
    { value: "9", name: I18n.getTranslation(location, "september") },
    { value: "10", name: I18n.getTranslation(location, "october") },
    { value: "11", name: I18n.getTranslation(location, "november") },
    { value: "12", name: I18n.getTranslation(location, "december") },
  ]);

  var sliderConfig = {
    start: 30,
    min: 18,
    max: 60,
    step: 1,
  };

  var tooltips = wNumb({
    prefix: I18n.getTranslation(location, "age") + "&nbsp;",
    decimals: 0,
  });

  const [ageSliderRef, setAgeSliderRef] = useState(null);
  const onAgeSliderRefUpdate = () => {
    ageSliderRef.noUiSlider.updateOptions({
      tooltips: tooltips,
    });
  };
  const [ageValue, setAgeValue] = useState("30");
  const onAgeUpdate = (value) => {
    setAgeValue(parseFloat(value).toFixed(0));
  };

  if (ageSliderRef) {
    onAgeSliderRefUpdate();
  }

  const handleCityChange = (event) => {
    event.preventDefault();
    setCityValue(event.target.value);
  };

  const handleDistrictChange = (event) => {
    event.preventDefault();
    setDistrictValue(event.target.value);
  };

  let citiesList =
    cities.length > 0 &&
    cities.map((item, i) => {
      return (
        <option key={i} value={item.masterDataId}>
          {locale === "eng" && item.nameLang1}
          {locale === "tch" && item.nameLang2}
          {locale === "sch" && item.nameLang3}
        </option>
      );
    }, this);

  let filteredDistrictsList =
    cityValue !== "" &&
    districts.filter(function (a) {
      return a.parentId === parseFloat(cityValue);
    });

  let districtsList =
    filteredDistrictsList.length > 0 &&
    filteredDistrictsList.map((item, i) => {
      return (
        <option key={i} value={item.masterDataId}>
          {locale === "eng" && item.nameLang1}
          {locale === "tch" && item.nameLang2}
          {locale === "sch" && item.nameLang3}
        </option>
      );
    }, this);

  const [isPopupActive, setPopupActive] = useState(false);
  const [isPopupTermsActive, setPopupTermsActive] = useState(false);
  const [isPopupPrivacyActive, setPopupPrivacyActive] = useState(false);

  const toggleTermsPopup = (e) => {
    setPopupActive(true);
    setPopupTermsActive(true);
    setPopupPrivacyActive(false);
    e.preventDefault();
    window.scrollTo(0, 0);
  };

  const togglePrivacyPopup = (e) => {
    setPopupActive(true);
    setPopupTermsActive(false);
    setPopupPrivacyActive(true);
    e.preventDefault();
    window.scrollTo(0, 0);
  };

  const togglePopupClose = (e) => {
    setPopupActive(false);
    setPopupTermsActive(false);
    setPopupPrivacyActive(false);
    e.preventDefault();
  };

  return (
    <div>
      <div className="page-title">
        <div className="container">
          <PageTitleAnimation />
          <div className="page-title-content">
            <I18n t="create_an_account" />
          </div>
        </div>
      </div>
      <div className="page-main">
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className="container">
            <div className="form" id="register-form">
              <div className="form-intro"></div>

              <div className="form-body">
                <div className="input-phone">
                  <div className="form-input">
                    <select
                      name="phonePrefix"
                      className="custom-select input-phone-dial-code"
                      ref={register()}
                    >
                      {Object.keys(phonePrefixOptions).map((item) => {
                        return (
                          <option
                            value={phonePrefixOptions[item].value}
                            key={phonePrefixOptions[item].key}
                          >
                            {phonePrefixOptions[item].name}
                          </option>
                        );
                      })}
                    </select>
                  </div>
                  <div className="form-input">
                    <input
                      type="text"
                      name="phone"
                      placeholder={I18n.getTranslation(
                        location,
                        "phone_number"
                      )}
                      autoComplete="off"
                      className={`form-control input-phone-number ${
                        errors.phone?.type === "required" ? "error" : ""
                      }`}
                      ref={register({ required: true, pattern: /[0-9]/ })}
                    />
                    <div className="error-icon"></div>
                  </div>
                  <div className="message">
                    {errors.phone?.type === "required" &&
                      I18n.getTranslation(location, "phone_number_required")}
                    {errors.phone?.type === "pattern" &&
                      I18n.getTranslation(location, "must_be_digit")}
                  </div>
                </div>

                <div className="form-input">
                  <input
                    type="password"
                    name="password"
                    placeholder={I18n.getTranslation(location, "password")}
                    autoComplete="off"
                    className={`form-control ${
                      errors.password?.type === "required" ? "error" : ""
                    }`}
                    ref={register({
                      required: true,
                      pattern: /[A-Za-z0-9]{8}/,
                    })}
                  />
                  <div className="error-icon"></div>
                </div>
                <div className="message">
                  {errors.password?.type === "required" &&
                    I18n.getTranslation(location, "password_required")}
                  {errors.password?.type === "pattern" &&
                    I18n.getTranslation(location, "password_formt_required")}
                </div>

                <div className="form-input">
                  <input
                    type="password"
                    name="passwordReType"
                    placeholder={I18n.getTranslation(
                      location,
                      "password_retype"
                    )}
                    autoComplete="off"
                    className={`form-control ${
                      errors.passwordReType?.type === "required" ? "error" : ""
                    }`}
                    ref={register({
                      required: true,
                      validate: (value) =>
                        value === password.current ||
                        I18n.getTranslation(location, "password_must_same"),
                    })}
                  />
                  <div className="error-icon"></div>
                </div>
                <div className="message">
                  {errors.passwordReType?.type === "required" &&
                    I18n.getTranslation(location, "password_required")}
                  {errors.passwordReType && errors.passwordReType.message}
                </div>

                <div className="form-sub-title">
                  <I18n t="personal_information" />
                </div>

                <div className="form-input">
                  <select
                    name="title"
                    className="custom-select"
                    ref={register()}
                  >
                    <option value="">
                      {I18n.getTranslation(location, "personal_title")}
                    </option>
                    {titleOptions.map(function (title) {
                      return (
                        <option value={title.value} key={title.key}>
                          {title.name}
                        </option>
                      );
                    })}
                  </select>
                </div>

                <div className="form-input">
                  <input
                    type="text"
                    name="givennameEng"
                    placeholder={I18n.getTranslation(
                      location,
                      "given_name_eng"
                    )}
                    autoComplete="off"
                    className="form-control"
                    ref={register()}
                  />
                </div>

                <div className="form-input">
                  <input
                    type="text"
                    name="surnameEng"
                    placeholder={I18n.getTranslation(location, "surname_eng")}
                    autoComplete="off"
                    className="form-control"
                    ref={register()}
                  />
                </div>

                <div className="form-input">
                  <input
                    type="text"
                    name="givennameTch"
                    placeholder={I18n.getTranslation(
                      location,
                      "given_name_tch"
                    )}
                    autoComplete="off"
                    className="form-control"
                    ref={register()}
                  />
                </div>

                <div className="form-input">
                  <input
                    type="text"
                    name="surnameTch"
                    placeholder={I18n.getTranslation(location, "surname_tch")}
                    autoComplete="off"
                    className="form-control"
                    ref={register()}
                  />
                </div>

                <>
                  <div className="form-input">
                    <select
                      className="custom-select"
                      name="city"
                      value={cityValue}
                      onChange={handleCityChange}
                      ref={register()}
                    >
                      <option value="">
                        {I18n.getTranslation(location, "city")}
                      </option>
                      {citiesList}
                    </select>
                  </div>
                  {cityValue && (
                    <div className="form-input">
                      <select
                        className="custom-select"
                        name="district"
                        value={districtValue}
                        onChange={handleDistrictChange}
                        ref={register()}
                      >
                        <option value="">
                          {I18n.getTranslation(location, "district")}
                        </option>
                        {districtsList}
                      </select>
                    </div>
                  )}
                </>

                <div className="form-input">
                  <input
                    type="email"
                    name="email"
                    placeholder={I18n.getTranslation(location, "email")}
                    autoComplete="off"
                    className={`form-control ${
                      errors.email?.type === "required" ? "error" : ""
                    }`}
                    ref={register({
                      required: true,
                      pattern: {
                        value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i,
                      },
                    })}
                  />
                  <div className="error-icon"></div>
                </div>
                <div className="message">
                  {errors.email?.type === "required" &&
                    I18n.getTranslation(location, "email_required")}
                  {errors.email?.type === "pattern" &&
                    I18n.getTranslation(location, "incorrect_email_format")}
                </div>

                <div className="form-input">
                  <select
                    name="birthMonth"
                    className="custom-select"
                    ref={register()}
                  >
                    <option value="">
                      {I18n.getTranslation(location, "birthday_month")}
                    </option>
                    {birthMonth.map((item, i) => {
                      return (
                        <option key={i} value={item.value}>
                          {item.name}
                        </option>
                      );
                    }, this)}
                  </select>
                </div>

                <div className="form-input form-range-input">
                  <div className="slider-range-label slider-min">
                    <I18n t="age" /> <span>{sliderConfig.min}</span>
                  </div>
                  <div className="slider-range-label slider-max">
                    <I18n t="age" /> <span>{sliderConfig.max}</span>+
                  </div>

                  <div id="slider">
                    <>
                      <Nouislider
                        instanceRef={(instance) => {
                          if (instance) {
                            setAgeSliderRef(instance);
                          }
                        }}
                        start={sliderConfig.start}
                        step={sliderConfig.step}
                        pips={{ mode: "range", density: 5 }}
                        connect="lower"
                        range={{
                          min: sliderConfig.min,
                          "30%": 30,
                          "60%": 40,
                          max: sliderConfig.max,
                        }}
                        tooltips={tooltips}
                        onSlide={onAgeUpdate}
                      />
                      {ageValue && (
                        <input
                          type="hidden"
                          name="age"
                          value={ageValue}
                          ref={register()}
                        />
                      )}
                    </>
                  </div>
                </div>

                <div className="form-sub-title">
                  <I18n t="default_language" />
                </div>

                <div className="form-input">
                  <div className="custom-control custom-radio">
                    <input
                      type="radio"
                      name="defaultLang"
                      autoComplete="off"
                      className="custom-control-input"
                      value="1"
                      id="defaultLang1"
                      ref={register()}
                    />
                    <label
                      className="custom-control-label"
                      htmlFor="defaultLang1"
                    >
                      <I18n t="english" />
                    </label>
                  </div>

                  <div className="custom-control custom-radio">
                    <input
                      type="radio"
                      name="defaultLang"
                      autoComplete="off"
                      className="custom-control-input"
                      value="2"
                      id="defaultLang2"
                      ref={register()}
                    />
                    <label
                      className="custom-control-label"
                      htmlFor="defaultLang2"
                    >
                      <I18n t="traditional_chinese" />
                    </label>
                  </div>

                  <div className="custom-control custom-radio">
                    <input
                      type="radio"
                      name="defaultLang"
                      autoComplete="off"
                      className="custom-control-input"
                      value="3"
                      id="defaultLang3"
                      ref={register()}
                    />
                    <label
                      className="custom-control-label"
                      htmlFor="defaultLang3"
                    >
                      <I18n t="simplified_chinese" />
                    </label>
                  </div>
                </div>

                <div className="form-sub-title">
                  <I18n t="referee" />
                </div>

                <div className="form-input">
                  <input
                    type="text"
                    name="refereeMembershipNumber"
                    placeholder={I18n.getTranslation(
                      location,
                      "referee_membership_number"
                    )}
                    autoComplete="off"
                    className="form-control"
                    ref={register()}
                  />
                </div>
              </div>

              <div className="form-footer">
                <div className="form-footer-remark">
                  <I18n t="register_remark1" />{" "}
                  <Link to="/" onClick={toggleTermsPopup}>
                    <I18n t="terms_of_service" />
                  </Link>{" "}
                  <I18n t="register_remark2" />{" "}
                  <Link to="/" onClick={togglePrivacyPopup}>
                    <I18n t="privacy_policy" />
                  </Link>
                </div>

                {errorMessage && (
                  <div className="message">
                    <br />
                    {errorMessage}
                  </div>
                )}

                <div className="form-footer-button">
                  <button
                    type="submit"
                    className="btn btn-submit uppercase btn-secondary"
                  >
                    <I18n t="submit" />
                  </button>
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>

      <div
        id="page-popup"
        className={`${isPopupActive === true ? "popup-active" : ""}`}
      >
        <div className="btn-close-holder">
          <Link className="btn-close" to="/" onClick={togglePopupClose}></Link>
        </div>
        <div
          id="terms"
          className={`page-popup-holder ${
            isPopupTermsActive === true ? "popup-terms-active" : ""
          }`}
        >
          <div className="container">
            <div className="page-popup-title">
              <I18n t="terms_of_service" />
            </div>
            <div className="page-popup-content">
              Lorem ipsum dolor sit amet, consectetur adipiscing elit. Praesent
              et vestibulum ex. Sed dignissim velit et arcu lobortis, sed
              egestas sem ornare. Pellentesque pretium velit risus, viverra
              fermentum purus auctor porta. Donec accumsan ligula et bibendum
              imperdiet.
              <br />
              <br />
              Proin eu velit sed mauris pretium placerat. Nullam arcu ipsum,
              dignissim ac auctor in, ornare in ligula. Fusce pharetra odio
              ligula, eu interdum mauris faucibus vitae. Aliquam molestie ut
              dolor malesuada ornare. Quisque consequat tortor ante, congue
              mattis arcu efficitur in. In interdum ornare commodo. Nam posuere
              vulputate mi, ac tincidunt felis convallis non. Phasellus ornare
              ligula ut consectetur consequat.
            </div>
          </div>
        </div>

        <div
          id="privacy"
          className={`page-popup-holder ${
            isPopupPrivacyActive === true ? "popup-privacy-active" : ""
          }`}
        >
          <div className="container">
            <div className="page-popup-title">
              <I18n t="privacy_policy" />
            </div>
            <div className="page-popup-content">
              Lorem ipsum dolor sit amet, consectetur adipiscing elit. Praesent
              et vestibulum ex. Sed dignissim velit et arcu lobortis, sed
              egestas sem ornare. Pellentesque pretium velit risus, viverra
              fermentum purus auctor porta. Donec accumsan ligula et bibendum
              imperdiet.
              <br />
              <br />
              Proin eu velit sed mauris pretium placerat. Nullam arcu ipsum,
              dignissim ac auctor in, ornare in ligula. Fusce pharetra odio
              ligula, eu interdum mauris faucibus vitae. Aliquam molestie ut
              dolor malesuada ornare. Quisque consequat tortor ante, congue
              mattis arcu efficitur in. In interdum ornare commodo. Nam posuere
              vulputate mi, ac tincidunt felis convallis non. Phasellus ornare
              ligula ut consectetur consequat.
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Register;
