import React, { useState, useEffect } from "react";
import { useUser } from "../../store/UseUser";
import {
  getRewardRedemptionHistory,
  getShopList,
  getBrandList,
} from "../../utils/httpUtils";
import { Link, useHistory } from "react-router-dom";
import I18n from "../../i18n";
import Loading from "../../components/Loading";
import moment from "moment";
import "./MyRedemption.css";
import noImage from "../../assets/images/sample/noimage.png";
import QRCode from "react-qr-code";

function MyRedemptionGiftDetail(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  document.body.className = "my-redemption my-redemption-gift-detail";
  const { accessToken } = useUser();
  let history = useHistory();
  let giftRedemptionTransactionId = props.match.params.id;
  let qrCode = giftRedemptionTransactionId;
  if (!giftRedemptionTransactionId) {
    history.push({ to: "my-redemption-gift" });
  }

  const [loading, setLoading] = useState(true);
  const [giftDetail, setGiftDetail] = useState([]);
  const [shopName, setShopName] = useState("");
  const [brandList, setBrandList] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);

      getRewardRedemptionHistory(accessToken, {
        numberOfLastRecord: 50,
      }).then((response) => {
        if (response.data.content.giftRedemptionTransactionList.length === 0) {
          history.push("/" + locale + "/my-redemption-gift");
        } else {
          let responseData = response.data.content.giftRedemptionTransactionList.sort(
            (a, b) => moment(b.reserveDatetime) - moment(a.reserveDatetime)
          );

          responseData = responseData.filter(function (a) {
            return (
              a.giftRedemptionTransactionId === giftRedemptionTransactionId
            );
          });

          setGiftDetail(responseData[0]);

          let mallId = responseData[0].mallId;
          let redeemLocationId = responseData[0].redeemLocationId;
          getShopList({
            mallId: mallId,
          })
            .then((response) => {
              getShopName(response.data.content.shopList, redeemLocationId);
              setLoading(false);
            })
            .catch(function (error) {
              // eslint-disable-next-line no-console
              console.log(error);
            });
        }
      });

      getBrandList()
        .then((response) => {
          setBrandList(response.data.content.mallList);
        })
        .catch(function (error) {
          // eslint-disable-next-line no-console
          console.log(error);
        });
    };
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [locale]);

  const dateFormat = (date) => {
    return moment(date).format("YYYY-MM-DD");
  };

  const getBrandName = (e) => {
    let brandName = "";
    let brandId = Number(e);
    if (brandId === 0) {
      brandName = <I18n t="super_mall" />;
    } else {
      for (let i = 0; i < brandList.length; i++) {
        if (brandList[i].mallId === brandId) {
          switch (locale) {
            case "eng":
              brandName = brandList[i].nameLang1;
              break;
            case "tch":
              brandName = brandList[i].nameLang2;
              break;
            case "sch":
              brandName = brandList[i].nameLang3;
              break;
            default:
              brandName = brandList[i].nameLang1;
              break;
          }
        }
      }
    }
    return brandName;
  };

  const getShopName = (shopList, locationId) => {
    if (shopList.length !== 0 && locationId !== 0) {
      let tempList = shopList.filter(function (a) {
        return a.shopId === locationId;
      });
      setShopName(tempList[0].shopName);
    } else {
      setShopName(<I18n t="super_mall" />);
    }
  };

  const handleErrorImg = (e) => {
    e.target.src = noImage;
  };

  return (
    <div>
      <div className="page-top">
        <div className="container-fluid">
          <div className="container">
            <div className="my-redemption-gift-qr-code-holder">
              <span className="qrcode-remark">
                <I18n t="for_staff_use" />
              </span>
              <div className="qrcode">
                <div className="arrow-top"></div>
                <div id="qrCode">
                  <QRCode value={String(qrCode)} size={200} />
                </div>
                <div className="arrow-bottom"></div>
              </div>
              <span className="qrcode-number">{qrCode}</span>
            </div>
          </div>
        </div>
      </div>
      <div className="page-main">
        {loading && <Loading />}
        <div className="container">
          <div className="my-redemption-gift-preview-holder">
            <div className="my-redemption-gift-image">
              <img
                src={giftDetail.itemImage ? giftDetail.itemImage : noImage}
                onError={handleErrorImg}
                alt=""
              />
            </div>
            <div className="my-redemption-gift-detail">
              <p>{giftDetail.itemName}</p>

              <table className="my-redemption-gift-detail-table">
                <tbody>
                  <tr>
                    <td>
                      <I18n t="redeem_date" />
                    </td>
                    <td>{dateFormat(giftDetail.reserveDatetime)}</td>
                  </tr>
                  <tr>
                    <td>
                      <I18n t="expiry_date" />
                    </td>
                    <td>{dateFormat(giftDetail.reserveExpiryDate)}</td>
                  </tr>
                  <tr>
                    <td>
                      <I18n t="brand" />
                    </td>
                    <td>{getBrandName(giftDetail.mallId)}</td>
                  </tr>
                  {shopName !== "" && (
                    <tr>
                      <td>
                        <I18n t="shop" />
                      </td>
                      <td>{shopName}</td>
                    </tr>
                  )}
                  <tr>
                    <td>
                      <I18n t="quantity" />
                    </td>
                    <td>1</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>

      <div className="page-black">
        <Link to={`/${locale}/my-redemption-gift`} className="btn-back"></Link>
      </div>
    </div>
  );
}

export default MyRedemptionGiftDetail;
