import React, { useState } from "react";
import { useUser } from "../../store/UseUser";
import { receiptUpload } from "../../utils/httpUtils";
import LoadingLogo from "../../assets/images/meLogo.svg";
import QrReader from "react-qr-reader";
import { useHistory } from "react-router-dom";

function Scan(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  document.body.className = "qr-code-scan";
  const { accessToken } = useUser();
  let history = useHistory();

  const [result, setResult] = useState(null);
  const [error, setError] = useState(false);

  const handleScan = (data) => {
    if (data) {
      setResult(data);
      let receiptList = [];
      // mallId|invoiceNo|transactionDate|shopId|paymentTypeId|productId|amount|...|productId|amount
      // 14|INV10001|2020-10-16|1000001|1|GG5238075103753|100|GG5238075103755|500
      let qrCodeResult = atob(result).split("|");
      let mallId = Number(qrCodeResult[0]);
      let transactionDate = qrCodeResult[2];
      for (let i = 6; i < qrCodeResult.length; i++) {
        let item = {};
        if (i % 2 === 0) {
          item["invoiceNo"] = qrCodeResult[1];
          item["paymentTypeId"] = Number(qrCodeResult[4]);
          item["shopId"] = Number(qrCodeResult[3]);
          item["productId"] = qrCodeResult[i - 1];
          item["amount"] = Number(qrCodeResult[i]);
          receiptList.push(item);
        }
      }

      if (receiptList.length) {
        //alert(JSON.stringify(receiptList));
        submitReceiptUpload(mallId, transactionDate, receiptList);
      }
    }
  };

  const submitReceiptUpload = (mallId, transactionDate, receiptList) => {
    receiptUpload(accessToken, {
      mallId: mallId,
      transactionDate: transactionDate,
      receiptList: receiptList,
    }).then((response) => {
      if (response.data.status === "S000") {
        history.push({
          pathname: "/" + locale + "/earn-points-review",
          state: { responseJson: response.data.content.submittedTransaction },
        });
      } else {
        history.push({
          pathname: "/" + locale + "/earn-points-failed",
          state: { response: response },
        });
      }
    });
  };

  const handleError = (err) => {
    if (err) {
      setError(true);
      history.push("/" + locale + "/home");
    }
  };

  return (
    <div>
      {error && (
        <div className="message error">
          <div className="page-loader" id="page-loader">
            <img src={LoadingLogo} alt="" />
          </div>
        </div>
      )}
      <div className="fullscreen">
        <QrReader
          delay={300}
          facingMode="environment"
          onError={handleError}
          onScan={handleScan}
          style={{ width: "100%" }}
        />
        <p>{result}</p>
      </div>
    </div>
  );
}

export default Scan;
