import React, { useState, useEffect } from "react";
// import { useUser } from "../../store/UseUser";
import { getShopList, getBrandList } from "../../utils/httpUtils";
import { Link, useHistory } from "react-router-dom";
import I18n from "../../i18n";
import Loading from "../../components/Loading";
import "./EarnPoints.css";
import moment from "moment";

function EarnPointsReview(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  document.body.className = "earn-points-review";
  // const { accessToken } = useUser();
  const [loading, setLoading] = useState(true);
  const [responseJson, setResponseJson] = useState(null);
  const [shopList, setShopList] = useState([]);
  const [brandList, setBrandList] = useState([]);
  let history = useHistory();

  //   let tempResponseJson = props.location.state
  //     ? props.location.state.responseJson
  //     : JSON.parse(sessionStorage.getItem("responseJson"));

  let tempResponseJson = [
    {
      spendingTransactionId: "ST6751776560936",
      mallId: 14,
      mallLogo: null,
      memberId: "ME0000001",
      amount: 200.0,
      earnedPoints: null,
      type: "B",
      matchedRulesJson: null,
      receiptImage: null,
      electronicReceiptImage: null,
      rejectReasonId: null,
      rejectReasonName: "",
      remark: null,
      otherRemark: null,
      approvalDate: null,
      status: "W",
      createDate: "2019-12-16 17:36:27",
      receiptList: [
        {
          receiptId: 9214704,
          mallId: 14,
          amount: 200.0,
          shopId: 1000001,
          shopName: "This is Shop1 from Brand 14",
          invoiceNo: "Mdcdc4316-a79e-4a6d-a240-8949391c0579",
          paymentTypeId: null,
          paymentTypeName: "",
          receiptDate: "2019-12-16 00:00:00",
          status: "W",
          createDate: "2019-12-16 17:36:27",
        },
      ],
    },
  ];

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);

      if (!tempResponseJson) {
        history.push("/" + locale + "/qrcode/scan");
      } else {
        setResponseJson(tempResponseJson[0]);
      }

      getBrandList()
        .then((response) => {
          setBrandList(response.data.content.mallList);

          if (tempResponseJson) {
            getShopList({
              mallId: tempResponseJson.mallId,
            }).then((response) => {
              setShopList(response.data.content.shopList);
              setLoading(false);
            });
          }
        })
        .catch(function (error) {
          // eslint-disable-next-line no-console
          console.log(error);
        });
    };
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [locale]);

  const dateFormat = (date) => {
    return moment(date).format("YYYY-MM-DD");
  };

  const getBrandName = (e) => {
    let brandName = "";
    let brandId = Number(e);
    if (brandId === 0) {
      brandName = <I18n t="super_mall" />;
    } else {
      for (let i = 0; i < brandList.length; i++) {
        if (brandList[i].mallId === brandId) {
          switch (locale) {
            case "eng":
              brandName = brandList[i].nameLang1;
              break;
            case "tch":
              brandName = brandList[i].nameLang2;
              break;
            case "sch":
              brandName = brandList[i].nameLang3;
              break;
            default:
              brandName = brandList[i].nameLang1;
              break;
          }
        }
      }
    }
    return brandName;
  };

  const getShopName = (e) => {
    let shopName = "";
    let shopId = Number(e);
    if (shopId === 0) {
      shopName = <I18n t="super_mall" />;
    } else {
      for (let i = 0; i < shopList.length; i++) {
        if (shopList[i].shopId === shopId) {
          shopName = shopList[i].shopName;
          break;
        }
      }
    }
    return shopName;
  };

  return (
    <div>
      <div className="page-title">
        <div className="container">
          <div className="page-title-content">
            <I18n t="earn_points_review" />
          </div>
        </div>
      </div>

      <div className="page-main">
        {loading && <Loading />}
        {responseJson !== null && (
          <div className="container">
            <div className="earn-points-review-holder">
              <table className="order-detail-table">
                <tbody>
                  <tr>
                    <td>
                      <I18n t="brand" />
                    </td>
                    <td>{getBrandName(responseJson.mallId)}</td>
                  </tr>
                  <tr>
                    <td>
                      <I18n t="shop" />
                    </td>
                    <td>{getShopName(responseJson.receiptList[0].shopId)}</td>
                  </tr>
                  <tr>
                    <td>
                      <I18n t="date_of_purchase" />
                    </td>
                    <td>
                      {dateFormat(responseJson.receiptList[0].receiptDate)}
                    </td>
                  </tr>
                  <tr>
                    <td>
                      <I18n t="amount_hkd" />
                    </td>
                    <td>{responseJson.amount}</td>
                  </tr>
                  <tr>
                    <td>
                      <I18n t="earn_points_title" />
                    </td>
                    <td>
                      <span className="points-color">
                        {responseJson.earnedPoints
                          ? responseJson.earnedPoints
                          : 0}
                      </span>
                    </td>
                  </tr>
                </tbody>
              </table>

              <br />

              <div className="terms">
                <I18n t="terms_and_conditions" />
                <br />
                <br />
                {responseJson.remark}
              </div>

              <div className="earn-points-review-button">
                <Link
                  to={`/${locale}/points-balance`}
                  className="btn uppercase"
                >
                  <I18n t="done" />
                </Link>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default EarnPointsReview;
