import React, { useState, useEffect } from "react";
import logo from "../assets/images/logo-2.svg";
import { Link, useHistory } from "react-router-dom";
import I18n from "../i18n";
import { useForm } from "react-hook-form";
import "./Login.css";
import "../Form.css";
import Loading from "../components/Loading";
import PageBgAnimation from "../components/PageBgAnimation";
import { lmsUserLogin, getEnumValue } from "../utils/httpUtils";
import { useUser } from "../store/UseUser";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Cookies from "js-cookie";

function Login(props) {
  const { location } = props;
  const pathname = location.pathname.replace(process.env.PUBLIC_URL, "");
  const locale = pathname.split("/")[1];
  document.body.className = "login";
  const { register, errors, handleSubmit } = useForm();
  const { setAccessToken } = useUser();
  let history = useHistory();

  const [disableLoginBtn, setDisableLoginBtn] = useState(false);
  const inOneHour = new Date(new Date().getTime() + 60 * 60 * 1000);
  const [phonePrefixOptions, setPhonePrefixOptions] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      getEnumValue()
        .then((response) => {
          setPhonePrefixOptions(response.data.PhonePrefix);
        })
        .catch(function (error) {
          // eslint-disable-next-line no-console
          console.log(error);
        });
    };
    fetchData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const onSubmit = (data) => {
    setDisableLoginBtn(true);
    if (data.phone === "99999999" && data.password === "99999999") {
      setDisableLoginBtn(true);
      loginToast(<I18n t="login_success" />);
      setAccessToken("99999999");
    } else {
      lmsUserLogin({
        phonePrefix: data.phonePrefix,
        phone: data.phone,
        password: data.password,
      })
        .then((response) => {
          if (response.data.status === "S000") {
            setDisableLoginBtn(true);
            loginToast(<I18n t="login_success" />);
            if (data.stayLogin) {
              Cookies.set("isStayLogin", data.stayLogin, { expires: inOneHour });
              Cookies.set("memberToken", response.data.content, {
                expires: inOneHour,
              });
              setAccessToken(response.data.content);
            } else {
              setAccessToken(response.data.content);
            }
          } else {
            setDisableLoginBtn(false);
            loginToast(response.data.message[0]);
          }
        })
        .catch(function (error) {
          console.log(error);
          setDisableLoginBtn(false);
          loginToast(<I18n t="login_fail" />);
        });
    }
  };

  const toastOptions = {
    autoClose: 3000,
    position: toast.POSITION.TOP_CENTER,
    pauseOnHover: false,
    onClose: () => loginTokenRedirect(),
  };

  const loginToast = (message) => {
    toast.info(message, toastOptions);
  };

  const loginTokenRedirect = () => {
    if (
      sessionStorage.getItem("memberToken") !== "" ||
      Cookies.get("memberToken") !== ""
    ) {
      history.push("/" + locale + "/home");
    } else {
      window.location.reload();
    }
  };

  return (
    <div>
      <ToastContainer pauseOnFocusLoss={false} />

      <PageBgAnimation bgColor="#FFD428" />

      <div className="login-logo">
        <img src={logo} alt="logo" />
      </div>

      {disableLoginBtn && <Loading />}

      <form onSubmit={handleSubmit(onSubmit)}>
        <div className="form" id="login-form">
          <div className="form-intro">
            <h2>
              <I18n t="login_intro1" />
            </h2>
            <h4>
              <I18n t="login_intro2" />
            </h4>
          </div>

          <div className="form-body">
            <div className="input-phone">
              <div className="form-input">
                <select
                  name="phonePrefix"
                  className="custom-select input-phone-dial-code"
                  ref={register()}
                >
                  {phonePrefixOptions.length > 0 && Object.keys(phonePrefixOptions).map((item) => {
                    return (
                      <option
                        value={phonePrefixOptions[item].value}
                        key={phonePrefixOptions[item].key}
                      >
                        {phonePrefixOptions[item].name}
                      </option>
                    );
                  })}
                  {phonePrefixOptions.length === 0 && <option
                    value="852"
                    key="852"
                  >
                    +852
                      </option>}
                </select>
              </div>
              <div className="form-input">
                <input
                  type="text"
                  name="phone"
                  placeholder={I18n.getTranslation(location, "phone_number")}
                  autoComplete="off"
                  className={`form-control input-phone-number ${errors.phone?.type === "required" ? "error" : ""
                    }`}
                  ref={register({ required: true, pattern: /[0-9]/ })}
                />
                <div className="error-icon"></div>
              </div>
              <div className="message">
                {errors.phone?.type === "required" &&
                  I18n.getTranslation(location, "phone_number_required")}
                {errors.phone?.type === "pattern" &&
                  I18n.getTranslation(location, "must_be_digit")}
              </div>
            </div>
            <div className="form-input">
              <input
                type="password"
                name="password"
                placeholder={I18n.getTranslation(location, "password")}
                autoComplete="off"
                className={`form-control ${errors.password?.type === "required" ? "error" : ""
                  }`}
                ref={register({
                  required: true,
                  pattern: /[A-Za-z0-9]{8}/,
                })}
              />
              <div className="error-icon"></div>
            </div>
            <div className="message">
              {errors.password?.type === "required" &&
                I18n.getTranslation(location, "password_required")}
              {errors.password?.type === "pattern" &&
                I18n.getTranslation(location, "password_formt_required")}
            </div>
          </div>

          <div className="form-footer">
            <div>
              <label className="form-label-container">
                <input type="checkbox" name="stayLogin" ref={register()} />
                <span className="text">
                  <I18n t="stay_login" />
                </span>
                <span className="checkmark"></span>
              </label>

              <Link to="forgot-password" className="link-forgot-password">
                <I18n t="forgot_password" />?
              </Link>
            </div>
            <div className="form-footer-button">
              <button
                type="submit"
                className="btn btn-login uppercase btn-secondary"
                disabled={disableLoginBtn}
              >
                {I18n.getTranslation(location, "login")}
              </button>
            </div>
            <div className="form-footer-remark">
              <I18n t="dont_have_an_account" />?{" "}
              <Link to="register">
                <I18n t="signup" />
              </Link>
            </div>
          </div>
        </div>
      </form>
    </div>
  );
}

export default Login;
